/***
  vds-avatar
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

// standard diamater for circular avatars as outlined by design docs
$avatar-size: px2em(48px);


.vds-avatar {
  display: inline-block;
  line-height: 0; // remove bottom spacing that appears when an avatar wraps avatar-photo

  &--label {
    @include screen-reader-only;
  }

  &--initials {
    background-color: theme(avatar-initials-background);
    border-radius: $avatar-size;
    color: theme(button--default-text);
    display: inline-block;
    font-size: px2em(20px);
    font-weight: theme(font-weight--light);
    height: px2em(28px);
    line-height: px2em(28px);
    padding: px2em(10px);
    text-align: center;
    vertical-align: middle;
    width: px2em(28px);

    &:hover {
      text-decoration: none !important;
    }
  }

  &--photo {
    background-color: theme(avatar-logo-background);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: $avatar-size;
    display: inline-block;
    max-height: $avatar-size;
    max-width: $avatar-size;
    min-height: $avatar-size;
    min-width: $avatar-size;
    width: $avatar-size;
    height: $avatar-size;

    .vds-icon {
      @include svg-icon($icon-size--low, theme(avatar-logo-foreground));
      margin: px2em(12px);
    }
  }

  &--text {
    background-color: theme(avatar-text-background);
    display: inline-block;
    font-family: theme(font);
    font-size: $base-font-size;
    font-weight: theme(font-weight--light);
    height: px2em(28px);
    line-height: px2em(28px);
    padding: px2em(10px);
    text-align: center;
    vertical-align: middle;

    &--light {
      color: theme(button--default-text);
    }

    &--dark {
      color: theme(avatar-text-dark-color);
    }
  }

  &--company-logo {
    background-color: theme(avatar-logo-background);
    display: inline-block;
    height: px2em(28px);
    max-height: px2em(28px);
    max-width: px2em(40px);
    padding: px2em(15px) px2em(10px);
  }
}
