.vds-divider {
  &--standard {
    border-bottom: 0;
    border-color: theme(divider-standard);//$color-smoke
    border-width: 1px;
  }

  &--section {
    border-color: theme(divider-section); //$color-indigo
    border-width: 1px;
  }
}
