/***
  vds-card
  BEM Naming Convention http://getbem.com/naming/

  @author dpoynor
*/

$color-card-background: theme(card-background);
$color-card-border: theme(card-border);
$card-padding: theme(card-padding);
$card-border-radius: theme(card-border-radius);
$color-card-content-category-background: theme(card-content-category-background);
$color-card-content-category-title: theme(card-content-category-title);
$color-card-content-category-icon: theme(card-content-category-icon);
$color-card-content-header-title: theme(card-content-header-title);
$color-card-content-mini-border-bottom: theme(card-content-mini-border-bottom);
$color-card-content-dash-separator: theme(card-content-dash-separator);
$color-card-dashboard-subtitle: theme(card-dashboard-subtitle);
$color-card-dashboard-mini-success: theme(notification-success);
$color-card-dashboard-mini-error: theme(notification-error);

@mixin card {
  background-color: $color-card-background;
  border: 1px solid $color-card-border;
  border-radius: $card-border-radius;
  overflow: hidden;

  &-content {
    padding: 1.25rem;
  }

  .vds-btn-container {
    margin-bottom: 0;
  }
}

.vds-card {
  &--content {
    @include card;

    &--mini {
      @include card;
      border-bottom: .5rem solid $color-card-content-mini-border-bottom;
    }

    &-category {
      background-color: $color-card-content-category-background;
      color: $color-card-content-category-title;

      .vds-text--body {
        color: $color-card-content-category-title;
        display: inline-block;
        margin: 0;
        text-transform: uppercase;
        vertical-align: middle;
      }

      .vds-icon {
        @include svg-icon(1.5rem, $color-card-content-category-icon);
        top: auto;
        vertical-align: middle;
      }
    }

    &-image {
      background-repeat: no-repeat;
      background-size: cover;
      height: 5.25rem;
      width: 100%;
    }

    &-separator {
      background-color: $color-card-content-dash-separator;
      border: 0;
      display: inline-block; // for IE11 left alignment
      height: 3px;
      margin: 0 0 .75rem;
      width: 6%;
    }

    &-footer {
      align-items: center;
      display: flex;

      .vds-btn-text--secondary {
        min-height: 2.1875rem;
        min-width: auto;
      }

      .vds-btn-label {
        padding: 0 .75rem;
      }
    }

    .vds-btn-text--secondary {
      margin: 0 .625rem 0 0;
    }
  }

  &--dashboard {
    @include card;

    &-header {
      color: $color-card-content-category-title;
      margin-left: .75rem;
    }

    &-subtitle {
      color: $color-card-dashboard-subtitle;
      font-weight: 600;
      margin-top: -.9rem;
    }

    &-data-vis {
      display: flex;
      justify-content: center;
    }

    &--mini {
      @include card;

      .vds-card--dashboard-header {
        margin-left: 0;
      }

      &-data-point {
        display: flex;
        flex-flow: row nowrap;
        margin: .5rem 0 1rem;
      }

      &-number--big {
        font-size: 3.5rem; // 56px
        font-weight: 300;
        line-height: 3rem; // 48px
      }

      &-measure {
        align-items: center;
        display: flex;
        flex-flow: column nowrap;
        justify-content: space-between;
        margin-left: .5rem;

        .vds-text--body {
          margin: 0;

          & + [class^='vds-icon'] {
            order: -1;
          }
        }

        & > [class^='vds-icon'] {
          & + .vds-text--body {
            order: 1
          }
        }
      }

      &-qualifier {
        margin: 0;
      }

      &.vds-state--success {
        .vds-card--dashboard--mini-number--big {
          color: $color-card-dashboard-mini-success;
        }

        .vds-card--dashboard--mini-measure {
          [class^='vds-icon'] {
            @include svg-icon($icon-size--low, $color-card-dashboard-mini-success);
          }
        }
      }

      &.vds-state--error {
        .vds-card--dashboard--mini-number--big {
          color: $color-card-dashboard-mini-error;
        }

        .vds-card--dashboard--mini-measure {
          [class^='vds-icon'] {
            @include svg-icon($icon-size--low, $color-card-dashboard-mini-error);
          }
        }
      }
    }
  }
}
