/***
  VDS-Web Core
  BEM Naming Convention http://getbem.com/naming/

  @author Visa UX Engineering
*/

/*
// Loading fonts
*/
@font-face {
  font-family: 'OpenSans';
  font-weight: 700;
  src: url('../_fonts/OpenSans-Semibold-webfont.eot'),
  url('../_fonts/OpenSans-Semibold-webfont.eot?#iefix') format('embedded-opentype'),
  url('../_fonts/OpenSans-Semibold-webfont.ttf') format('truetype'),
  url('../_fonts/OpenSans-Semibold-webfont.svg#open_sanssemibold') format('svg');
}

@font-face {
  font-family: 'OpenSans';
  font-weight: 500;
  src: url('../_fonts/OpenSans-Regular-webfont.eot'),
  url('../_fonts/OpenSans-Regular-webfont.eot?#iefix') format('embedded-opentype'),
  url('../_fonts/OpenSans-Regular-webfont.ttf') format('truetype'),
  url('../_fonts/OpenSans-Regular-webfont.svg#open_sans') format('svg');
}

@font-face {
  font-family: 'OpenSans';
  font-weight: 300;
  src: url('../_fonts/OpenSans-Light-webfont.eot'),
  url('../_fonts/OpenSans-Light-webfont.eot?#iefix') format('embedded-opentype'),
  url('../_fonts/OpenSans-Light-webfont.ttf') format('truetype'),
  url('../_fonts/OpenSans-Light-webfont.svg#open_sanslight') format('svg');
}

html {
  font-size: $root-font-size;
}

body {
  font-size: theme(font-size);
}

html,
body {
  background-color: theme(background);
  color: theme(font-color);
  font-family: theme(font);
  font-weight: theme(font-weight--regular);
  line-height: theme(line-height);
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4 {
  color: theme(font-color-headline);
  margin: 0;
}

p {
  @include text-body;
}

dl,
ol,
ul {
  margin-top: 0;
}

a {
  @include state-focus;
  color: theme(link-color);
  text-decoration: theme(link-underline);

  &:hover,
  &:focus {
    color: theme(link-color--hover);
    text-decoration: theme(link-underline--hover);
  }

  &:active {
    color: theme(link-color--active);
  }

  &:visited {
    color: theme(link-color--visited);
  }

  &.vds-state--disabled {
    color: theme(link-color--disabled);
  }

  .vds-icon {
    position: relative;
    top: 3px;
  }
}

input {
  font-family: theme(font);
}

svg {
  &.vds-icons {
    display: none;
  }
}

// remove default focus outline
:focus {
  &:not(.focus-visible) {
    outline: none;
  }
}

// remove default focus outline on firefox
::-moz-focus-inner {
  border:0;
}

:active {
  outline: none !important;
}

.vds-state--hidden {
  display: none !important;
}

.vds-state--show {
  display: block !important;
}

.vds-state--disabled {
  pointer-events: none;
}

.vds-sr,
.vds-screen-reader {
  @include screen-reader-only;
}

.vds-viewport {
  @include display-flex;
  background-color: $color-white;
  min-height: 100vh;

  &.vds-state--low {
    $vds-viewport--low-offset: 64px;
    margin-top: $vds-viewport--low-offset;

    .vds-panel {
      &-tabs {
        height: calc(100vh - #{$vds-viewport--low-offset});
      }
    }
  }

  &.vds-state--lower {
    $vds-viewport--lower-offset: 118px;
    margin-top: $vds-viewport--lower-offset;

    .vds-panel {
      &-tabs {
        height: calc(100vh - #{$vds-viewport--lower-offset});
      }
    }
  }

  .vds-content {
    @include display-flex;
    flex-direction: column;
    flex-grow: 7;
    float: left;
    min-height: 100%;
    padding: 40px 0 0 0;

    & > .vds-grid,
    & > .vds-grid--fluid {
      align-content: flex-start;
      flex-grow: 1;
    }
  }
}

.vds-text--center {
  text-align: center;
}

.vds-text--left {
  text-align: left;
}

.vds-text--right {
  text-align: right;
}
