/***
  vds-breadcrumbs
  BEM Naming Convention http://getbem.com/naming/
  PX to Em Conversion http://px-em.com/

  @author sacuna
*/

$vds-breadcrumb-padding--horizontal: theme(breadcrumb-padding--horizontal);
$vds-breadcrumb-separator-color: theme(breadcrumb-separator-color);

.vds-breadcrumb {
  &s {
    padding: 0;
    // &-container {
    //
    // }
  }

  & {
    display: inline-block;
    list-style-type: none;
    padding: 0 $vds-breadcrumb-padding--horizontal 0 0;

    &:first-of-type {
      padding-left: 0;
    }

    &.vds-state--active {
      font-weight: theme(font-weight--bold);
    }

    &-link {
      text-decoration: underline;
    }

    [class^='vds-icon'] {
      @include svg-icon($icon-size--tiny, $vds-breadcrumb-separator-color);
      padding-left: $vds-breadcrumb-padding--horizontal;
      position: relative;
      top: 3px;
    }
  }

}
