/***
  vds-tabs
  BEM Naming Convention http://getbem.com/naming/

  @author dpoynor
*/

$color-tab-default--background: theme(#{transparent}); // $color-white
$color-tab-default--text: theme(font-color-headline); // $color-indigo, #2a2e69
$color-tab-default--icon: theme(action-primary); // $color-blue, #003ea9
$color-tab-hover--background: theme(button--secondary-text--hover-background); // $color-ice, #e0eafa
$color-tab-hover--text: theme(link-color--hover); // $color-light-blue, #0051dc
$color-tab-focus--text: theme(link-color--hover); // $color-light-blue, #0051dc
$color-tab-active--text: theme(link-color--hover); // $color-light-blue, #0051dc
$color-tab-active--underline: theme(link-color--hover); // $color-light-blue, #0051dc
$color-tab-disabled--text: theme(link-color--disabled); // $color-cloudy, #b4b4c6

.vds-tab {
  background-color: $color-tab-default--background;
  border: 0;
  color: $color-tab-default--text;
  cursor: pointer;
  //display: inline-table; // for vertical centering in old IE
  display: inline-block;
  font-family: theme(font); // buttons seem to reverse to system font
  font-size: theme(font-size);
  height: px2em(46px);
  line-height: px2em(46px);
  padding: 0 px2em(16px);
  text-align: center;

  &-list {
    display: inline-block;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  &-item {
    display: inline;
    margin: 0;
    padding: 0;
  }

  &--action {
    @extend %ie-btn-jump;
    display: inline-block;
    height: px2em(32px);
    line-height: px2em(28px);
    margin-left: auto;
    margin-right: px2em(7px) * -1;
    min-width: auto;
    vertical-align: middle;
    width: px2em(32px);
  }

  &--vertical {
    width: 100%;

    .vds-tab {
      display: block;
      text-align: inherit;
      width: 100%;

      &.vds-state--selected {
        box-shadow: 4px 0 0 $color-tab-active--underline inset;
      }

      &:active {
        box-shadow: 4px 0 0 $color-tab-active--underline inset;
      }

      &-item {
        display: block;
      }
    }

    a {
      &.vds-tab {
        max-width: calc(100% - 2rem);
      }
    }

    button {
      &.vds-tab {
        .vds-tab--action {
          float: right;
          line-height: 2.875rem;
          margin: 0;
        }
      }
    }
  }

  .vds-icon {
    @include svg-icon(px2em(24px), $color-tab-default--icon);
    margin-right: px2em(8px);
    top: auto;
    vertical-align: middle;
  }

  .vds-icon--tiny {
    @include svg-icon(px2em(16px), $color-tab-default--icon);
    margin: auto;
  }

  &:first-of-type {
    margin-left: 0;
  }

  &:last-of-type {
    margin-right: 0;
  }

  @include state-focus;

  &:focus,
  &:focus-visible,
  &.vds-state--focus {
    color: $color-tab-focus--text;
    text-decoration: none;
  }

  &:focus {
    text-decoration: none;
  }

  &::-moz-focus-inner {
    border: 0;
  }

  &:hover {
    background-color: $color-tab-hover--background;
    color: $color-tab-hover--text;
    text-decoration: none;

    .vds-icon {
      @include svg-icon(px2em(24px), $color-tab-hover--text);
    }

    .vds-icon--tiny {
      @include svg-icon(px2em(16px), $color-tab-hover--text);
    }

  }

  &:active {
    box-shadow: 0 -2px 0 $color-tab-active--underline inset;
    color: $color-tab-active--text;
    text-decoration: none;

    .vds-icon {
      @include svg-icon(px2em(24px), $color-tab-active--text);
    }

    .vds-icon--tiny {
      @include svg-icon(px2em(16px), $color-tab-active--text);
      transform: rotate(180deg);
    }

  }

  &:disabled,
  &.vds-state--disabled {
    color: $color-tab-disabled--text;
    pointer-events: none;

    .vds-icon {
      @include svg-icon(px2em(24px), $color-tab-disabled--text);
    }

    .vds-icon--tiny {
      @include svg-icon(px2em(16px), $color-tab-disabled--text);
    }

  }

  &.vds-state--selected {
    box-shadow: 0 -2px 0 $color-tab-active--underline inset;
    color: $color-tab-default--text;

    &:focus,
    &:focus-active {
      background-color: $color-tab-hover--background;
      color: $color-tab-default--text;
    }
  }

  &-label {
    @extend %ie-btn-jump;
  }
}

.vds-tab--tall {
  display: inline-table;
  line-height: 1;
  min-height: px2em(70px);

  &--wrapper {
    display: table-cell;
    vertical-align: middle;
  }

  .vds-icon {
    margin: 0;
  }

  &--caption {
    font-size: px2em(12px);
    margin-top: px2em(6px);
  }
}
