/***
  vds-nav-vertical
  BEM Naming Convention http://getbem.com/naming/
  PX to Em Conversion http://px-em.com/

  @author pejin
*/

$vds-nav--vertical-width--open: 240px;
$vds-nav--vertical-width--closed: 56px;
$vds-nav--vertical-divider: theme(navigation-divider);

@mixin nav-vertical-light-svg-icon($nav-vertical-icon-color) {
  .vds-icon {
    @include svg-icon(24px, $nav-vertical-icon-color);
  }

  .vds-icon--tiny {
    @include svg-icon(16px, $nav-vertical-icon-color);
  }
}

@mixin nav-vertical-flex-mixin {
  // display: flex;
  @include display-flex;
  flex-direction: column;
}

@mixin nav-vertical-tab-item-mixin {
  .vds-tab-item {
    @include nav-vertical-light-svg-icon($color-cloudy);
    padding: 1px 0 1px 0;

    .vds-tab {
      border: 0;
      height: 48px;
      line-height: 48px;
      margin: 0;
      max-height: 48px;
      max-width: calc(100% - 32px);
      padding: 0 16px 0 16px;

      &.vds-disclosure-header {
        // display: flex;
        @include display-flex;
      }

      .vds-tab-item-label {
        cursor: pointer;
        font-size: px2em(14px);
        pointer-events: none;
      }

      &:active,
      &:focus,
      &.vds-state--selected,
      &.vds-state--focus {
        @include nav-vertical-light-svg-icon($color-white);
        color: $color-white;
      }
    }


    .vds-disclosure-content {
      padding: 0;

      .vds-tab {
        height: 32px;
        line-height: 32px;
        max-height: 32px;
        min-width: 100%;
        padding: 4px 0;
      }

      .vds-tab-item-label {
        cursor: pointer;
        padding-left: 32px;
        pointer-events: none;
      }
    }

    // .vds-tab-item-label {
    //   padding-left: 16px;
    // }

    .vds-tab-item-button {
      background: none;
      border: 0;
      float: right;
      height: 48px;
      margin-top: -48px;
      width: auto;

      &:focus,
      &:active {
        border: 1px dotted $color-white;
      }

      [class^='vds-icon'] {
        @include svg-icon(16px, $color-cloudy);
        padding: 9px;
      }
    }

    /*  group header, no focus and select state, only active state */
    .vds-tab-item-label {
      cursor: default;
    }

    /* anchor link, expendable button */
    a,
    .vds-disclosure-header {
      &.vds-tab {
        &:active,
        &.vds-state--selected,
        &.vds-state--active {
          @include nav-vertical-light-svg-icon($color-white);
          // background: none;
          box-shadow: 4px 0 0 theme(nav-link-default--active-border) inset;
          color: $color-white;
        }
      }
    }
  }
}

.vds-nav {
  &.vds-nav--vertical {
    @include nav-vertical-flex-mixin;
    max-width: $vds-nav--vertical-width--open;
    min-height: 100vh;
    min-width: $vds-nav--vertical-width--open;
    width: $vds-nav--vertical-width--open;
    z-index: $utils-layer-4 + 2;

    .vds-nav-panel {
      @include display-flex;
      flex-direction: column;
      height: 100vh;
      left: 0;
      max-height: 100vh;
      overflow-x: hidden;
      overflow-y: auto;
      position: fixed;
      width: $vds-nav--vertical-width--open;
    }


    .vds-nav-brand {
      @include display-flex;
      justify-content: center;
      padding: 0 0;
    }

    .vds-nav-links {
      @include nav-vertical-flex-mixin;
      flex: 1 0 auto;
      min-height: auto;
      overflow-x: hidden;
      position: relative;
      width: 100%;

      .vds-tab-list {
        @include nav-vertical-flex-mixin;
        @include nav-vertical-tab-item-mixin;
        flex: 1;
        width: 100%;

        .vds-tab-item {
          width: 100%;

          .vds-tab {
            width: 100%;

            .vds-tab-item-label {
              width: inherit;
            }
          }
        }
      }
    }

    .vds-nav-vertical-header,
    .vds-nav-vertical-footer {
      @include nav-vertical-flex-mixin;
      @include nav-vertical-tab-item-mixin;
      flex-shrink: 0;
    }

    // @TODO rename to .vds-nav--container
    .vds-nav-vertical-container {
      // display: flex;
      @include display-flex;
    }

    // @TODO rename to .vds-nav--vertical-footer
    .vds-nav-vertical-footer {

      .vds-avatar {
        border-top: 1px solid theme(navigation-divider);
        display: block;
      }

      /* vertical nav toggle button */
      .vds-nav-vertical-toggle-button {
        @include nav-vertical-light-svg-icon($color-cloudy);
        background: none;
        border: 0;
        border-top: 1px solid theme(navigation-divider);
        margin: 0;
        padding: 4px 0 !important;
        text-align: right;

        &:hover,
        &:focus,
        &:active {
          @include nav-vertical-light-svg-icon($color-white);
        }

        .vds-btn--action {
          border: 0;

          [class^='vds-icon'] {
            @include svg-icon(16px, $color-cloudy);
            padding: 9px;
          }
        }
      }
    }

    // @TODO rename to .vds-nav--marketing
    &.vds-type--marketing {
      .vds-nav-links {
        .vds-tab-list {
          .vds-tab-item {
            a,
            .vds-disclosure-header {
              &.vds-tab {
                &:active,
                &.vds-state--selected,
                &.vds-state--active {
                  @include nav-vertical-light-svg-icon($color-white);
                  // background: none;
                  box-shadow: 4px 0 0 theme(nav-link-marketing--active-border) inset;
                  color: $color-white;
                }
              }
            }
          }
        }
      }
    }
  }

  // @TODO rename to .vds-nav--vertical--mini
  /* mini vertical nav */
  &.vds-nav-vertical-mini {
    max-width: $vds-nav--vertical-width--closed;
    min-width: $vds-nav--vertical-width--closed;
    width: $vds-nav--vertical-width--closed;

    .vds-nav-skip {
      max-width: $vds-nav--vertical-width--closed;
      min-width: $vds-nav--vertical-width--closed;
      width: $vds-nav--vertical-width--closed;
    }

    // @TODO rename to .vds-nav--vertical-header
    // @TODO rename to .vds-nav--vertical-footer
    .vds-nav-vertical-header,
    .vds-nav-vertical-footer {
      max-width: $vds-nav--vertical-width--closed;
    }

    // @TODO rename to .vds-nav--vertical-header
    .vds-nav-vertical-header {
      .vds-nav-brand-logo-image {
        width: 40px;
      }
    }

    // @TODO rename to .vds-nav--vertical-footer
    .vds-nav-vertical-footer {
      .vds-nav-vertical-toggle-button {
        margin: 0;
        max-width: $vds-nav--vertical-width--closed;
        min-width: $vds-nav--vertical-width--closed;
        text-align: center;
        width: $vds-nav--vertical-width--closed;
      }
    }

    .vds-nav-panel {
      width: $vds-nav--vertical-width--closed;
    }

    .vds-nav-links {
      .vds-tab-list {
        .vds-tab-item {
          max-width: $vds-nav--vertical-width--closed;
          min-width: $vds-nav--vertical-width--closed;
          width: $vds-nav--vertical-width--closed;

          .vds-tab {
            max-width: 100%;
            padding: 0;
            text-align: center;
            width: 100%;

            &.vds-disclosure-header {
              justify-content: center;

              .vds-disclosure-header-icon {
                display: none;
              }
            }
          }

          &-label {
            display: none;
          }
        }
      }
    }

    .vds-disclosure {
      &-content {
        display: none;
      }
    }
  }
}

// @TODO move to corresponding place within .vds-nav--vertical definition
/*vertical nav light theme */
.vds-type--light {
  &.vds-nav {
    &.vds-nav--vertical {
      background: theme(nav-light-background);

      .vds-nav-panel {
        background: theme(nav-light-background);
      }

      .vds-nav-links {
        .vds-tab-item {
          /*  group header, no focus and select state, only active state */
          /* link, all state */
          a,
          span,
          .vds-disclosure-header {
            &.vds-tab {
              @include nav-vertical-light-svg-icon($color-blue);
              color: $color-indigo;

              &:hover {
                @include nav-vertical-light-svg-icon($color-light-blue);
                background-color: $color-ice;
                color: $color-light-blue;
              }

              &.vds-state--active,
              &.vds-state--selected,
              &:active {
                @include nav-vertical-light-svg-icon($color-light-blue);
                background-color: $color-ice;
                box-shadow: 4px 0 0 $color-light-blue inset;
                color: $color-light-blue;
              }

              &:focus,
              &.vds-state--focus {
                @include nav-vertical-light-svg-icon($color-light-blue);

                &:not(:active) {
                  color: $color-light-blue;
                  outline: 1px dashed $color-night;
                }
              }
            }
          }

          .vds-tab-item-button {
            @include nav-vertical-light-svg-icon($color-blue);
            color: $color-indigo;

            &:focus,
            &:active {
              border: 1px dotted $color-night;
            }
          }
        }
      }

      .vds-nav-vertical-footer {
        .vds-avatar,
        .vds-nav-vertical-toggle-button  {
          border-color: $color-cloudy;
        }

        .vds-nav-vertical-toggle-button {
          .vds-btn--action {
            @include svg-icon(16px, $color-indigo);
          }
        }
      }
    }
  }

  /* vertical nav toggle button light theme */
  .vds-nav-vertical-toggle-button {
    .vds-btn--action {
      @include nav-vertical-light-svg-icon($color-blue);
      background: none;
      border: 0;

      &:focus,
      &:active {
        @include nav-vertical-light-svg-icon($color-light-blue);
      }
    }
  }
}
