/***
  vds-typography
  BEM Naming Convention http://getbem.com/naming/

  @author dpoynor
*/

$color-typography-instructions: theme(font-color-info);
$color-typography-fine-print: theme(font-color-fine-print);

.vds-text {

  // headlines

  &--display-s {
    @at-root h1, h1#{&} {
      @extend %header-display-s;
    }
  }

  &--headline {
    @at-root h2, h1#{&}, h2#{&} {
      @extend %header-headline;
    }
  }

  &--headline--alt {
    @at-root h1#{&}, h2#{&} {
      @extend %header-headline--alt;
    }
  }

  &--title {
    @at-root h3, h1#{&}, h2#{&}, h3#{&} {
      @extend %header-title;
    }
  }

  &--title--alt {
    @at-root h1#{&}, h2#{&}, h3#{&} {
      @extend %header-title--alt;
    }
  }

  &--subtitle {
    @at-root h4, h2#{&}, h3#{&}, h4#{&} {
      @extend %header-subtitle;
    }
  }

  &--subtitle--alt {
    @at-root h2#{&}, h3#{&}, h4#{&} {
      @extend %header-subtitle--alt;
    }
  }

  // body text

  &--body {
    @include text-body;
    margin-bottom: .75rem;
  }

  &--body--alt {
    @include text-body;
    font-weight: theme(font-weight--semibold);
    margin-bottom: .75rem;
  }

  &--caption {
    font-size: .75rem;
    line-height: 1rem;
    margin-bottom: .75rem;
  }

}
