/***
  vds-select
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

$select-group-bottom-margin: theme(select-group-bottom-margin);
$select-input-box-height: 40px;

.vds-input {
  &--select {
    @include default-input-element;
    @include xbrowser-property(appearance, none);
    color: theme(font-color);
    cursor: pointer !important;
    height: $select-input-box-height;
    padding-right: 1.5rem;
    width: calc(100% + 1.5rem);

    @-moz-document url-prefix() {
      width: 100%;
    }

    &::-ms-expand {
      display: none;
    }

    // when keyboard focused using .focus-visible polyfill,
    // remove the default browser outline
    &.focus-visible {
      &:focus {
        outline: none;
      }
    }

    & + .vds-input-option {
      height: $select-input-box-height;
      pointer-events: none;
      position: absolute;
      right: 0;
    }
  }

  &.vds-state--focus {
    .vds-input {
      &--select-focusbox {
        border: 1px dashed theme(focus-outline-color);
        display: block;
        height: px2em(42px);
        left: px2em(3px) * -1;
        padding: 2px;
        pointer-events: none;
        position: absolute;
        top: px2em(1px) * -1;
        width: 100%;
        z-index: $utils-layer-0 + 1;
      }

      &--select {
        &:focus {
          &::-ms-value {
            background: theme(input-background);
            color: theme(font-color);
          }
        }

        &:-moz-focusring {
          color: transparent;
          text-shadow: 0 0 0 #000;
        }
      }
    }
  }

  &.vds-state--readonly,
  &.vds-state--disabled {
    .vds-input-option {
      [class^="vds-icon"] {
        @include svg-icon($icon-size--tiny, theme(action-input--disabled));
      }
    }
  }
}

.vds-select {
  &-group {
    flex-wrap: wrap;
    margin-bottom: $select-group-bottom-margin;
    max-width: none;
    position: relative;

    .vds-fieldset {
      border: 0;
      margin: 0;
      padding: 0;
    }

    .vds-legend {
      left: px2em(2px);
      margin: 0;
      // margin-top: px2em(-20px);
      padding: 0;
      position: absolute;
      top: px2em(3px);

      & + .vds-select-group-container {
        margin-top: px2em(20px);
      }
    }

    &-container {
      display: inline-flex;
    }

    &-delimiter {
      display: inline-block;
      margin: px2em(32px) px2em(4px) 0 px2em(4px);
      min-height: px2em(24px);
      text-align: center;
    }

    &-message {
      @include default-input-message;
      @include display-flex;
    }

    &.vds-state--error {
      .vds-select-group-message {
        // bottom: px2em(23px) * -1;
        // display: block;
        // left: px2em(3px);
        // top: auto;
        @include default-input-message-error;
      }
    }
  }
}
