/***
  @vds/vds-badge
  BEM Naming Convention http://getbem.com/naming/
  PX to Em Conversion http://px-em.com/

  @author dpoynor
*/

$color--accent-fill: theme(badge--accent-fill); // $color-visa-gold
$color--accent-label: theme(badge--accent-label); // $color-lead
$color--critical-fill: theme(badge--critical-fill); // $color-red
$color--critical-label: theme(badge--critical-label); // $color-white
$color--warning-fill: theme(badge--warning-fill); // $color-orange
$color--warning-label: theme(badge--warning-label); // $color-lead
$color--stable-fill: theme(badge--stable-fill); // $color-green
$color--stable-label: theme(badge--stable-label); // #color-white
$color--disabled-fill: theme(badge--disabled-fill); // $color-smoke
$color--disabled-label: theme(badge--disabled-label); // $color-lead
$color--status-fill: theme(badge--status-fill); // $color-transparent
$color--status-label: theme(badge--status-label); // $color-lead

.vds-badge {
  border-radius: 1px;
  display: inline-block;
  font-size: theme(font-size);
  line-height: px2em(32px);
  padding: 0 px2em(16px);

  &--small {
    font-size: px2em(12px);
    line-height: px2em(20px);
    padding: 0 px2em(8px);
  }

  &--accent {
    background-color: $color--accent-fill;
    color: $color--accent-label;
  }

  &--critical {
    background-color: $color--critical-fill;
    color: $color--critical-label;
  }

  &--warning {
    background-color: $color--warning-fill;
    color: $color--warning-label;
  }

  &--stable {
    background-color: $color--stable-fill;
    color: $color--stable-label;
  }

  &--disabled {
    background-color: $color--disabled-fill;
    color: $color--disabled-label;
  }

  &--status {
    background-color: $color--status-fill;
    color: $color--status-label;
  }
}
