/***
  VDS-Web Global Utitilies
  BEM Naming Convention http://getbem.com/naming/

  @author Visa UX Engineering
*/

/*
// Spatial Model
// https://visavds.invisionapp.com/boards/2H345VYZRNVT3/#/5233102/159361860
*/
$utils-layer-0: 100; // active canvas/side panel
$utils-layer-1: 200; // step-through process
$utils-layer-2: 300; // footer
$utils-layer-3: 400; // notifications
$utils-layer-4: 500; // global navigation
$utils-layer-5: 600; // dialog

/*
// Theme variable function
*/
@function theme($name) {
  @return var(--theme-#{$name}, map-get($theme, #{$name}));
}

/*
// Base font-size
*/
$root-font-size: 16px;
$base-font-size: 14px;

/*
// Responsive Breakpoints
*/
$grid-width-sm: 480px;
$grid-width-md: 768px;
$grid-width-lg: 1024px;
$grid-width-xl: 1440px;

// grid min, max
$grid-width-min: 320px;
$grid-width-max: 1600px;

$grid-gutter-sm: 16px / 2;
$grid-gutter-lg: 24px / 2;

$media-screen-xs: 'only screen and (max-width: #{$grid-width-sm - 1})'; // <480
$media-screen-sm: 'only screen and (min-width: #{$grid-width-sm}) and (max-width: #{$grid-width-md - 1})'; // 480-767
$media-screen-md: 'only screen and (min-width: #{$grid-width-md}) and (max-width: #{$grid-width-lg - 1})'; // 768-1023
$media-screen-lg: 'only screen and (min-width: #{$grid-width-lg}) and (max-width: #{$grid-width-xl - 1})'; // 1024-1439
$media-screen-xl: 'only screen and (min-width: #{$grid-width-xl}) and (max-width: #{$grid-width-max - 1})'; // 1440-1599
$media-screen-xxl: 'only screen and (min-width: #{$grid-width-max})'; // >=1600

/*
// Calculate mixin for multiple browsers
// https://coderwall.com/p/qac-og/sass-css3-calc-mixin
*/
@mixin calc($property, $expression) {
  // sass-lint:disable-block no-duplicate-properties
  #{$property}: -webkit-calc(#{$expression});
  #{$property}: -moz-calc(#{$expression});
  #{$property}: calc(#{$expression});
}

/*
// Utility to render as flexbox cross browser
// http://stackoverflow.com/questions/37306138/flexbox-not-working-properly-on-firefox-but-okay-on-chrome-safari
*/
@mixin display-flex {
  // sass-lint:disable-block no-duplicate-properties
  display: -webkit-box; /* OLD - iOS 6-, Safari 3.1-6 */
  display: -moz-box; /* OLD - Firefox 19- (buggy but mostly works) */
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}

/*
// Utility to add dashed focus
// use this property only on elements that have the dashed border focus
*/
@mixin state-focus($color: theme(focus-outline-color)) {
  // apply WITH the polyfill
  &.focus-visible {
    outline: 1px dashed $color;
  }

  // apply WITHOUT the polyfill
  @at-root html:not(.js-focus-visible) #{&}:focus {
    outline: 1px dashed $color;
  }
}

/*
// Utility for crossbrowser compatibility
// Use this mixin when the property name changes for xbrowser compatibility
*/
@mixin xbrowser-property($property, $expression) {
  // sass-lint:disable-block property-sort-order
  -webkit-#{$property}: #{$expression}; /* NEW - Chrome */
  -moz-#{$property}: #{$expression}; /* Firefox */
  -ms-#{$property}: #{$expression}; /* TWEENER - IE 10 */
  -o-#{$property}: #{$expression}; /* Opera */
  #{$property}: #{$expression};
}

/*
// Utility for crossbrowser compatibility
// Use this mixin when the expression value changes for xbrowser compatibility
*/
@mixin xbrowser-expression($property, $expression) {
  // sass-lint:disable-block no-duplicate-properties
  #{$property}: -webkit-(#{$expression}); /* NEW - Chrome */
  #{$property}: -moz-(#{$expression}); /* Firefox */
  #{$property}: -ms-(#{$expression}); /* TWEENER - IE 10 */
  #{$property}: -o-(#{$expression}); /* Opera */
  #{$property}: #{$expression};
}

/*
// Utility for screen reader only text
*/
@mixin screen-reader-only {
  height: 1px;
  left: -1000000px;
  overflow: hidden;
  position: absolute !important;
  top: auto;
  width: 1px;
}

/*
// Utility for crossbrowser gradient background
*/
@mixin gradient-background($args...) {
  // sass-lint:disable-block no-duplicate-properties
  background: $color-white; /* For browsers that do not support gradients */
  background: -webkit-linear-gradient($args); /* For Safari 5.1 to 6.0 */
  background: -moz-linear-gradient($args); /* For Firefox 3.6 to 15 */
  background: -ms-linear-gradient($args); /* For Firefox 3.6 to 15 */
  background: -o-linear-gradient($args); /* For Opera 11.1 to 12.0 */
  background: linear-gradient($args); /* Standard syntax */
}

@function px2em($px) {
  @if unit($px) == 'px' {
    @return ($px / $root-font-size) * 1rem;
  } @else {
    @error 'Property #{$px} must be a px value.';
  }
}

/*
// Utility for default body text
*/
@mixin text-body {
  color: theme(font-color);
  font-family: theme(font);
  font-size: theme(font-size); // 14px
  font-weight: theme(font-weight--regular);
  line-height: theme(line-height); // 20px
  margin-bottom: .75rem; // 12px
  margin-top: 0;

  a {
    @include inline-links;
  }
}

/*
// Utility for inline links
*/
@mixin inline-links {
  text-decoration: theme(link-inline-underline);
}

/*
// Utility for icon definition and dimensions
*/
$icon-size--link: 1em;
$icon-size--low: 24px !default;
$icon-size--high: 48px;
$icon-size--tiny: 16px;
$error-msg-size: 12px;
@mixin svg-icon($dimension: $icon-size--low, $color: currentColor) {
  fill: #{$color};
  height: #{$dimension};
  width: #{$dimension};
}
