/***
  vds-radio
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

$color-radio-background: theme(radio-background); // $color-white
$color-radio-background--disabled: theme(radio-background--disabled); // $color-cloudy
$color-radio-background--checked: theme(radio-background--checked); // $color-blue
$color-radio-box-shadow--checked: theme(radio-box-shadow--checked); // $color-white
$color-radio--checked: theme(radio--checked); // $color-blue
$color-radio-border--focus: theme(radio-border--focus); // $color-light-blue
$color-radio-outline-background--focus: theme(radio-outline-background--focus); // $color-ice
$color-radio-outline-border--focus: theme(focus-outline-color); // $color-night
$color-radio-error: theme(notification-error);

.vds-radio {
  clip: rect(0, 0, 0, 0);
  margin-left: 1.25em; // line up html5 error message
  margin-top: 1.1em;
  position: absolute;

  &:checked {
    & + .vds-radio-label {
      &::after {
        background-color: $color-radio-background--checked;
        box-shadow: inset 0 0 0 px2em(2px) $color-radio-box-shadow--checked;
      }

      &.vds-state--disabled {
        &::after {
          background-color: $color-radio-background--disabled;
        }
      }

      // @TODO rename to .vds-state--error
      &.vds-state--invalid,
      &.vds-state--error {
        &:not(.vds-state--disabled) {
          &::after {
            background-color: $color-radio-error;
            border-color: $color-radio-error;
          }
        }
      }
    }
  }

  @mixin radio-label-focused {
    &.vds-state--focus {
      &::before {
        background-color: $color-radio-outline-background--focus;
        border: 1px dashed $color-radio-outline-border--focus;
      }
    }
  }

  // apply WITH the polyfill
  &.focus-visible {
    + .vds-radio-label {
      @include radio-label-focused;
    }
  }

  // apply WITHOUT the polyfill
  @at-root html:not(.js-focus-visible) {
    .vds-radio-label {
      @include radio-label-focused;
    }
  }

  // @TODO remove in favor of .vds-fieldset and .vds-legend
  &-group {
    border: 0;
    margin: 0 0 .5em;
    padding: 0;

    &-legend {
      margin-bottom: px2em(8px);
    }

    &--error-required {
      .vds-radio-group-legend {
        color: $color-radio-error;
      }
    }

    &:disabled,
    &.vds-state--disabled {
      color: $color-radio-background--disabled;

      .vds-radio-group-legend {
        color: $color-radio-background--disabled;
      }
    }
  }

  @mixin radio-list-item {
    margin-bottom: px2em(4px);
  }

  &-list {
    list-style-type: none;
    margin: 0;
    padding: 0;

    &-item {
      @include radio-list-item;
    }

    &.vds-radio-list--horizontal {
      .vds-radio-list-item {
        display: inline-block;
      }
    }
  }

  // @TODO remove legacy .vds-radio-item in favor of
  // .vds-radio-list-item (to resemble .vds-checkbox-list-item)
  &-item {
    @include radio-list-item;
  }

  // @TODO combine with .vds-checkbox-error-message
  // into .vds-fieldset-message
  // @TODO combined into .vds-input-message
  // remove this block when ready to release new major version
  &-error-message {
    @include error-message($color-radio-error);
    display: flex;
  }

  &-label {
    border: 0;
    cursor: pointer;
    display: inline-block;
    line-height: px2em(20px);
    // margin-bottom: px2em(4px);
    // margin-top: px2em(4px);
    min-height: px2em(20px);
    padding: px2em(10px) px2em(4px) px2em(10px) px2em(40px);
    position: relative;

    &:empty {
      padding-bottom: 0;
      padding-right: 0 !important;
    }

    &:active {
      &::before {
        background-color: $color-radio-outline-background--focus;
      }
    }

    &::before {
      -webkit-background-clip: padding-box; /* for Safari */
      background-clip: padding-box;
      background-color: theme(#{transparent});
      border: 0;
      border-radius: px2em(20px);
      box-sizing: border-box;
      content: '';
      height: px2em(40px);
      left: 0;
      position: absolute;
      top: 0;
      width: px2em(40px);
    }

    &::after {
      background-color: $color-radio-background;
      border: px2em(2px) solid $color-radio--checked;
      border-radius: px2em(10px);
      box-sizing: border-box;
      content: '';
      display: block;
      height: px2em(20px);
      left: px2em(10px);
      position: absolute;
      top: px2em(10px);
      width: px2em(20px);
    }



    &.vds-state {

      &--disabled {
        color: $color-radio-background--disabled;
        pointer-events: none;

        &::after {
          border-color: $color-radio-background--disabled;
        }
      }

      // &--active {
      //   &::before {
      //     background-color: $color-radio-outline-background--focus;
      //     border-color: theme(#{transparent});
      //   }
      // }

      // @TODO rename to .vds-state--error
      &--invalid,
      &--error {
        &:not(.vds-state--disabled) {
          &::after {
            border-color: $color-radio-error;
          }
        }

        // &:checked {
        //   &::after {
        //     background-color: $color-radio-error;
        //   }
        // }
      }
    }
  }
}
