/***
  vds-switch
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

$vds-switch-node--dimension: px2em(16px);
$vds-switch-node-track--width: px2em(34px);
$vds-switch-node-track--height: px2em(10px);
$vds-focus-box--dimension: px2em(38px);

// Label text
$color-switch-label: theme(notification-foreground-dark);

// Value text
$color-switch-value: theme(action-primary);

// button
$color-switch-button-background: #{transparent};

// Rail
$color-switch-background: theme(button--secondary-text--background--disabled);
$color-switch-border: theme(action-toggle-outline);

// Switch off (not checked)
$color-switch-node-background: theme(slider-thumb-background-default-value--rest);
$color-switch-node-border: theme(action-primary);
// Switch off: hover
$color-switch-node-border--hover: theme(link-color);
// Switch off: focus and pressed
$color-switch-node-highlight-border--focus: theme(focus-outline-color);
// Switch off: focus
$color-switch-node-highlight-background--focus: theme(action-toggle-highlight-background--focus);
// Switch off: disabled
$color-switch-node-border--disabled: theme(link-color--disabled);

// Switch on (checked)
$color-switch-node-checked-background: theme(action-primary);
// Switch on: hover
$color-switch-node-checked-background--hover: theme(link-color);
$color-switch-node-checked-border--hover: theme(link-color);
// Switch on: focus and pressed
$color-switch-node-checked-background--focus: theme(link-color);
$color-switch-node-checked-border--focus: theme(focus-outline-color);
// Switch on: disabled
$color-switch-node-checked-background--disabled: theme(link-color--disabled);
$color-switch-node-checked-border--disabled: #{transparent};

$color-switch-label-and-value-text--disabled: theme(link-color--disabled);

@mixin toggletrack {
  border-radius: $vds-switch-node-track--height;
  height: $vds-switch-node-track--height;
}

.vds-switch {
  cursor: pointer;
  position: relative;
  z-index: 1;

  &::after {
    background-color: $color-switch-node-highlight-background--focus;
    border: px2em(1px) dashed $color-switch-node-highlight-border--focus;
    content: '';
    display: none;
    height: $vds-focus-box--dimension;
    position: absolute;
    right: px2em(20px);
    top: px2em(12px) * -1;
    transition: .25s ease;
    width: $vds-focus-box--dimension;
    z-index: -1;
  }

  &-toggle {
    /*The left/right attributes of the toggle switch are a factor of the size of the focus box.
      To center the box over the node, they are positioned 1/3 of the way into the box,
      or ~px2em(12px).
      The px2em(58px) covers 12px for right-side focus box, 34px for the switch track,
      and left-side focus box. (nirusso) */

    padding-left: px2em(58px);

    &::before {
      @include toggletrack;
      background-color: $color-switch-background;
      border: px2em(1px) solid $color-switch-border;
      content: '';
      display: block;
      position: absolute;
      right: px2em(12px);
      top: 50%;
      transform: translateY(-50%);
      width: $vds-switch-node-track--width;
    }

    &::after {
      background-color: $color-switch-node-background;
      border: px2em(2px) solid $color-switch-node-border;
      border-radius: 100%;
      content: '';
      display: block;
      height: $vds-switch-node--dimension;
      position: absolute;
      right: px2em(28px);
      top: 50%;
      transform: translateY(-50%);
      transition: .25s ease;
      width: $vds-switch-node--dimension;
    }
  }

  &-input {
    clip: rect(0, 0, 0, 0);
    left: px2em(120px);
    position: absolute;
  }

  &-label {
    cursor: pointer;
    display: flex;
    font-family: theme(font);
    font-size: 1rem;
    font-weight: 500;
    line-height: 1;
    margin-right: px2em(16px);
    position: relative;
  }

  &-value {
    color: theme(font-color);
    cursor: pointer;
    display: inline-block;
    font-family: theme(font);
    font-size: 1rem;
    font-weight: 500;
    line-height: 1;
    margin-left: auto;
    margin-right: px2em(14px);
    position: relative;
  }

  &:hover {
    .vds-switch-toggle {
      &::after {
        border: px2em(2px) solid $color-switch-node-border--hover;
      }
    }
  }

  &:active {
    &::after {
      display: none;
    }
  }

  &.vds-state--focus {
    &::after {
      display: block;
    }

    .vds-switch-toggle {
      &::after {
        background: $color-switch-node-background;
        border: px2em(2px) solid $color-switch-node-highlight-border--focus;
        outline: none !important;
      }
    }
  }

  &.vds-state--active {
    &::after {
      right: 0;
    }

    .vds-switch-toggle {
      &::after {
        background-color: $color-switch-node-checked-background;
        border-color: transparent;
        right: px2em(12px);
      }
    }

    &:hover {
      .vds-switch-toggle {
        &::after {
          background-color: $color-switch-node-checked-background--hover;
          border: px2em(2px) solid $color-switch-node-checked-border--hover;
        }
      }
    }

    &.vds-state--focus {
      .vds-switch-toggle {
        &::after {
          background-color: $color-switch-node-checked-background--focus;
          border: px2em(2px) solid $color-switch-node-checked-border--focus;
        }
      }
    }
  }

  &:disabled {
    cursor: default;
  }

  &.vds-state--disabled {
    .vds-switch-value {
      color: $color-switch-label-and-value-text--disabled;
    }

    .vds-switch-label {
      color: $color-switch-label-and-value-text--disabled;
    }

    .vds-switch-toggle {
      &::before {
        border-color: $color-switch-node-border--disabled;
      }

      &::after {
        background-color: $color-switch-node-border--disabled;
        border: px2em(2px) solid $color-switch-node-border--disabled;
      }
    }
  }
}
