/***
  vds-tooltip
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

$color-tooltip-background: theme(tooltip-background);
$color-tooltip-border: theme(tooltip-outline);
$color-tooltip-foreground: theme(tooltip-text);


.vds-tooltip {
  background-color: $color-tooltip-background;
  border: 1px solid $color-tooltip-border;
  display: none;
  max-width: px2em(320px);
  position: absolute;
  z-index: $utils-layer-2 - 1;

  &.vds-state--show {
    display: block !important;
  }

  @at-root #{&}-content {
    display: block;
    padding: theme(tooltip-padding);
  }

  @at-root #{&}-text {
    color: $color-tooltip-foreground;
    display: block;
    font-size: px2em(11px);
    line-height: 1.4em;
    text-align: center;
  }
}
