/***
  vds Input
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

.vds-input {
  @include default-input;

  %vds-input-textarea {
    font-family: theme(font);
    font-size: 1rem;
    overflow: hidden;
    padding: px2em(4px);
    width: calc(100% - 10px);
  }

  &-textarea-container {
    @include default-input;

    &::after {
      display: none;
    }

    .vds-input--textarea {
      @extend %vds-input-textarea;
      min-height: 75px;
      resize: none;
    }

    &.vds-state--focus {
      &.vds-state--readonly {
        .vds-input--textarea {
          -webkit-user-select: all;
          user-select: all;
        }
      }
    }
  }

  &-sr-message {
    @include screen-reader-only;
  }

  &--input-text,
  &--textarea {
    @include default-input-element;

    &:focus {
      outline: none;
    }
  }

  &--input-text {
    width: calc(100% - 10px);

    &::-ms-clear {
      display: none;
    }
  }

  // multiline textarea (input with autoresize)
  &--textarea {
    @extend %vds-input-textarea;
    resize: vertical;
    width: 100%;
  }

  &-linesize {
    display: none;
    font-size: 1em;
    left: -999px;
    line-height: normal;
    min-height: px2em(19px) !important;
    position: fixed;
    top: -999px;
    visibility: hidden;
    white-space: pre-wrap;
    word-wrap: break-word;
    z-index: -1;
  }

  .vds-ie-placeholder {
    left: 0;
    padding-left: 5px;
    position: absolute;
    top: -9px;
  }
}
