/***
  vds-wizard
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

$step-size: 32px;

$color-wizard-step: theme(wizard-foreground);
$color-wizard-step-background: theme(wizard-background);
$color-wizard-step-border: theme(wizard-border);
$color-wizard-step--hover: theme(wizard--hover-foreground);
$color-wizard-step--hover-background: theme(wizard--hover-background);
$color-wizard-step--hover-border: theme(wizard--hover-border);
$color-wizard-step--completed: theme(wizard--completed-foreground);
$color-wizard-step--completed-background: theme(wizard--completed-background);
$color-wizard-step--completed-border: theme(wizard--completed-border);
$color-wizard-step--active: theme(wizard--active-foreground);
$color-wizard-step--active-background: theme(wizard--active-background);
$color-wizard-step--active-border: theme(wizard--active-border);
$color-wizard-step--error: theme(wizard--error-foreground);
$color-wizard-step--error-background: theme(wizard--error-background);
$color-wizard-step--error-border: theme(wizard--error-border);

.vds-wizard {
  @extend %default-pagination;
  animation-fill-mode: forwards;
  border: 1px solid transparent;
  cursor: default !important;

  @mixin step-element {
    height: $step-size;
    max-height: $step-size;
    max-width: $step-size;
    min-height: $step-size;
    min-width: $step-size;
    width: $step-size;
  }

  @at-root #{&}-step {
    @extend %default-pagination-step;
    border: 1px solid transparent;
    margin: 0 px2em(24px);
    max-height: px2em(32px) !important;
    z-index: $utils-layer-2;

    &:not(.vds-state--completed) {
      &:not(.vds-state--active) {
        &:not(.vds-state--error) {
          .vds-btn-icon--light-tiny {
            &:hover {
              background-color: $color-wizard-step--hover-background;
            }

            &:focus {
              background-color: $color-wizard-step--hover-background;
            }
          }
        }
      }
    }

    &--titles {
      margin: 0 px2em(48px);
    }

    &-label {
      display: none;
    }

    &-title {
      color: theme(font-color);
      line-height: px2em(20px);
      margin-top: px2em(8px);
      max-width: px2em(120px);
      position: absolute;
      transform: translateX(-50%) translateY(-2px);
    }

    &-error {
      background-color: $color-wizard-step--error-background;
      border: 2px solid $color-wizard-step--error;
      border-radius: 100%;
      height: px2em(12px);
      position: absolute;
      right: -4px;
      top: -4px;
      width: px2em(12px);
      z-index: 2;

      [class^='vds-icon'] {
        @include svg-icon(px2em(12px), $color-wizard-step--error);
      }
    }

    .vds-btn-icon--light-tiny {
      @extend %default-pagination-step-button;
      @include step-element;
      background-color: transparent;

      .vds-btn-label {
        color: $color-wizard-step;
        position: relative;
        top: -1px;
      }
    }

    &.vds-state--completed {
      background-color: $color-wizard-step--completed-background;

      .vds-btn-icon--light-tiny {
        background-color: $color-wizard-step--completed-background;
        position: relative;
        z-index: 1;

        .vds-btn-label {
          color: $color-wizard-step--completed;
        }
      }

      .vds-wizard-step-label {
        @include step-element;
        color: $color-wizard-step--completed;
        display: block;
        margin-top: -$step-size;
        position: absolute;
        transition-timing-function: linear;
        z-index: 2;

        _:-ms-fullscreen, // sass-lint:disable-line force-pseudo-nesting
        .ie11up,
        & {
          margin-top: $step-size * -1.34;
        }
      }
    }

    &.vds-state--error {
      background-color: $color-wizard-step--error-background;
      border-color: $color-wizard-step--error;
      position: relative;

      .vds-btn-icon--light-tiny {
        background-color: $color-wizard-step--error-background;
        position: relative;
        z-index: 1;

        .vds-btn-label {
          color: $color-wizard-step--error;
        }
      }
    }

    &.vds-state--active {
      background-color: $color-wizard-step--active-background;
      border-color: $color-wizard-step--active;
      color: $color-wizard-step--active;

      .vds-btn-icon--light-tiny {
        background-color: $color-wizard-step--active-background;
        cursor: default;

        .vds-btn-label {
          color: $color-wizard-step--active;
        }
      }

      .vds-wizard-step-title {
        color: theme(font-color);
      }
    }
  }

  //border movement
  @at-root #{&}-border {
    border: 1px solid transparent;
    border-radius: $step-size;
    display: block;
    height: $step-size;
    pointer-events: none;
    position: relative;
    width: $step-size;
    z-index: $utils-layer-2 + 1;
  }

  &-container {
    &.vds-state--animated {
      .vds-wizard {
        &-step {
          &.vds-state--active {
            border-color: transparent;
          }
        }

        &-border {
          border-color: $color-wizard-step--active;
          transition: left .2s ease;
        }
      }
    }
  }
}
