/***
  vds-disclosure
  BEM Naming Convention http://getbem.com/naming/
  PX to Em Conversion http://px-em.com/
  @author pejin
*/

.vds-disclosure {
  &-item {
    border-bottom: 1px solid $color-gray;
  }

  &-header {
    @include state-focus;
    align-items: center;
    border: 0;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    font-family: theme(font);
    height: 36px;
    width: 100%;

    &--reversed {
      flex-flow: row-reverse wrap;
      justify-content: space-between;
    }

    .vds-disclosure-header-icon {
      margin-right: 10px;
    }

    .vds-disclosure-header-wrapper {
      font-size: 16px;
    }

    &.focus-visible {
      outline-offset: -4px;
    }
  }

  &-content {
    padding: 8px;
  }
}
