/***
  vds-navmenu
  BEM Naming Convention http://getbem.com/naming/
  PX to Em Conversion http://px-em.com/

  @author sacuna
*/
$color-listbox-text--active: theme(listbox-text--active);

.vds-navmenu {
  display: inline-block;

  // positioning the popper-container on the reference element allows the
  // sub-menu to consistently align without manual offset modifiers
  .vds-popper-container {
    display: block;
    height: 100%;
  }

  %navmenu-alt-state {
    .vds-btn {
      text-decoration: none !important;
    }
  }

  &:hover,
  &:focus,
  &:active {
    @extend %navmenu-alt-state;
  }

  &:focus {
    &:not(:active) {
      @extend %navmenu-alt-state;
    }
  }

  .vds-btn {
    @include state-focus;
    background-color: transparent !important;
    border: 0;
    cursor: pointer;
    padding-left: 0;
    padding-right: 20px;
    position: relative;

    & > .vds-avatar {
      @extend %ie-btn-jump;

      & + [class^='vds-icon'] {
        height: 1rem;
        margin-top: -7px;
        position: absolute;
        right: -20px;
        top: 50%;
        width: 1rem;
      }
    }

    & > [class^='vds-icon'] {
      right: 0 !important;
    }

    &:focus {
      &:not(:active) {
        text-decoration: none !important;
      }
    }
  }

  .vds-listbox {
    .vds-listbox-item {
      &:active {
        .vds-listbox-item-container {
          color: $color-listbox-text--active;
        }
      }
    }
  }
}
