/***
  vds-menu
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

$color-menu-background: theme(action-secondary);
$color-menu-title: theme(action-toggle);
$color-menu-shadow: rgba($color-cloudy, .4);

@mixin default-menu {
  background-color: $color-menu-background;
  border-radius: 2px;
  box-shadow: 0 0 4px 0 $color-menu-shadow;
  min-width: 64px;
  overflow-x: hidden;
  overflow-y: scroll;
  padding: 4px 0;

  &-title {
    color: $color-menu-title;
    padding: px2em(8px);
  }

  .vds-listbox {
    border: 0;
    overflow-y: auto;
    z-index: $utils-layer-1 - 1;
  }

  .vds-btn-group {
    padding: px2em(8px);
  }
}

.vds-menu {
  @include default-listbox;
  @include default-menu;

  .vds-listbox {
    margin: 0;
    padding: 0;
  }

  @at-root #{&}--absolute {
    @include default-menu;
    display: none;
    position: absolute;
    z-index: $utils-layer-1 - 2;
  }

  @at-root #{&}--fixed {
    @include default-menu;
    display: none;
    position: fixed;
    z-index: $utils-layer-1 - 2;
  }

  @at-root #{&}-curtain {
    background-color: transparent !important;
    display: none;
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: $utils-layer-1 - 3;
  }

  @at-root #{&}--show {
    display: block !important;
  }
}
