/***
  vds-pagination
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/
$vds-step-size: px2em(30px);

// default pagination
$color-pagination-item-text: theme(pagination-item-text);
$color-pagination-item-background: theme(pagination-item-background);
// pagination on hover
$color-pagination-item-background--hover: theme(pagination-item-background--hover);
$color-pagination-item-text--hover: theme(pagination-item-text--hover);
$color-pagination-item-background--hover: theme(pagination-item-background--hover);
// pagination on focus
$color-pagination-item-background--focus: theme(pagination-item-background--focus);
$color-pagination-item-text--focus: theme(pagination-item-text--focus);
$color-pagination-item-background--focus: theme(pagination-item-background--focus);
// pagination on active
$color-pagination-item-text--active: theme(pagination-item-text--active);
$color-pagination-item-background--active: theme(pagination-item-background--active);
$color-pagination-item-border--active: theme(pagination-item-border--active);
// pagination on selected
$color-pagination-item-background--selected: theme(pagination-item-background--selected);
$color-pagination-item-border--selected: theme(pagination-item-border--selected);
$color-pagination-item-text--selected: theme(pagination-item-text--selected);

$color-pagination-dots: theme(pagination--disabled);
$color-pagination-dots--active: theme(action-primary--hover);
$color-pagination--disabled: theme(pagination--disabled);
$color-pagination-button-icon--light: theme(button-icon);

.vds-pagination {
  @extend %default-pagination;
  flex-direction: row;
  flex-wrap: wrap;

  &-control,
  &-control-link,
  &-overflow,
  &-page {
    @extend %default-pagination-step;

    .vds-btn-icon,
    .vds-link {
      border: 1px solid theme(#{transparent});

      &:hover {
        background-color: $color-pagination-item-background--hover;
        color: $color-pagination-item-text--hover;
      }

      &:focus,
      &:focus:not(:active) { // sass-lint:disable-line force-pseudo-nesting
        @include state-focus;
        background-color: $color-pagination-item-background--focus;
        // border: 1px dashed theme(focus-outline-color);
        border-radius: 2.5rem;
        color: $color-pagination-item-text--focus;
        outline: 0;
      }

      &:active,
      &:active:focus { // sass-lint:disable-line force-pseudo-nesting
        background-color: $color-pagination-item-background--active;
        // border: 0px;
        color: $color-pagination-item-text--active;
      }
    }

    .vds-btn-icon {
      @extend %default-pagination-step-button;
      font-size: 0.875rem;
    }

    .vds-link {
      @extend %default-pagination-step-link;
      align-items: center;
      border-radius: 2.5rem;
      display: flex;
      font-weight: 600;
      justify-content: center;

      [class^='vds-icon'] {
        vertical-align: middle;
      }
    }

    &.vds-state--selected {
      %pagination-selected-button-style {
        background-color: $color-pagination-item-background--selected;
        border: 1px solid $color-pagination-item-border--selected;
        color: $color-pagination-item-text--selected;
      }
      .vds-btn-icon,
      .vds-link {
        @extend %pagination-selected-button-style;

        &:focus,
        &:focus:not(:active) { // sass-lint:disable-line force-pseudo-nesting
          @extend %pagination-selected-button-style;
        }

        &:active,
        &:active:focus { // sass-lint:disable-line force-pseudo-nesting
          @extend %pagination-selected-button-style;
        }
      }
    }
  }

  &-control,
  &-control-link,
  &-overflow {
    .vds-btn-icon,
    .vds-link {
      max-width: theme(pagination-page-size) !important;

      &[disabled] {
        color: $color-pagination--disabled !important;
      }
    }
  }

  &-control,
  &-control-link {
    [class^='vds-btn'] {
      &:active {
        [class^='vds-icon'] {
          @include svg-icon($icon-size--tiny, $color-pagination-button-icon--light);
        }
      }

      &[disabled] {
        [class^='vds-icon'] {
          @include svg-icon($icon-size--tiny, $color-pagination--disabled);
        }
      }
    }

    .vds-link {
      [class^='vds-icon'] {
        @include svg-icon($icon-size--tiny, $color-pagination-button-icon--light);
      }

      &.vds-state--disabled {
        [class^='vds-icon'] {
          @include svg-icon($icon-size--tiny, $color-pagination--disabled);
        }
      }
    }
  }

  &-overflow {

    [class^='vds-btn'] {
      &[disabled] {
        [class^='vds-icon'] {
          @include svg-icon($icon-size--tiny, $color-pagination-item-text);
        }
      }
    }

    .vds-link {
      &.vds-state--disabled {
        [class^='vds-icon'] {
          @include svg-icon($icon-size--tiny, $color-pagination-item-text);
        }
      }
    }
  }
}
