@import 'utils';
@import 'colors';
@import 'theme';
@import 'mixins';

/***
  Visa-UI-Web Styleguide Stylesheet
  BEM Naming Convention http://getbem.com/naming/

  @author Visa UX Engineering
 */

*[codify] {
  display: block;
  margin-bottom: 20px;
  width: 100%;
}

*[hidden] {
  display: none !important;
  // visibility:hidden!important;
}

h2 {
  .vds-icon {
    @include svg-icon(.7em);
  }
}

code {
  background-color: $color-fog;
  border-left: 5px solid $color-denim;
  padding: 2px 2px 2px 10px;

  &.block {
    display: block;
  }

  &.inline {
    border-left: 0;
    font-weight: normal;
    padding-left: 2px;
    white-space: nowrap;
  }

  // DIFF
  // dark theme falls back to these also
  .diff {
    &-add,
    &-add * {
      background-color: $color-goblin;
      color: $color-white !important;
    }

    &-del,
    &-del * {
      background-color: $color-crimson;
      color: $color-white !important;
    }
  }

  @at-root .vds-js {
    code {
      color: theme(font-color);
      font-weight: normal;
    }
  }
}

.code-theme--dark {
  code {
    background-color: $color-mine-shaft;
    color: $color-workflow-grey;

    &.inline {
      color: $color-gold;
    }

    a {
      color: $color-gray-suit;

      &:hover {
        color: $color-workflow-grey;
      }

      &:visited,
      &:active {
        color: $color-cloudy;
      }
    }

    // General
    .pln {
      color: $color-workflow-grey;
    }

    // HTML
    .tag {
      color: $color-pigeon-post;
    }

    .atn {
      color: $color-shadow-green;
    }

    .atv {
      color: $color-gold;
    }

    .com {
      // color previously dove-gray
      // changed for a11y color contrast
      color: $color-dusty-gray;
    }

    .lit {
      color: $color-gold;
    }

    // JS
    .pun {
      color: $color-pigeon-post;
    }

    .str {
      color: $color-gold;
    }

    .kwd {
      color: $color-shadow-green;
    }
  }

  .vds-js {
    code {
      color: $color-gold;
    }
  }
}

.styleguide-topics {
  p {
    padding: 5px 20px;
  }
}

.code-container {
  @include display-flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;

  .vds-btn-container {
    flex: 1 0 100%;
    margin-bottom: 0;
  }

  code {
    flex: 1 0 95%;
    margin: 10px auto;
    overflow-x: auto;
    padding-left: 5px;
    width: 100%;

    .indent {
      display: block;
      margin-left: 20px;

      &:first-child {
        margin-left: 0;
      }
    }
  }
}

/* === Code - Fluid Responsive Sizes ========== */
@media #{$media-screen-xs} {
  .vds-grid--fluid {
    .code-container {
      margin: auto;
      max-width: theme(grid-width-min);
      width: 100%;
    }
  }
}

@media #{$media-screen-sm} {
  .vds-grid--fluid {
    .code-container {
      margin: auto;
      max-width: theme(media-break-sm);
      width: 100%;
    }
  }
}

@media #{$media-screen-md} {
  .vds-grid--fluid {
    .code-container {
      margin: auto;
      max-width: theme(media-break-md);
      width: 100%;
    }
  }
}

@media #{$media-screen-lg} {
  .vds-grid--fluid {
    .code-container {
      margin: auto;
      max-width: theme(media-break-lg);
      width: 100%;
    }
  }
}

@media #{$media-screen-xl} {
  .vds-grid--fluid {
    .code-container {
      margin: auto;
      max-width: theme(media-break-xl);
      width: 100%;
    }
  }
}

@media #{$media-screen-xxl} {
  .vds-grid--fluid {
    .code-container {
      margin: auto;
      max-width: theme(grid-width-max);
      width: 100%;
    }
  }
}

.styleguide-margin-top {
  margin-top: 40px;
}

.styleguide-padding-top {
  padding-top: 40px;
}

.styleguide-navbar-example {
  z-index: 10;
}

.styleguide-alert-example {
  z-index: $utils-layer-0 !important;
}

.styleguide-alert-label {
  display: inline-block !important;
}

.styleguide-intro {
  margin: 80px 0 120px;

  h1 {
    margin-bottom: 40px;
  }
}

.styleguide-dependencies {
  margin-top: 10px;
}

.styleguide-img {
  max-width: 500px;
}

.vds-stage-step {
  &.vds-state--accessible {
    &--axe {
      background-color: $color-shadow-green;
    }

    &--vgar {
      background-color: theme(notification-success);
    }
  }
}

.vds-js {
  list-style: line inside;
  padding-left: 20px;

  ul {
    list-style: line inside;
    padding-left: 30px;

    li {
      list-style: line;
    }
  }

  &-event {
    color: $color-green;
    font-weight: theme(font-weight--bold);
  }

  &-condition {
    color: $color-crimson;
    font-weight: theme(font-weight--bold);
  }

  &-action {
    color: $color-denim;
    font-weight: theme(font-weight--bold);
  }

  &-comment {
    color: $color-dove-gray;
    font-weight: theme(font-weight--regular);
  }

  code {
    border-left: 0;
    padding-left: 2px;
  }
}

.resource {
  border: 1px solid $color-gray-suit;
  display: block;
  margin: 0 20px 20px 0;
  padding: 10px;
  transition: .25s ease;

  &:hover {
    border: 1px solid $color-kimberly;
    text-decoration: none !important;

    .vds-fineprint {
      text-decoration: none !important;
    }
  }

  img,
  svg {
    margin-right: 6px;
    max-height: 20px;
    max-width: 20px;
    position: relative;
  }

  img {
    top: 4px;
  }

  svg {
    top: 0;
  }
}

img {
  &.sm-img {
    margin-right: 6px;
    max-height: 20px;
    max-width: 20px;
    position: relative;
    top: 4px;
  }
}

.grid-demo {
  *[class*='vds-grid'] {
    &.highlight-grid {
      border: 1px solid $color-pigeon-pre;
      margin-top: 10px;
    }
  }

  .row {
    border: 1px solid $color-pigeon-pre;
  }

  *[class*='col'] {
    background-color: $color-workflow-grey;

    &:hover {
      background-color: $color-periwinkle-gray !important;
    }

    &:nth-child(2n) {
      background-color: $color-catskill-white;
    }
  }

  &-col-vertical-alignment {
    height: 360px;
  }
}

.vds-data-table {
  &.components {
    .vds-th {
      [class^='vds-icon'] {
        height: 1rem;
        position: relative;
        top: 2px;
        width: 1rem;
      }
    }
  }

  &.browsers {
    width: 100%;

    .vds-tr,
    .vds-td {
      padding: .5em;
      vertical-align: middle;
    }

    .browser-icon {
      height: 32px;
      width: auto;
    }
  }
}

// sass-lint:disable-block no-ids
#components-status-tooltip, // sass-lint:disable-line no-ids
#components-vgar-tooltip,   // sass-lint:disable-line no-ids
#components-js-tooltip {    // sass-lint:disable-line no-ids
  .vds-tooltip-text {
    text-align: left;

    ul {
      padding-left: 20px;
    }
  }
}

.sidebar {
  h2 {
    &:not(:first-child) {
      padding-top: 15px;
    }
  }
}

#icons-list { // sass-lint:disable-line no-ids
  &-lr {
    [class^='vds-icon'] {
      svg {
        height: 1em;
        width: 1em;

        path {
          height: 1em;
          width: 1em;
        }
      }
    }
  }

  &-hr {
    max-height: px2em(360px);

    [class^='vds-icon'] {
      svg {
        height: 1.2em;
        width: 1.2em;

        path {
          height: 2em;
          width: 2em;
        }
      }
    }
  }
}

#icon-icons { // sass-lint:disable-line no-ids
  max-height: 600px;
  overflow-y: auto;
  text-align: center;
}

#icon-code-example { // sass-lint:disable-line no-ids
  text-align: center;

  .code-container {
    text-align: left !important;
  }
}

#vds-icon-defs-value { // sass-lint:disable-line no-ids
  max-height: 300px;
  overflow-x: scroll;
  width: 50vw;
}

.demo-dark-background {
  background-color: $color-mine-shaft;
  border-bottom: 2px solid $color-mine-shaft;
  border-left: 2px solid $color-mine-shaft;
  border-top: 2px solid $color-mine-shaft;
}

#avatar-text-light { // sass-lint:disable-line no-ids
  .vds-avatar {
    background-color: $color-night;
  }
}

.styleguide-inline-note {
  border-left: 4px solid $color-indigo;
  box-sizing: border-box;
  display: inline-block;
  max-width: 100%;
  min-height: 56px;
  padding: 10px 8px 10px 16px;
  position: relative;

  h2,
  h3 {
    &:not(#a) { // sass-lint:disable-line no-ids
      margin: 0 0 4px;
      padding: 0;
    }
  }

  p {
    margin: 8px 0 0;
  }

  .icon-wrapper {
    align-items: center;
    background-color: $color-indigo;
    border-radius: 100%;
    color: $color-white;
    display: flex;
    height: 24px;
    justify-content: center;
    left: -14px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 24px;

    [class^='vds-icon'] {
      @include svg-icon($icon-size--tiny);
    }
  }

  &.info-note {
    background-color: rgba($color-indigo, .1);// sass-lint:disable-line no-color-literals
  }

  &.to-do-note {
    background-color: rgba($color-orange, .1);// sass-lint:disable-line no-color-literals
    border-left-color: $color-orange;

    .icon-wrapper {
      background-color: $color-orange;
    }
  }
}
