/***
  vds-panel
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/
// $vds-panel-height: 100vh;
// $vds-panel-width--open: 422px;
// $vds-panel-width--closed: 62px;
$vds-mainpanel-width: 94%;
$vds-mainpanel-contracted-width: $vds-mainpanel-width - 31%;

$vds-panel-panel-width--open: 360px;
$vds-panel-panel-width--closed: 0px;

$vds-panel-tabs-width: 56px;
$vds-panel-tabs-right--closed: 0;
$vds-panel-tabs-right--open: $vds-panel-panel-width--open;

$vds-panel-height: 100vh;
$vds-panel-width--open: $vds-panel-tabs-width + 1 + $vds-panel-panel-width--open;
$vds-panel-width--closed: $vds-panel-tabs-width + 1 + $vds-panel-panel-width--closed;
$vds-panel-animation-speed: .35s;
$vds-panel-margin-top: 16px;

$vds-mainpanel-color: theme(panel-background);
$vds-panel-border: theme(panel-border);
$vds-panel-shadow: theme(panel-shadow);

// Side panel content
.vds-panel {
  $panel: &;
  background: $vds-mainpanel-color;
  box-shadow: $vds-panel-shadow;
  display: inline-flex;
  float: right;
  min-width: $vds-panel-width--closed;
  width: $vds-panel-width--closed;
  z-index: $utils-layer-0 + 5;

  @media (max-width: $grid-width-lg - 1) {
    display: block;
    height: 100vh;
    margin-top: px2em(-8px);
    min-height: 100vh;
    position: fixed;
    right: 0px;
  }

  &-tabs {
    background: $vds-mainpanel-color;
    border-left: 0;
    border-right: 1px solid $vds-panel-border;
    display: flex;
    flex-direction: column;
    float: left;
    height: 100%;
    position: fixed;
    right: 0;
    user-select: none;
    width: $vds-panel-tabs-width;
    z-index: $utils-layer-0 + 6;

    .vds-tab-list {
      flex-grow: 1;
      margin-top: px2em($vds-panel-margin-top);

      &.vds-panel-tabs-close {
        align-items: flex-end;
        display: flex;
      }

      .vds-tab {
        max-width: $vds-panel-tabs-width;
        min-height: px2em(48px);
      }
    }
  }

  &-panel {
    background: $vds-mainpanel-color;
    float: right;
    overflow-x: hidden;
    overflow-y: auto;
    position: fixed;
    right: 0;
    width: $vds-panel-panel-width--closed;
    z-index: $utils-layer-0 + 6;

    #{$panel}-content {
      display: none;
      font-weight: bold;
      min-height: 100vh;
      padding: $vds-panel-margin-top * 1.5 $vds-panel-margin-top;
      width: $vds-panel-panel-width--open;

      .vds-text--title {
        color: theme(font-color);
        margin-bottom: .5rem;
      }

      .vds-text--subtitle {
        color: theme(font-color);
        margin-bottom: .1rem;
      }
    }
  }

  &.vds-state--open {
    &.vds-state--animated {
      animation: openSidePanel $vds-panel-animation-speed;
      animation-fill-mode: forwards;

      #{$panel}-tabs {
        animation: moveTabsLeft $vds-panel-animation-speed;
        animation-fill-mode: forwards;

        #{$panel}-deactivator {
          visibility: visible;
        }
      }

      #{$panel}-panel {
        animation: openPanel $vds-panel-animation-speed;
        animation-fill-mode: forwards;
      }
    }

    &:not(.vds-state--animated) {
      min-width: $vds-panel-width--open;
      width: $vds-panel-width--open;

      @media (max-width: $grid-width-lg - 1) {
        min-width: calc(100vw - #{$vds-panel-tabs-width});
        width: calc(100vw - #{$vds-panel-tabs-width});
      }

      #{$panel}-tabs {
        right: $vds-panel-tabs-right--open;

        #{$panel}-deactivator {
          visibility: visible;
        }
      }

      #{$panel}-panel {
        width: $vds-panel-panel-width--open;
      }
    }
  }

  &.vds-state--close {
    &.vds-state--animated {
      animation: closeSidePanel $vds-panel-animation-speed;
      animation-fill-mode: forwards;

      #{$panel}-tabs {
        animation: moveTabsRight $vds-panel-animation-speed;
        animation-fill-mode: forwards;

        #{$panel}-deactivator {
          visibility: hidden;
        }
      }

      #{$panel}-panel {
        animation: closePanel $vds-panel-animation-speed;
        animation-fill-mode: forwards;
      }
    }

    &:not(.vds-state--animated) {
      min-width: $vds-panel-width--closed;
      width: $vds-panel-width--closed;

      #{panel}-tabs {
        right: $vds-panel-tabs-right--closed;

        #{panel}-deactivator {
          visibility: hidden;
        }
      }

      #{panel}-panel {
        width: $vds-panel-panel-width--closed;
      }
    }
  }
}

//State-driven
.vds-viewport {
  min-height: 100vh;

  &.vds-state--low {
    .vds-panel {
      min-height: calc(100vh - 72px);
    }
  }

  &.vds-state--lower {
    .vds-panel {
      min-height: calc(100vh - 144px);
    }
  }
}

@keyframes closePanel {
  from { width: $vds-panel-panel-width--open; }
  to { width: $vds-panel-panel-width--closed; }
}

@keyframes openPanel {
  from { width: $vds-panel-panel-width--closed; }
  to { width: $vds-panel-panel-width--open; }
}

@keyframes moveTabsLeft {
  from { right: $vds-panel-tabs-right--closed; }
  to { right: $vds-panel-tabs-right--open;  }
}

@keyframes moveTabsRight {
  from { right: $vds-panel-tabs-right--open; }
  to { right: $vds-panel-tabs-right--closed; }
}

@keyframes openSidePanel {
  from {
    min-width: $vds-panel-width--closed;
    width: $vds-panel-width--closed;
  }

  to {
    min-width: $vds-panel-width--open;
    width: $vds-panel-width--open;
  }
}

@keyframes closeSidePanel {
  from {
    min-width: $vds-panel-width--open;
    width: $vds-panel-width--open;
  }

  to {
    min-width: $vds-panel-width--closed;
    width: $vds-panel-width--closed;
  }
}

@keyframes vdsContentShrink {
  from { width: calc(100vh - #{$vds-panel-width--open}); }
  to { width: calc(100vh - #{$vds-panel-width--closed}); }
}

@keyframes vdsContentGrow {
  from { width: calc(100vh - #{$vds-panel-width--closed}); }
  to { width: calc(100vh - #{$vds-panel-width--open}); }
}

@keyframes contractMain {
  from { width: $vds-mainpanel-width; }
  to {width: $vds-mainpanel-contracted-width;}
}
