/***
  vds Icon
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

$color-icon--fill: theme(secondary);

.vds-icon {

  &,
  &--low {
    @include svg-icon($icon-size--low, $color-icon--fill);
  }

  &--high {
    @include svg-icon($icon-size--high, $color-icon--fill);
  }

  &--tiny {
    @include svg-icon($icon-size--tiny, $color-icon--fill);
  }
}

a {
  .vds-icon {
    &,
    &--low,
    &--high,
    &--tiny {
      @include svg-icon($icon-size--link, theme(link-color));
    }
  }

  &:hover {
    .vds-icon {
      &,
      &--low,
      &--high,
      &--tiny {
        @include svg-icon($icon-size--link, theme(link-color--hover));
      }
    }
  }
}
