/***
  @vds/vds-link
  BEM Naming Convention http://getbem.com/naming/
  PX to Em Conversion http://px-em.com/

  @author dpoynor
*/

a,
button {
  &.vds-link {
    color: theme(link-color);
    text-decoration: theme(link-underline);
    //text-transform: none;

    [class^='vds-icon'] {
      @include svg-icon(px2em($icon-size--tiny), theme(link-color));
      margin: 0 px2em(4px);
      position: relative;
      top: 2px;
    }

    &:visited {
      color: theme(link-color--visited);

      [class^='vds-icon'] {
        @include svg-icon(px2em($icon-size--tiny), theme(link-color--visited));
      }
    }

    &:hover {
      color: theme(link-color--hover);
      text-decoration: theme(link-underline--hover);

      [class^='vds-icon'] {
        @include svg-icon(px2em($icon-size--tiny), theme(link-color--hover));
      }
    }

    &:active {
      color: theme(link-color--active);
      text-decoration: theme(link-underline--hover);

      [class^='vds-icon'] {
        @include svg-icon(px2em($icon-size--tiny), theme(link-color--active));
      }
    }

    &:focus {
      color: theme(link-color--focus);
      text-decoration: theme(link-underline--focus);

      [class^='vds-icon'] {
        @include svg-icon(px2em($icon-size--tiny), theme(link-color--focus));
      }
    }

    &.vds-state--disabled {
      color: theme(link-color--disabled);
      display: inline-block;  /* For IE11/ MS Edge pointer-evetns bug */
      pointer-events: none;

      [class^='vds-icon'] {
        @include svg-icon(px2em($icon-size--tiny), theme(link-color--disabled));
      }
    }

    &.vds-link--inline {
      @include inline-links;
    }
  }
}
