// sass-lint:enable quotes

#vds-card-num-labelledby { // sass-lint:disable-line no-ids
  display: none;
}

.vds-input-card {
  position: relative;

  .vds-input {
    &-option {
      pointer-events: none;
    }

    &-input {
      margin-right: px2em(24px);
    }
  }
}

.vds-card-indicator {
  background: url($generic-img) no-repeat;
  height: 24px;
  position: relative;
  right: 4px;
  width: 38px;

  &--amex {
    background: url($amex-img) no-repeat;
    right: -10px;
  }

  &--discover {
    background: url($discover-img) no-repeat;
  }

  &--elo {
    background: url($elo-img) no-repeat;
    right: -10px;
  }

  &--mastercard {
    background: url($mastercard-img) no-repeat;
  }

  &--visa {
    background: url($visa-img) no-repeat;
  }
}
