/***
  vds-slider
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

$color-track-background: theme(slider-track-background); // $color-smoke
$color-track-border: theme(slider-track-border); // $color-plum
$color-range-background: theme(slider-range-background); // $color-blue
$color-discrete-number-default-ticks: theme(slider-discrete-number-default-ticks); // $color-plum
$color-discrete-number-range-tick: theme(slider-discrete-number-range-tick); // $color-ice

// labels
$color-slider-label-text: theme(slider-slider-label-text); // $color-plum
$color-slider-label-text--active-focus: theme(slider-slider-label-text--active-focus); // $color-light-blue
$color-slider-label-value: theme(slider-slider-label-value); // $color-lead
$color-slider-label--disabled: theme(slider-slider-label--disabled); // $color-cloudy

// thumb: default-value (no selection) state
$thumb-background-default-value--rest: theme(slider-thumb-background-default-value--rest); // $color-white
$thumb-background-default-value--hover: theme(slider-thumb-background-default-value--hover); // $color-white
$thumb-background-default-value--focus: theme(slider-thumb-background-default-value--focus); // $color-white
$thumb-background-default-value--active: theme(slider-thumb-background-default-value--active); // $color-white
$thumb-background-default-value--disabled: theme(slider-thumb-background-default-value--disabled); // $color-white
$thumb-border-default-value--rest: theme(slider-thumb-border-default-value--rest); // $color-blue
$thumb-border-default-value--hover: theme(slider-thumb-border-default-value--hover); // $color-blue
$thumb-border-default-value--focus: theme(slider-thumb-border-default-value--focus); // $color-night
$thumb-border-default-value--active: theme(slider-thumb-border-default-value--active); // $color-night
$thumb-border-default-value--disabled: theme(slider-thumb-border-default-value--disabled); // $color-cloudy
// thumb: other states
$thumb-background--rest: theme(slider-thumb-background--rest); // $color-blue
$thumb-background--hover: theme(slider-thumb-background--hover); // $color-light-blue
$thumb-background--focus: theme(slider-thumb-background--focus); // $color-light-blue
$thumb-background--active: theme(slider-thumb-background--active); // $color-light-blue
$thumb-background--disabled: theme(slider-thumb-background--disabled); // $color-cloudy
$thumb-border--rest: theme(slider-thumb-border--rest); // transparent
$thumb-border--hover: theme(slider-thumb-border--hover); // transparent
$thumb-border--focus: theme(slider-thumb-border--focus); // $color-night
$thumb-border--active: theme(slider-thumb-border--active); // $color-night
$thumb-border--disabled: theme(slider-thumb-border--disabled); // $color-cloudy
// thumb: focus-visible state
$thumb-highlight-background--focus: theme(slider-thumb-highlight-background--focus); // $color-ice
$thumb-highlight-border--focus: theme(slider-thumb-highlight-border--focus); // $color-night

.vds-slider {
  box-sizing: border-box;
  position: relative;
  width: 280px;
  z-index: 1;

  * {
    &,
    &::before,
    &::after {
      box-sizing: inherit;
    }
  }

  &-label {
    display: block;
    padding-bottom: px2em(40px);
    white-space: nowrap;

    &-text {
      color: $color-slider-label-text;
    }

    &-value {
      color: $color-slider-label-value;
      margin-left: px2em(12px);
    }
  }

  &-track {
    background: $color-track-background;
    border-radius: 2px;
    box-shadow: 0 0 0 1px $color-track-border inset;
    cursor: pointer;
    height: 4px;
    position: relative;
    transform-style: preserve-3d;
    width: 100%;
    z-index: 1;
  }

  &-range {
    background: $color-range-background;
    border-radius: 2px;
    height: 4px;
    left: 0;
    position: absolute;
    top: 0;
    width: 0;
  }

  &-thumb {
    background: $thumb-background--rest;
    border: 2px solid $thumb-border--rest;
    border-radius: 100%;
    height: 20px;
    // the commented out styles are not used by React and actually causes issues.
    // leaving here in case we need to revisit
    // - Manny
    // left: -.5px;
    // margin-left: 0;
    // position: absolute;
    // top: -8px;
    transform-style: preserve-3d;
    width: 20px;

    //&.vds-state--tooltip {
    //  &::before {
    //    background-color: $color-white;
    //    border: 1px solid $color-red;
    //    content: attr(data-tooltip-content);
    //    height: px2em(28px);
    //    left: 50%;
    //    line-height: 1;
    //    padding: px2em(6px) px2em(10px);
    //    position: absolute;
    //    top: -40px;
    //    transform: translateX(-50%);
    //    width: auto;
    //  }
    //}

    &:hover {
      background: $thumb-background--hover;
      border: 2px solid $thumb-border--hover;
      cursor: pointer;
    }

    &:active {
      background: $thumb-background--active;
      border: 2px solid $thumb-border--active;
    }

    &:focus {
      outline: none;
    }

    &.focus-visible,
    &.vds-state--active {
      &::after {
        background-color: $thumb-highlight-background--focus;
        border: 1px dashed $thumb-highlight-border--focus;
        content: '';
        height: px2em(40px);
        left: 50%;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%) translateZ(-1px);
        width: px2em(40px);
        z-index: -1;
      }
    }
  }

  &--continuous-number,
  &--discrete-number {
    .vds-slider-thumb {
      border-radius: 0;
      height: px2em(24px);
      // top: -10px;
      width: px2em(8px);


      &::after {
        width: px2em(28px);
      }
    }

    &.vds-state--active {

      .vds-slider-track {
        .vds-slider-thumb {
          &::after {
            width: px2em(28px);
          }
        }
      }
    }
  }

  &-marker-values,
  &-marker-value-icons {
    align-items: center;
    color: $color-gray;
    display: flex;
    font-size: px2em(12px);
    justify-content: space-between;
    list-style-type: none;
    margin: 28px 0 0;
    padding: 0;
  }

  &-marker-value-item {
    flex: 1 1 100%;
    text-align: center;

    &:first-child {
      flex-basis: 50%;
      text-align: left;
    }

    &:last-child {
      flex-basis: 50%;
      text-align: right;
    }
  }

  &--discrete-number {

    .vds-slider-track {
      // using str-slice here since sass vars in gradients dont work
      background-image: linear-gradient(90deg, str-slice($color-discrete-number-default-ticks, 50, -2) 0px, str-slice($color-discrete-number-default-ticks, 50, -2) 4px, transparent 4px, transparent 100%);
      // background-size: calculated in js
      height: 4px;
      width: 100%;
      //left: $color-discrete-number-default-ticks;
    }

    .vds-slider-range {
      // using str-slice here since sass vars in gradients dont work
      background-image: linear-gradient(90deg, str-slice($color-discrete-number-range-tick, 48, -2) 0px, str-slice($color-discrete-number-range-tick, 48, -2) 4px, transparent 4px, transparent 50%);
      // background-size: calculated in js
      height: 4px;
    }
  }

  &.vds-state--default-value {
    .vds-slider-thumb {
      background-color: $thumb-background-default-value--rest;
      border: 2px solid $thumb-border-default-value--rest;
    }
  }

  &:focus-within,
  &.vds-state--active {
    .vds-slider-label-text {
      color: $color-slider-label-text--active-focus;
    }
  }

  &.vds-state--disabled {
    .vds-slider-label-text,
    .vds-slider-label-value,
    .vds-slider-marker-values,
    .vds-slider-marker-icons {
      color: $color-slider-label--disabled;
    }

    [class^='vds-icon'] {
      @include svg-icon(1.5rem, $color-slider-label--disabled);
    }

    .vds-slider-track {
      background: $color-track-background;
      box-shadow: none;
    }

    .vds-slider-range {
      display: none;
    }


    .vds-slider-thumb {
      background-color: $thumb-background--disabled;
      border-color: $thumb-border--disabled;
    }

    &.vds-state--default-value {
      .vds-slider-thumb {
        background-color: $thumb-background-default-value--rest;
        border: 2px solid $thumb-border-default-value--disabled;
      }
    }
  }

  ~ .vds-state--persistent {
    display: block;
    outline: 1px solid $color-red;
  }

  .vds-slider-input {
    -webkit-appearance: none;
    width: 100%;
    
    &::-webkit-slider-runnable-track {
      height: 5px;
      background: $color-track-background;
      border: 1px solid $thumb-border-default-value--rest;
      border-radius: 3px;
    }

    &::-webkit-slider-thumb {
      -webkit-appearance: none;
      border: 2px solid $thumb-border-default-value--rest;
      height: 20px;
      width: 20px;
      border-radius: 50%;
      background: white;
      margin-top: -9px;
    }

    &::-ms-track {
      height: 4px;
      background: transparent;
      border-color: transparent;
      border-width: 10px 0px;
      color: transparent;
    }

    &::-ms-fill-lower {
        background: $color-range-background;
        border-radius: 10px;
    }
    &::-ms-fill-upper {
        background: $color-track-background;
        border-radius: 10px;
    }
    &::-ms-thumb {
        background: white;
        border-radius: 50%;
        border: 2px solid $color-track-border;
        cursor: pointer;
        height: 20px;
        width: 20px;
    }
    &:focus::-ms-fill-lower {
        background: $color-range-background;
    }
    &:focus::-ms-fill-upper {
        background: $color-track-background;
    }
  }
}
