  /***
  vds-dialog
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

// $color-modal-header-text: theme(action-primary--hover);
$dialog-curtain-color: theme(dialog-curtain-color);
$dialog-padding: theme(dialog-padding);

%modal-button--exit {
  border-radius: 32px !important;
  height: 32px !important;
  max-height: 32px !important;
  max-width: 32px !important;
  padding: 0px !important;
  width: 32px !important;
}

%modal-card {
  padding-left: $dialog-padding;
  padding-right: $dialog-padding;
}

.vds-dialog {
  @include display-flex;
  align-items: center;
  height: 100%;
  justify-content: center;
  left: 0px;
  position: fixed;
  top: 0px;
  visibility: hidden;
  width: 100%;
  z-index: $utils-layer-5;

  &-curtain {
    @include xbrowser-property(opacity, 0);
    background-color: $dialog-curtain-color !important;
    height: 100%;
    left: 0px;
    position: fixed;
    top: 0px;
    transition: all .25s ease;
    width: 100%;
    z-index: $utils-layer-5 + 1;
  }

  &-card {
    @include xbrowser-property(opacity, 0);
    background-color: $color-white !important;
    border-top: 4px solid $color-white;
    max-height: 100%;
    max-width: 700px;
    // min-height: 300px;
    // min-width: 400px;
    transition: all .25s ease;
    z-index: $utils-layer-5 + 2;

    @media print {
      & {
        height: auto !important;
        overflow: visible !important;
        overflow-y: visible !important;
        top: 0px !important;
      }
    }

    &--close {
      padding-bottom: 0;
      padding-left: px2em(8px);
      padding-right: px2em(8px);
      padding-top: px2em(4px);

      [class^='vds-btn'] {
        padding-left: 0;
        padding-right: 0;

        .vds-btn-label {
          margin-left: 0;
          margin-right: 0;
        }
      }
    }

    &--head {
      @extend %modal-card;
      padding-bottom: 0px;
      padding-top: px2em(20px);

      &-icon {
        margin-right: px2em(8px);
        position: relative;
        top: px2em(4px);
      }

      &-title {
        @extend %header-title;
        display: inline-block;
      }
    }

    &--body {
      @extend %modal-card;
      line-height: px2em(20px);
      overflow: auto;
      padding: $dialog-padding;
      padding-bottom: px2em(8px);
      padding-top: px2em(8px);

      p {
        margin-bottom: 16px;
      }
    }

    &--footer {
      @extend %modal-card;
      padding: $dialog-padding;
      padding-top: px2em(8px);
    }
  }

  &.vds-state--show {
    // sass-lint:disable-block no-duplicate-properties
    display: -webkit-box !important; /* OLD - iOS 6-, Safari 3.1-6 */
    display: -moz-box !important; /* OLD - Firefox 19- (buggy but mostly works) */
    display: -ms-flexbox !important;
    display: -webkit-flex !important;
    display: flex !important;
    visibility: visible;

    .vds-dialog-card {
      @include xbrowser-property(opacity, 1);
      display: block;
    }

    .vds-dialog-curtain {
      @include xbrowser-property(opacity, 1);
    }
  }

  &.vds-state--closeable {
    .vds-dialog-card--head {
      padding-top: 0;
    }
  }

  &.vds-state--success {
    .vds-dialog-card {
      border-top-color: theme(notification-success);

      &--head {
        .vds-icon {
          @include svg-icon($icon-size--low, theme(notification-success));
        }
      }
    }
  }

  &.vds-state--warning {
    .vds-dialog-card {
      border-top-color: theme(notification-warning);

      &--head {
        .vds-icon {
          @include svg-icon($icon-size--low, theme(notification-foreground--dark));
        }
      }
    }
  }

  &.vds-state--error {
    .vds-dialog-card {
      border-top-color: theme(notification-error);

      &--head {
        .vds-icon {
          @include svg-icon($icon-size--low, theme(notification-error));
        }
      }
    }
  }
}

// to support cases where the curtain
// is not a child of the dialog
// e.g. curtain child of document body
.vds-dialog-curtain {
  &.vds-state--show {
    @include xbrowser-property(opacity, 1);
  }
}
