/***
  vds-progress-gauge
  BEM Naming Convention http://getbem.com/naming/
  PX to Em Conversion http://px-em.com/

  @author dpoynor
*/

// large
$progress-gauge-ring-size--large: px2em(112px);
$progress-gauge-icon-size--large: px2em(96px);
// small
$progress-gauge-ring-size--small: px2em(64px);
$progress-gauge-icon-size--small: px2em(48px);

$color-progress-gauge-background--not-started: theme(progress-gauge-background--not-started);
$color-progress-gauge-background--in-progress: theme(progress-gauge-background--in-progress);
$color-progress-gauge-background--complete: theme(progress-gauge-background--complete);
$color-progress-gauge-background--approved: theme(progress-gauge-background--approved);
$color-progress-gauge-ring--not-started: theme(progress-gauge-ring--not-started);
$color-progress-gauge-ring--in-progress: theme(progress-gauge-ring--in-progress);
$color-progress-gauge-ring--completed: theme(progress-gauge-ring--complete);
$color-progress-gauge-ring--approved: theme(progress-gauge-ring--approved);
$color-progress-gauge-text--not-started: theme(progress-gauge-text--not-started);
$color-progress-gauge-text--in-progress: theme(progress-gauge-text--in-progress);
$color-progress-gauge-text--completed: theme(progress-gauge-text--complete);
$color-progress-gauge-text--approved: theme(progress-gauge-text--approved);
$color-progress-gauge-icon: theme(progress-gauge-icon-color);

.vds-progress-gauge {
  text-align: center;

  &-display {
    align-items: center;
    display: flex;
    height: $progress-gauge-ring-size--large;
    justify-content: center;
    margin: 0 auto px2em(16px);
    position: relative;
    width: $progress-gauge-ring-size--large;
  }

  &-ring {
    height: $progress-gauge-ring-size--large;
    left: 0;
    position: absolute;
    top: 0;
    width: $progress-gauge-ring-size--large;
  }

  &-background {
    @extend %default-spinner-props;
    stroke: $color-progress-gauge-ring--not-started;
    z-index: $utils-layer-3 + 1;
  }

  &-icon {
    @include svg-icon($icon-size--high, $color-white);
    align-items: center;
    background-color: $color-progress-gauge-background--not-started;
    border-radius: 100%;
    display: flex;
    height: $progress-gauge-icon-size--large;
    justify-content: center;
    width: $progress-gauge-icon-size--large;

    & svg {
      fill: $color-progress-gauge-icon !important;
    }
  }

  &-step {
    font-size: px2em(18px);
    margin-bottom: px2em(5px);
  }

  &-label {
    color: $color-progress-gauge-text--not-started;
    margin-top: px2em(8px);
  }

  &.vds-status {

    &--small {
      .vds-progress-gauge {
        &-display {
          height: $progress-gauge-ring-size--small;
          width: $progress-gauge-ring-size--small;
        }

        &-ring {
          height: $progress-gauge-ring-size--small;
          width: $progress-gauge-ring-size--small;
        }

        &-icon {
          @include svg-icon($icon-size--low, $color-white);
          height: $progress-gauge-icon-size--small;
          width: $progress-gauge-icon-size--small;
        }
      }
    }

    &--progress {
      .vds-progress {
        &--spinner {
          &-track {
            height: $progress-gauge-ring-size--large;
            width: $progress-gauge-ring-size--large;
          }

          &-background {
            stroke: $color-progress-gauge-ring--not-started;
          }

          &-bar {
            stroke: $color-progress-gauge-ring--in-progress;
          }
        }
      }


      .vds-progress-gauge {
        &-icon {
          background-color: $color-progress-gauge-background--in-progress;
        }

        &-label {
          color: $color-progress-gauge-text--in-progress;
        }
      }
    }

    &--completed {
      .vds-progress-gauge {
        &-background {
          stroke: $color-progress-gauge-ring--completed;
        }

        &-icon {
          background-color: $color-progress-gauge-background--complete;
        }

        &-label {
          color: $color-progress-gauge-text--completed;
        }
      }
    }

    &--approved {
      .vds-progress-gauge {
        &-background {
          stroke: $color-progress-gauge-ring--approved;
        }

        &-icon {
          background-color: $color-progress-gauge-background--approved;
        }

        &-label {
          color: $color-progress-gauge-text--approved;
        }
      }
    }
  }
}
