/***
  vds-search
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

[type='search'] {
  -moz-appearance: none;
  -webkit-appearance: none;

  &::-ms-clear,
  &::-ms-reveal {
    display: none;
    height: 0;
    width: 0;
  }

  // NOTE: combining -ms and -webkit pseudo element overrides will not work in IE11
  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.vds-search {
  position: relative;

  &:not(.vds-search--dynamic) {
    &:not(.vds-search--expandable) {
      .vds-input {
        &:hover,
        &.vds-state--focus,
        &.vds-state--filled {
          .vds-input-bar {
            &::before {
              border-bottom: 0;
            }
          }
        }
      }
    }
  }

  .vds-input {
    margin-top: 0;
    min-width: px2em(288px);
    width: 100%;

    &,
    &.vds-state--filled {
      .vds-input-bar {
        width: calc(100% - 2px);
      }
    }
  }

  .vds-input--input-text {
    box-sizing: border-box;
    font-size: 1rem;
    height: px2em(48px);
    padding: 0 px2em(8px);
    width: 100%;
  }

  .vds-input-label {
    display: none;
  }

  .vds-search-button {
    position: absolute;
    right: px2em(2px);
    top: calc(50% - 1px);
    transform: translateY(-50%);
    z-index: 1;
  }

  &--dynamic {
    z-index: $utils-layer-4;

    .vds-input--input-text {
      height: px2em(40px);
      padding: 0 px2em(40px);
    }

    .vds-search-icon {
      height: px2em(24px);
      left: px2em(8px);
      position: absolute;
      top: calc(50% - 1px);
      transform: translateY(-50%);
      width: px2em(24px);

      [class^='vds-icon'] {
        @include svg-icon(px2em(24px), $color-indigo);
      }
    }

    .vds-search-clear-btn {
      display: none;
      height: px2em(32px);
      position: absolute;
      right: px2em(4px);
      top: calc(50% - 1px);
      transform: translateY(-50%);
      width: px2em(32px);
    }

    .vds-menu--absolute {
      width: 100%;
    }

    .vds-menu-curtain {
      z-index: -1;
    }
  }

  &--expandable {

    &.vds-search--container {
      min-height: px2em(48px);
    }

    .vds-input {
      margin-left: auto;
      margin-right: px2em(24px);
      max-width: 0;
      min-width: 0;
      overflow: hidden;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .25s ease;
    }

    .vds-input--input-text {
      padding-right: px2em(52px);
    }

    .vds-state--expanded {
      margin-right: 0;
      max-width: 100%;
      min-width: px2em(288px);

      .vds-input-bar {
        width: calc(100% - 2px);
      }
    }
  }

  // for vds-nav-bar inclusion
  &--nav-bar {
    .vds-search-button {
      position: relative;
    }

    .vds-input {
      &,
      &:hover,
      &.vds-state--focus,
      &.vds-state--filled {
        .vds-input-bar {
          border: 0;

          &::before {
            border: 0;
          }
        }
      }
    }
  }
}
