/***
  vds-action-bar
  BEM Naming Convention http://getbem.com/naming/
  PX to Em Conversion http://px-em.com/

  @author dpoynor
*/

$color-action-bar-active-controls-background: theme(action-bar-active-controls-background); // $color-ice

.vds-action-bar {
  padding: 3px 0; // 7px
  //outline: 1px solid $color-red;

  [type='button'] {
    margin: 0;

    &:not(:first-child) {
      margin-left: .5rem; // 8px
    }
  }

  &.row {
    .col {
      align-items: center;
      display: flex;
      flex-direction: row;

      .vds-action-bar-selected-items-count {
        margin-left: .5rem;
      }
    }
  }

  .col--align-end {
    justify-content: flex-end;

    .vds-action-bar-selected-items-count {
      margin-right: .5rem;
    }
  }

  .vds-btn-text,
  .vds-btn-split {
    margin-bottom: 0;
  }

  &--active-controls {
    align-items: center;
    background-color: $color-action-bar-active-controls-background;
    display: flex;
    margin: 0;
    padding-left: .4375rem; // 7px
    padding-right: .4375rem;
  }
}
