/***
  vds-date-picker
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

$color-date-picker-input-btn-icon-background--active: theme(date-picker-input-btn-icon-background--active);
$color-date-picker-input-btn-icon--readonly-disabled: theme(date-picker-input-btn-icon--readonly-disabled);
$color-date-picker-calendar: theme(date-picker-calendar);
$color-date-picker-calendar-shadow: theme(date-picker-calendar-shadow);
$color-date-picker-calendar-header-divider: theme(date-picker-calendar-header-divider);
$color-date-picker-calendar-dates-btn-text: theme(date-picker-calendar-dates-btn-text);
$color-date-picker-calendar-dates-btn-background: theme(date-picker-calendar-dates-btn-background);
$color-date-picker-calendar-dates-btn-background--focus-visible-hover: theme(date-picker-calendar-dates-btn-background--focus-visible-hover);
$color-date-picker-calendar-dates-btn-text--active: theme(date-picker-calendar-dates-btn-text--active);
$color-date-picker-calendar-dates-btn-background--active: theme(date-picker-calendar-dates-btn-background--active);
$color-date-picker-calendar-dates-btn-text--selected: theme(date-picker-calendar-dates-btn-text--selected);
$color-date-picker-calendar-dates-btn-background--selected: theme(date-picker-calendar-dates-btn-background--selected);
$color-date-picker-calendar-dates-btn-previous-and-next-month: theme(date-picker-calendar-dates-btn-previous-and-next-month);
$color-date-picker-calendar-dates-btn-previous-and-next-month--selected: theme(date-picker-calendar-dates-btn-previous-and-next-month--selected);
$color-date-picker-calendar-date-btn-label-border--current: theme(date-picker-calendar-date-btn-label-border--current);
$color-date-picker-calendar-date-btn-label-border--current-and-selected: theme(date-picker-calendar-date-btn-label-border--current-and-selected);

.vds-grid {
  &.vds-date-picker-calendar {
    background-color: $color-date-picker-calendar;
    display: none;
    margin: initial;
    max-width: px2em(324px);
    position: absolute;
    width: px2em(324px);

    .col {
      padding: px2em(8px);
    }
  }

  .vds-date-picker-date-list {
    > .col {
      padding-top: 0;
    }
  }
}

.vds-date-picker {
  display: inline-block;
  max-width: px2em(324px);
  width: px2em(324px);

  &.vds-state--active {
    .vds-input {
      .vds-btn-icon {
        background-color: $color-date-picker-input-btn-icon-background--active;

        [class^='vds-icon'] {
          @include svg-icon(1rem, theme(listbox-text--active));
        }
      }
    }

    .vds-grid {
      z-index: 1;

      &.vds-date-picker-calendar {
        box-shadow: $color-date-picker-calendar-shadow;
        display: block;
        z-index: $utils-layer-1 + 1;
      }
    }
  }

  .vds-input {
    &.vds-state {
      &--disabled,
      &--readonly {
        [class^='vds-icon'] {
          @include svg-icon(1rem, $color-date-picker-input-btn-icon--readonly-disabled);
        }
      }
    }
  }

  &-header,
  &-footer,
  &-days-header {
    .col {
      align-items: center;
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
    }
  }

  &-header {
    .vds-date-picker-title {
      letter-spacing: normal;
      margin: 4px 0;
      text-transform: none;
    }
  }

  &-footer {
    .col {
      &:not(.x) {
        justify-content: flex-end;
        padding-top: 0;
      }
    }
  }

  &-header-divider {
    &[class~='row'] {
      .col {
        overflow: visible;
        padding-bottom: 0;
        padding-top: 0;
      }

      .vds-divider--standard {
        border: 0;
        border-bottom: 1px solid $color-date-picker-calendar-header-divider;
        margin: 0;
        width: 100%;
      }
    }
  }

  &-date-grid {
    ul {
      display: flex;
      flex-flow: row wrap;
      list-style-type: none;
      margin: 0;
      max-width: 100%;
      padding: 0;
      text-align: center;
      width: 100%;
    }
  }

  &-day,
  &-date {
    flex-basis: 14.2857%;
    height: px2em(44px);
    margin: 0;
    padding: 0;

    abbr {
      text-decoration: none;

      &:hover {
        cursor: default;
      }
    }
  }

  &-day {
    height: auto;
  }

  &-month,
  &-year {
    align-items: center;
    display: flex;
    flex-basis: 33%;
    height: px2em(62px);
    justify-content: center;
  }

  &-title,
  &-days-header {
    font-weight: theme(font-weight--semibold);
  }

  .vds-btn-text {
    background-color: $color-date-picker-calendar-dates-btn-background;
    border-radius: 0;
    color: $color-date-picker-calendar-dates-btn-text;
    height: px2em(44px);
    letter-spacing: normal;
    margin: 0;
    min-height: auto;
    min-width: auto;
    text-transform: none;
    width: 100%;

    &.focus-visible,
    &:hover {
      background-color: $color-date-picker-calendar-dates-btn-background--focus-visible-hover;
    }

    &:active {
      background-color: $color-date-picker-calendar-dates-btn-background--active;
      color: $color-date-picker-calendar-dates-btn-text--active;
    }

    &.vds-state--selected {
      background-color: $color-date-picker-calendar-dates-btn-background--selected;
      color: $color-date-picker-calendar-dates-btn-text--selected;
    }

    .vds-btn-label {
      pointer-events: none;
    }
  }

  &-date-list {
    .vds-btn-label {
      height: px2em(32px);
      line-height: 1;
      padding: 0;
      width: px2em(32px);
    }

    .vds-date-picker-month,
    .vds-date-picker-year {
      .vds-btn-label {
        width: calc(100% - 10px);
      }
    }

    .vds-date-picker-state--previous-month-day,
    .vds-date-picker-state--next-month-day {
      -webkit-font-smoothing: antialiased;

      .vds-btn-text {
        .vds-btn-label {
          color: $color-date-picker-calendar-dates-btn-previous-and-next-month;
        }

        &.vds-state--selected {
          .vds-btn-label {
            color: $color-date-picker-calendar-dates-btn-previous-and-next-month--selected;
          }
        }
      }
    }
  }

  &-current {
    .vds-btn-label {
      border: 1px solid $color-date-picker-calendar-date-btn-label-border--current;
      border-radius: 100%;
    }

    &.vds-date-picker-month,
    &.vds-date-picker-year {
      .vds-btn-label {
        border-radius: px2em(16px);
      }
    }

    .vds-state--selected {
      .vds-btn-label {
        border: 1px solid $color-date-picker-calendar-date-btn-label-border--current-and-selected;
      }
    }
  }

  &-close-button {
    margin: 0;

    span {
      pointer-events: none;
    }
  }

  &:not(:last-of-type) {
    margin-right: px2em(6px);
  }

  &-curtain {
    display: none;
    height: 100%;
    left: 0;
    overflow: hidden;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: $utils-layer-1;

    &.vds-state--show {
      display: block;
    }
  }
}

$datepicker__background-color: $color-date-picker-calendar;
$datepicker__border-color: #aeaeae !default;
$datepicker__highlighted-color: $color-date-picker-calendar-dates-btn-background--active;
$datepicker__muted-color: #ccc !default;
$datepicker__selected-color: #216ba5 !default;
$datepicker__text-color: $color-date-picker-calendar-dates-btn-text;
$datepicker__header-color: #000 !default;
$datepicker__navigation-disabled-color: $color-date-picker-input-btn-icon--readonly-disabled;

// $datepicker__border-radius: 0.3rem !default;
$datepicker__day-margin: 0.166rem !default;
// $datepicker__font-size: 0.8rem !default;
// $datepicker__font-family: "Helvetica Neue", Helvetica, Arial,
//   sans-serif !default;
$datepicker__item-size: 1.7rem !default;
$datepicker__margin: 0.4rem !default;
$datepicker__navigation-size: 0.45rem !default;
$datepicker__triangle-size: 8px !default;

%triangle-arrow {
  margin-left: -$datepicker__triangle-size;
  position: absolute;

  &,
  &::before {
    box-sizing: content-box;
    position: absolute;
    border: $datepicker__triangle-size solid transparent;

    height: 0;
    width: 1px;
  }

  &::before {
    content: "";
    z-index: -1;
    border-width: $datepicker__triangle-size;

    left: -$datepicker__triangle-size;
    border-bottom-color: $datepicker__border-color;
  }
}

%triangle-arrow-up {
  @extend %triangle-arrow;

  top: 0;
  margin-top: -$datepicker__triangle-size;

  &,
  &::before {
    border-top: none;
    border-bottom-color: $datepicker__background-color;
  }

  &::before {
    top: -1px;
    border-bottom-color: $datepicker__border-color;
  }
}

%triangle-arrow-down {
  @extend %triangle-arrow;

  bottom: 0;
  margin-bottom: -$datepicker__triangle-size;

  &,
  &::before {
    border-bottom: none;
    border-top-color: #fff;
  }

  &::before {
    bottom: -1px;
    border-top-color: $datepicker__border-color;
  }
}


.react-datepicker-wrapper {
  display: inline-block;
  padding: 0;
  border: 0;
}

.react-datepicker-wrapper .vds-input-bar {
  align-items: center;
}

.react-datepicker {
  // font-family: $datepicker__font-family;
  // font-size: $datepicker__font-size;
  background-color: #fff;
  color: $datepicker__text-color;
  // border: 1px solid $datepicker__border-color;
  // border-radius: $datepicker__border-radius;
  box-shadow: $color-date-picker-calendar-shadow;
  display: inline-block;
  position: relative;
  // width: px2em(324px);
}

.react-datepicker--time-only {
  .react-datepicker__triangle {
    left: 35px;
  }

  .react-datepicker__time-container {
    border-left: 0;
  }

  .react-datepicker__time {
    border-radius: 0.3rem;
  }

  .react-datepicker__time-box {
    border-radius: 0.3rem;
  }
}

.react-datepicker__triangle {
  position: absolute;
  left: 50px;
}

.react-datepicker-popper {
  z-index: 1;

  &[data-placement^="bottom"] {
    margin-top: $datepicker__triangle-size + 2px;

    .react-datepicker__triangle {
      @extend %triangle-arrow-up;
    }
  }
  
  &[data-placement="bottom-end"],
  &[data-placement="top-end"] {
    .react-datepicker__triangle {
      left: auto;
      right: 50px;
    }
  }

  &[data-placement^="top"] {
    margin-bottom: $datepicker__triangle-size + 2px;

    .react-datepicker__triangle {
      @extend %triangle-arrow-down;
    }
  }

  &[data-placement^="right"] {
    margin-left: $datepicker__triangle-size;

    .react-datepicker__triangle {
      left: auto;
      right: 42px;
    }
  }

  &[data-placement^="left"] {
    margin-right: $datepicker__triangle-size;

    .react-datepicker__triangle {
      left: 42px;
      right: auto;
    }
  }
}

.react-datepicker__header {
  text-align: center;
  // background-color: $datepicker__background-color;
  // border-bottom: 1px solid $datepicker__border-color;
  // border-top-left-radius: $datepicker__border-radius;
  // border-top-right-radius: $datepicker__border-radius;
  padding: 0.5rem 0.5rem 0;
  position: relative;

  &--time {
    padding-bottom: 8px;
    padding-left: 5px;
    padding-right: 5px;
  }
}

.react-datepicker__year-dropdown-container--select,
.react-datepicker__month-dropdown-container--select,
.react-datepicker__month-year-dropdown-container--select,
.react-datepicker__year-dropdown-container--scroll,
.react-datepicker__month-dropdown-container--scroll,
.react-datepicker__month-year-dropdown-container--scroll {
  display: inline-block;
  margin: 0 2px;
}

.react-datepicker__current-month,
.react-datepicker-time__header,
.react-datepicker-year-header {
  color: $datepicker__header-color;
  font-weight: bold;
  margin-top: 0;
  padding: 0.5rem 0 1rem;
  // font-size: $datepicker__font-size * 1.18;
}

.react-datepicker-time__header {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.react-datepicker__navigation {
  background: none;
  // line-height: $datepicker__item-size;
  text-align: center;
  cursor: pointer;
  position: absolute;
  top: 10px;
  width: 0;
  padding: 0;
  // border: $datepicker__navigation-size solid transparent;
  z-index: 1;
  // height: 10px;
  width: 10px;
  text-indent: -999em;
  overflow: hidden;

  &--previous {
    left: 10px;
    // border-right-color: $datepicker__muted-color;

    &:hover {
      // border-right-color: darken($datepicker__muted-color, 10%);
    }

    &--disabled,
    &--disabled:hover {
      // border-right-color: $datepicker__navigation-disabled-color;
      cursor: default;
    }
  }

  &--next {
    right: 10px;
    // border-left-color: $datepicker__muted-color;
    &--with-time:not(&--with-today-button) {
      right: 80px;
    }

    &:hover {
      // border-left-color: darken($datepicker__muted-color, 10%);
    }

    &--disabled,
    &--disabled:hover {
      // border-left-color: $datepicker__navigation-disabled-color;
      cursor: default;
    }
  }

  &--years {
    position: relative;
    top: 0;
    display: block;
    margin-left: auto;
    margin-right: auto;

    &-previous {
      top: 4px;
      border-top-color: $datepicker__muted-color;

      &:hover {
        border-top-color: darken($datepicker__muted-color, 10%);
      }
    }

    &-upcoming {
      top: -4px;
      border-bottom-color: $datepicker__muted-color;

      &:hover {
        border-bottom-color: darken($datepicker__muted-color, 10%);
      }
    }
  }
}

.react-datepicker__month-container {
  float: left;
}

.react-datepicker__month {
  margin: $datepicker__margin;
  text-align: center;
  .react-datepicker__month-text,
  .react-datepicker__quarter-text {
    display: inline-block;
    width: 4rem;
    margin: 2px;
  }
}

.react-datepicker__input-time-container {
  clear: both;
  width: 100%;
  float: left;
  margin: 5px 0 10px 15px;
  text-align: left;
  .react-datepicker-time__caption {
    display: inline-block;
  }
  .react-datepicker-time__input-container {
    display: inline-block;
    .react-datepicker-time__input {
      display: inline-block;
      margin-left: 10px;
      input {
        width: 85px;
      }
      input[type="time"]::-webkit-inner-spin-button,
      input[type="time"]::-webkit-outer-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }
      input[type="time"] {
        -moz-appearance: textfield;
      }
    }
    .react-datepicker-time__delimiter {
      margin-left: 5px;
      display: inline-block;
    }
  }
}

.react-datepicker__time-container {
  float: right;
  border-left: 1px solid $datepicker__border-color;
  width: 85px;

  &--with-today-button {
    display: inline;
    border: 1px solid #aeaeae;
    border-radius: 0.3rem;
    position: absolute;
    right: -72px;
    top: 0;
  }

  .react-datepicker__time {
    position: relative;
    background: white;

    .react-datepicker__time-box {
      width: 85px;
      overflow-x: hidden;
      margin: 0 auto;
      text-align: center;
      ul.react-datepicker__time-list {
        list-style: none;
        margin: 0;
        height: calc(195px + (#{$datepicker__item-size} / 2));
        overflow-y: scroll;
        padding-right: 0px;
        padding-left: 0px;
        width: 100%;
        box-sizing: content-box;

        li.react-datepicker__time-list-item {
          height: 30px;
          padding: 5px 10px;
          white-space: nowrap;
          &:hover {
            cursor: pointer;
            background-color: $datepicker__background-color;
          }
          &--selected {
            background-color: $datepicker__selected-color;
            color: white;
            font-weight: bold;
            &:hover {
              background-color: $datepicker__selected-color;
            }
          }
          &--disabled {
            color: $datepicker__muted-color;

            &:hover {
              cursor: default;
              background-color: transparent;
            }
          }
        }
      }
    }
  }
}

.react-datepicker__week-number {
  color: $datepicker__muted-color;
  display: inline-block;
  width: $datepicker__item-size;
  line-height: $datepicker__item-size;
  text-align: center;
  margin: $datepicker__day-margin;
  &.react-datepicker__week-number--clickable {
    cursor: pointer;
    &:hover {
      // border-radius: $datepicker__border-radius;
      background-color: $datepicker__background-color;
    }
  }
}

.react-datepicker__day-names,
.react-datepicker__week {
  white-space: nowrap;
}

.react-datepicker__day-names {
  border-top: 1px solid theme(date-picker-calendar-header-divider);

  .react-datepicker__day-name {
    height: inherit;
    line-height: inherit;
    padding-top: 0.5rem;
  }
}

.react-datepicker__day-name,
.react-datepicker__day,
.react-datepicker__time-name {
  color: $datepicker__text-color;
  display: inline-block;
  height: 2.75rem;
  line-height: 2.75rem;
  // margin: $datepicker__day-margin;
  text-align: center;
  width: 2.75rem;
}

.react-datepicker__month,
.react-datepicker__quarter {
  &--selected,
  &--in-selecting-range,
  &--in-range {
    // border-radius: $datepicker__border-radius;
    background-color: $color-date-picker-calendar-dates-btn-background--selected; //$datepicker__selected-color;
    color: #fff;

    &:hover {
      background-color: $color-date-picker-calendar-dates-btn-background--focus-visible-hover;
    }
  }
  &--disabled {
    color: $datepicker__muted-color;
    pointer-events: none;
    &:hover {
      cursor: default;
      background-color: transparent;
    }
  }
}

.react-datepicker__day,
.react-datepicker__month-text,
.react-datepicker__quarter-text {
  cursor: pointer;

  &:hover {
    // border-radius: $datepicker__border-radius;
    background-color: $color-date-picker-calendar-dates-btn-background--focus-visible-hover;
  }

  &--today {
    border: 1px solid $color-date-picker-calendar-date-btn-label-border--current;
    border-radius: 50%;
    font-weight: bold;
  }

  &--highlighted {
    // border-radius: $datepicker__border-radius;
    background-color: $color-date-picker-calendar-dates-btn-background--selected;
    color: #fff;

    &:hover {
      background-color: $color-date-picker-calendar-dates-btn-background--focus-visible-hover;
      color: $color-date-picker-calendar-dates-btn-text;
    }

    &-custom-1 {
      color: magenta;
    }

    &-custom-2 {
      color: green;
    }
  }

  &--selected,
  &--in-selecting-range,
  &--in-range {
    // border-radius: $datepicker__border-radius;
    background-color: $color-date-picker-calendar-dates-btn-background--selected;
    color: #fff;

    &:hover {
      background-color: $color-date-picker-calendar-dates-btn-background--focus-visible-hover;
      color: $color-date-picker-calendar-dates-btn-text;
    }
  }

  &--keyboard-selected {
    // border-radius: $datepicker__border-radius;
    background-color: lighten($datepicker__selected-color, 10%);
    color: #fff;

    &:hover {
      background-color: darken($datepicker__selected-color, 5%);
    }
  }

  &--in-selecting-range:not(&--in-range) {
    background-color: rgba($datepicker__selected-color, 0.5);
  }

  &--in-range:not(&--in-selecting-range) {
    .react-datepicker__month--selecting-range & {
      background-color: $datepicker__background-color;
      color: $datepicker__text-color;
    }
  }

  &--disabled {
    cursor: default;
    color: $datepicker__muted-color;

    &:hover {
      background-color: transparent;
    }
  }
}

.react-datepicker__month-text,
.react-datepicker__quarter-text {
  &.react-datepicker__month--selected,
  &.react-datepicker__month--in-range,
  &.react-datepicker__quarter--selected,
  &.react-datepicker__quarter--in-range {
    &:hover {
      background-color: $datepicker__selected-color;
    }
  }
  &:hover {
    background-color: $datepicker__background-color;
  }
}

.react-datepicker__input-container {
  position: relative;
  display: inline-block;
  width: 100%;
}

.react-datepicker__year-read-view,
.react-datepicker__month-read-view,
.react-datepicker__month-year-read-view {
  border: 1px solid transparent;
  // border-radius: $datepicker__border-radius;

  &:hover {
    cursor: pointer;

    .react-datepicker__year-read-view--down-arrow,
    .react-datepicker__month-read-view--down-arrow {
      border-top-color: darken($datepicker__muted-color, 10%);
    }
  }

  &--down-arrow {
    @extend %triangle-arrow-down;
    border-top-color: $datepicker__muted-color;
    float: right;
    margin-left: 20px;
    top: 8px;
    position: relative;
    border-width: $datepicker__navigation-size;
  }
}

.react-datepicker__year-dropdown,
.react-datepicker__month-dropdown,
.react-datepicker__month-year-dropdown {
  background-color: $datepicker__background-color;
  position: absolute;
  width: 50%;
  left: 25%;
  top: 30px;
  z-index: 1;
  text-align: center;
  // border-radius: $datepicker__border-radius;
  border: 1px solid $datepicker__border-color;

  &:hover {
    cursor: pointer;
  }

  &--scrollable {
    height: 150px;
    overflow-y: scroll;
  }
}

.react-datepicker__year-option,
.react-datepicker__month-option,
.react-datepicker__month-year-option {
  line-height: 20px;
  width: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;

  &:first-of-type {
    // border-top-left-radius: $datepicker__border-radius;
    // border-top-right-radius: $datepicker__border-radius;
  }

  &:last-of-type {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    // border-bottom-left-radius: $datepicker__border-radius;
    // border-bottom-right-radius: $datepicker__border-radius;
  }

  &:hover {
    background-color: $datepicker__muted-color;

    .react-datepicker__navigation--years-upcoming {
      border-bottom-color: darken($datepicker__muted-color, 10%);
    }

    .react-datepicker__navigation--years-previous {
      border-top-color: darken($datepicker__muted-color, 10%);
    }
  }

  &--selected {
    position: absolute;
    left: 15px;
  }
}

.react-datepicker__close-icon {
  cursor: pointer;
  background-color: transparent;
  border: 0;
  outline: 0;
  padding: 0px 6px 0px 0px;
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  display: table-cell;
  vertical-align: middle;

  &::after {
    cursor: pointer;
    background-color: $datepicker__selected-color;
    color: #fff;
    border-radius: 50%;
    height: 16px;
    width: 16px;
    padding: 2px;
    font-size: 12px;
    line-height: 1;
    text-align: center;
    display: table-cell;
    vertical-align: middle;
    content: "\00d7";
  }
}

