/***
  vds-data-table
  BEM Naming Convention http://getbem.com/naming/
  PX to Em Conversion http://px-em.com/

  author sacuna
  @author dpoynor
*/

$color-header-text: theme(data-table-header-text); // $color-indigo
$color-header-background: theme(data-table-header-background); // $color-white
$color-header-underline: theme(data-table-header-underline); // $color-indigo
$color-row-background: theme(data-table-row-background); // $color-white
$color-row-background--alt: theme(data-table-row-background--alt); // $color-fog
$color-row-background--hover: theme(data-table-row-background--hover); // $color-ice
$color-row-background--pressed: theme(data-table-row-background--pressed); // $color-smoke
$color-row-outline--focus: theme(focus-outline-color); // $color-night
$color-row-background--focus: theme(data-table-row-background--focus); // $color-ics
$color-row-background--selected: theme(data-table-row-background--selected); // $color-smoke
$color-divider-line: theme(data-table-divider-line); // $color-smoke

$table-padding: theme(data-table-padding); // 14px
$table-header-height: theme(data-tableheader-height); // 48px
$table-row-height: theme(data-tablerow-height); // 72px
$table-row-height--compact: theme(data-tablerow-height--compact); // 44px
$table-header-font-weight: theme(font-weight--semibold);

.vds {

  &-thead {
    .vds-th {
      background-color: $color-header-background;
      box-shadow: 0 -2px 0 $color-header-underline inset;
    }
  }

  &-tbody {
    .vds-tr {
      &:nth-child(odd) {
        background: $color-row-background;
      }

      &:nth-child(even) {
        background: $color-row-background--alt;
      }
    }
  }

  &-th {
    color: $color-header-text;
    font-weight: $table-header-font-weight;
    height: $table-header-height;
    text-align: left;
    vertical-align: middle;
  }

  &-td {
    height: $table-row-height;
  }

  &-td,
  &-th {
    padding: 0 $table-padding;
  }

  &-data-table {
    border-collapse: collapse;
    width: 100%;

    &.vds-state {

      &--single-select,
      &--multi-select {
        .vds-tbody {
          .vds-tr {
            &:hover {
              background-color: $color-row-background--hover;
            }

            &:active {
              background-color: $color-row-background--pressed;
            }

            &:focus {
              background-color: $color-row-background--focus;
              outline: 1px dashed $color-row-outline--focus;

              &.vds-state--selected {
                background-color: $color-row-background--selected;
              }
            }

            &.vds-state--selected {
              background-color: $color-row-background--selected;
            }
          }
        }
      }

      &--multi-select {
        .vds-th,
        .vds-td {
          &:first-child,
          &:first-child {
            padding: 0 0 0 px2em(5px);
          }

          &:nth-child(2),
          &:nth-child(2) {
            padding-left: 0;
          }
        }
      }

      &--compact {
        .vds-td {
          height: $table-row-height--compact;
        }
      }

      &--customizable-columns {
        .vds-thead {
          .vds-data-table--column-control {
            padding-right: 0;
          }
        }
      }

      &--key-value-pairs {
        .vds-th {
          background-color: theme(#{transparent});
          box-shadow: none;
          font-weight: $table-header-font-weight;
        }

        .vds-tr {
          &:nth-child(odd) {
            background: $color-row-background--alt;
          }

          &:nth-child(even) {
            background: $color-row-background;
          }
        }
      }

      &--divider-lines {

        .vds-tr {
          &:nth-child(n) { // override even/odd selector
            background-color: $color-row-background;
          }

          &:not(:last-child) {
            .vds-td,
            .vds-th {
              border-bottom: 1px solid $color-divider-line;
            }
          }
        }
      }

      &--reveal {
        &-first-column {
          .vds-th,
          .vds-td {
            &:first-child,
            &:first-child {
              display: none;
            }
          }
        }

        &-last-column {
          .vds-th,
          .vds-td {
            &:last-child,
            &:last-child {
              display: none;
            }
          }
        }

        &:hover {
          .vds-th,
          .vds-td,
          .vds-th,
          .vds-td {
            &:first-child,
            &:first-child,
            &:last-child,
            &:last-child {
              display: table-cell;
            }
          }

          .vds-data-table-inline-actions-cell {
            padding: 0 $table-padding;
          }

          .vds-btn-group {
            display: block;
          }
        }

        &.vds-state--multi-select {
          .vds-th,
          .vds-td {
            &:nth-child(2),
            &:nth-child(2) {
              padding-left: $table-padding;
            }
          }
        }
      }

      &--sticky-header {
        .vds-th {
          position: sticky;
          //top: 72px; // defined dynamically via js
        }
      }
    }

    &-inline-actions {

      &-cell {
        padding: 0;
        width: 0;

        .vds-btn-group {
          display: none;
          white-space: nowrap;
        }
      }

      &-row {
        &:hover {
          .vds-data-table-inline-actions-cell {
            padding: 0 $table-padding;
          }

          .vds-btn-group {
            display: block;
          }
        }
      }
    }

    &--border-left {
      .vds-th,
      .vds-td {
        &:not(:first-child),
        &:not(:first-child) {
          border-left: 1px solid $color-divider-line;
        }
      }
    }

    .vds-btn-icon {
      display: inline-block;
      margin-right: px2em(4px);
    }

    // helper classes

    .vds-data-table-cell-border-left {
      border-left: 1px solid $color-divider-line;
    }

    .vds-data-table-color-background-alt {
      background-color: $color-row-background--alt;
    }

    .vds-text--right {
      text-align: right;
    }
  }

  &-pagination-bar {

    .col {
      align-items: center;
      display: flex;

      &.col--no-gutter {
        flex-direction: row;
      }

      &--align-end {
        justify-content: flex-end;
      }
    }
  }

  &-pagination {
    margin-right: 0;
    width: auto;

    &-page-items-count {
      margin-left: px2em(27px);
    }

    .vds-btn-split {
      margin: 0;
    }
  }

  &-btn-sortable {
    @include state-focus;
    -webkit-font-smoothing: antialiased; // to look more like Sketch mock
    align-items: center;
    background: theme(#{transparent});
    border: 0;
    box-sizing: border-box;
    color: theme(font-color);
    cursor: pointer;
    display: inline-flex;
    font-family: theme(font);
    font-size: theme(font-size);
    font-weight: theme(font-weight--semibold); // match body--alt
    height: auto;
    justify-content: flex-start;
    outline: none;
    padding: 0;
    text-align: left;

    &-icon {
      display: flex;
      height: px2em(32px);
      width: px2em(32px);

      .vds-icon--tiny {
        margin: auto;
      }
    }
  }
}
