/***
  vds Accordion
  BEM Naming Convention http://getbem.com/naming/

  @author nrusso
*/

$color-accordion-stroke: theme(button--default-text--disabled-background); // $color-smoke
$color-accordion-text: theme(accordion-header); // $color-indigo
$color-accordion-background: theme(accordion-background); //$color-white
$color-accordion-collapsed-hover-stroke: theme(button--default-text--disabled-background); // $color-smoke
$color-accordion-hover-text: theme(link-color); // $color-light-blue
$color-accordion-hover-background: theme(button--secondary-text--background--hover); // $color-ice
$color-accordion-focus-stroke: theme(focus-outline-color); // $color-smoke
$color-accordion-focus-text: theme(link-color); // $color-light-blue
$color-accordion-active-stroke: theme(accordion-header); // $color-indigo
$color-accordion-active-text: theme(link-color); // $color-light-blue
$color-accordion-expanded-text: theme(accordion-header); // $color-indigo
$color-accordion-expanded-stroke: theme(accordion-header); // $color-indigo
$color-accordion-expanded-hover-stroke: theme(accordion-header); // $color-indigo
$color-accordion-disabled-stroke: theme(button--default-text--disabled-background); //$color-smoke
$color-accordion-disabled-text: theme(link-color--disabled); // $color-cloudy
$color-accordion-icon--fill: theme(secondary);

.vds-accordion {
  // accordion elements
  &-heading {
    align-items: center;
    background-color: $color-accordion-background;
    border: 1px solid theme(#{transparent});
    border-bottom-color: $color-accordion-stroke;
    cursor: pointer;
    display: flex;
    height: 48px;
    padding: 0;
    padding-bottom: 15px;
    padding-top: 15px;
    text-align: left;
    width: 100%;

    &:hover {
      background-color: $color-accordion-hover-background;

      .vds-accordion-title,
      .vds-accordion-subtitle {
        color: $color-accordion-hover-text;
      }

      [class^='vds-icon'] {
        @include svg-icon($icon-size--tiny, $color-accordion-hover-text);
      }
    }

    &:focus {
      outline: 1px dashed $color-accordion-focus-stroke;
      outline-offset: 1px;

      .vds-accordion-title,
      .vds-accordion-subtitle {
        color: $color-accordion-focus-text;
      }
    }

    &:active {
      border: 1px solid #{transparent};
      border-bottom: 2px solid $color-accordion-active-stroke;
      padding-bottom: 14px;
    }

    &.vds-state {
      // active
      &--active {
        & {
          border-bottom: 2px solid $color-accordion-active-stroke;
          padding-bottom: 14px;
        }

        &.vds-state--focus {
          [class^='vds-icon'] {
            @include svg-icon($icon-size--tiny, $color-accordion-icon--fill);
          }

          .vds-accordion-title,
          .vds-accordion-subtitle {
            color: $color-accordion-text;
          }
        }
      }
    }
  }

  [class^='vds-icon'] {
    display: inline-block;
    padding: 0 16px;
    pointer-events: none;
  }

  &-title,
  &-subtitle {
    @extend %header-subtitle;
    color: $color-accordion-text;
    display: inline-block !important;
    font-family: theme(font);
    margin-bottom: 0;
    top: 0;
  }

  &-subtitle {
    font-size: .875rem;
    margin-left: 25%;
  }

  &-body {
    padding: 16px;
  }

  &.vds-state {
    &--disabled {
      .vds-accordion-heading {
        &.vds-state--active {
          border-bottom-color: $color-accordion-stroke;
        }
      }

      .vds-accordion-title,
      .vds-accordion-subtitle {
        color: $color-accordion-disabled-text;
      }

      .vds-accordion-body *,
      .vds-accordion-body *::before,
      .vds-accordion-body *::after {
        border-color: $color-accordion-stroke;
        color: $color-accordion-disabled-text;
        outline-color: $color-accordion-stroke;
      }

      [class^='vds-icon'] {
        @include svg-icon($icon-size--tiny, $color-accordion-disabled-text);
      }
    }
  }
}
