/***
  vds-toggle-button
  BEM Naming Convention http://getbem.com/naming/

  @author dpoynor
*/

$color-toggle-button-text--default: theme(button--secondary-text);
$color-toggle-button-background--default: theme(button--secondary-text--background);
$color-toggle-button-text--hover: theme(button--secondary-text--hover);
$color-toggle-button-background--hover: theme(button--secondary-text--background--hover);
$color-toggle-button-text--selected: theme(button--secondary-text--active);
$color-toggle-button-background--selected: theme(button--secondary-text--background--active);
$color-toggle-button-outline--focused: theme(focus-outline-color);
$toggle-button-corner-radius: theme(button--radius);
$color-checkbox-focus-highlight-background: theme(radio-outline-background--focus); // $color-light-blue

@mixin focus-selected {
  &.vds-state--focus {
    background-color: $color-checkbox-focus-highlight-background;
    outline: 1px dashed $color-toggle-button-outline--focused;
    outline-offset: -4px;
  }

  &.vds-state--selected {
    background-color: $color-toggle-button-background--selected;
    color: $color-toggle-button-text--selected;
    padding-right: px2em(36px);

    &.vds-state--focus {
      outline: 1px dashed $color-toggle-button-background--default;
    }
  }
}

.vds-toggle-button {
  clip: rect(0, 0, 0, 0);
  position: absolute;

  // apply WITH the polyfill
  &.focus-visible {
    + .vds-toggle-button-label {
      @include focus-selected;
    }
  }

  // apply WITHOUT the polyfill
  @at-root html:not(.js-focus-visible) {
    .vds-toggle-button-label {
      @include focus-selected;
    }
  }

  &-set {
    border: 0;
    margin: 0 0 px2em(8px);
    padding: 0;

    &-legend {
      margin-bottom: px2em(8px);
    }
  }

  &-list {
    display: inline-flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  &-item {

    &:first-of-type {
      .vds-toggle-button-label {
        border-bottom-left-radius: $toggle-button-corner-radius;
        border-left-width: 2px;
        border-top-left-radius: $toggle-button-corner-radius;
      }
    }

    &:last-of-type {
      .vds-toggle-button-label {
        border-bottom-right-radius: $toggle-button-corner-radius;
        border-right-width: 2px;
        border-top-right-radius: $toggle-button-corner-radius;
      }
    }
  }

  &-label {
    @include display-flex;
    -webkit-font-smoothing: antialiased; // match mockup aliasing on semibold text
    align-items: center;
    background-color: $color-toggle-button-background--default;
    border: 2px solid $color-toggle-button-background--selected;
    border-left-width: 1px;
    border-right-width: 1px;
    color: $color-toggle-button-text--default;
    cursor: pointer;
    font-weight: 600;
    height: px2em(40px);
    line-height: 1;
    padding-left: px2em(14px);
    padding-right: px2em(14px);
    position: relative;
    text-transform: uppercase;
    transition: background .25s ease;
    vertical-align: middle;
    white-space: nowrap;

    &:hover {
      background-color: $color-toggle-button-background--hover;
      color: $color-toggle-button-text--hover;
    }

    &.vds-state--selected {
      background-color: $color-toggle-button-background--selected;
      color: $color-toggle-button-text--selected;
      padding-right: px2em(36px);

      &::after {
        border-bottom: 2px solid;
        border-left: 2px solid;
        content: '';
        height: px2em(10px);
        margin-bottom: auto;
        margin-right: px2em(2px);
        margin-top: auto;
        position: absolute;
        right: px2em(6px);
        top: px2em(10px);
        transform: rotate(-45deg);
        width: px2em(20px);
      }

      &.vds-state--focus {
        &::before {
          border-color: $color-toggle-button-background--default;
          display: block;
        }
      }
    }

    [class^='vds-icon'] {
      &:first-child {
        margin-right: px2em($icon-size--tiny);
      }
    }

  }
}
