/***
  vds-nav
  BEM Naming Convention http://getbem.com/naming/
  PX to Em Conversion http://px-em.com/

  @author shubgupt
*/
$parent: vds-nav;

%navlink-label {
  @extend %navlink-label;
  display: inline;
  flex: 0;
  padding: 0;
}

.vds-nav {
  background: theme(nav-default-background);
  color: theme(navigation-foreground);

  .#{$parent}-skip {
    background-color: theme(nav-skip-background);
    bottom: 0;
    display: block;
    left: -99999px;
    padding: 20px 0;
    position: absolute;
    text-align: center;
    top: 0;
    // @TODO move z-index to absolute/fixed nav only
    z-index: $utils-layer-4 + 5;

    &:focus {
      left: 0;
      right: 0;
    }
  }

  .row {
    .col {
      flex-direction: row;
    }
  }

  .#{$parent}-panel {
    background: theme(nav-default-background);
  }

  .#{$parent}-brand {
    // display: flex;
    @include display-flex;
    flex-direction: row;
    padding: 0 1rem;

    &-info {
      @include display-flex;
      max-width: 100%;
    }

    &-cobrand {
      font-size: 22px;
      font-weight: 300;
      line-height: 64px;
      padding-left: 1rem;
    }

    .vds-btn-icon {
      // @TODO move z-index to absolute/fixed nav only
      z-index: $utils-layer-4 + 4;

      @media (min-width: $grid-width-lg) {
        display: none;
      }
    }

    &-logo {
      display: block;
      margin: auto;
      max-height: 20px;
      padding: 18px 0;

      @media (min-width: $grid-width-lg) {
        margin: 0;
        padding: 22px 0;
      }

      &-image {
        height: 100%;
        width: 60px;
      }

      &:focus {
        outline: 1px dotted theme(nav-link--focus-outline);
      }
    }
  }

  .#{$parent}-site {
    display: none;

    @media (min-width: $grid-width-lg) {
      @include display-flex;
      flex-basis: 0;
      flex-grow: 1;
      max-width: 100%;
    }

    &.vds-state--mobile {
      background: theme(nav-default-background);
      display: block;
      left: 0;
      list-style-type: none;
      margin: 0;
      padding-bottom: 10px;
      right: 0;
      z-index: $utils-layer-4 + 4;

      .#{$parent}-links {
        width: 100%;

        &-menubar,
        .vds-tab-list {
          display: block;
          margin-top: 10px;

          &--item,
          .vds-tab-item {
            display: block;

            .vds-tab {
              border-bottom: 0;
              display: block;
              padding: 2px 30px;

              .vds-btn-label {
                @extend %navlink-label;
              }

              .vds-icon {
                position: absolute;
                right: 35px;
                top: inherit;
              }

              &:active,
              &.vds-state--selected {
                border-left: 4px solid theme(nav-link-default--active-border);
                padding-left: 26px;
              }
            }
          }
        }
      }

      [class^='vds-menu'] {
        box-shadow: none;
        overflow-y: auto;
        position: inherit;

        .vds-listbox {
          &--item {
            border: 0;

            &-container {
              padding: 14px 52px;
            }
          }
        }
      }

      .vds-navmenu {
        display: block;
        margin: 0;
        width: 100%;

        .vds-btn {
          display: block;
          height: inherit;
          padding: 5px 20px;
          text-align: left;
          width: 100%;

          .vds-icon {
            position: absolute;
            right: 35px !important;
          }

          // @TODO remove .vds-state--selected
          &:active,
          &.vds-state--selected,
          &.vds-state--active {
            border-left: 4px solid theme(nav-link-default--active-border);
            padding-left: 16px;
          }
        }
      }

      .#{$parent}-user {
        border-top: 2px solid theme(nav-listbox--border);
        margin-top: 16px;
        padding-top: 14px;
      }
    }
  }

  .#{$parent}-user {
    @include display-flex;
    margin: 0 0 0 auto;
  }

  .vds-navmenu {
    // display: none;

    .vds-popper-container {
      @include display-flex;
      align-items: center;
    }

    .vds-btn {
      box-shadow: none;
      cursor: pointer;
      height: 100%;
      padding-right: 24px;
      // padding: 16px 40px 16px 16px; // large nav size

      .vds-avatar--text {
        color: theme(nav-link);
        font-weight: theme(font-weight--regular);
        white-space: nowrap;
      }

      // style direct child icons of the button (e.g. arrow-expand) without affecting nested avatar-icons
      > [class^='vds-icon'] {
        @include svg-icon(1rem, theme(nav-link));
        margin-top: -9px !important;
        right: 8px !important;
        // right: 16px !important; // large nav size
      }

      &:hover {
        background-color: theme(nav-link--hover-background) !important;
        outline: 0;
      }

      &:focus {
        &:not(:active) {
          outline: 1px dotted theme(nav-link--focus-outline);
        }
      }

      &:active,
      &.vds-state--selected {
        background-color: theme(nav-button--active-background) !important;
        color: theme(nav-link--active);
        outline: 0;

        .vds-avatar--text {
          color: theme(nav-link--active);
        }

        > [class^='vds-icon'] {
          @include svg-icon(1rem, theme(nav-link--active));
        }
      }
    }

    @media (min-width: $grid-width-lg) {
      display: inline-block;
    }
  }

  .#{$parent}-links {
    .vds-tab-list {
      list-style-type: none;
      margin: 0;
      padding: 0;

      .vds-tab-item {
        margin: 0;

        .#{$parent}-links-panel {
          display: none;
        }

        .vds-tab {
          background: transparent;
          border: 0px;
          border-bottom: 4px solid transparent;
          box-shadow: none;
          color: theme(nav-link);
          cursor: pointer;
          font: {
            family: theme(font);
            size: theme(font-size);
            weight: theme(font-weight--regular);
          }
          letter-spacing: normal;
          margin: 0;
          outline: none;
          text-align: left;
          white-space: nowrap;

          .vds-icon {
            @include svg-icon(1rem, theme(nav-link));
            margin-left: 4px;
            position: relative;
          }

          &:hover {
            background-color: theme(nav-link--hover-background);
            box-shadow: none;
            text-decoration: none;
          }

          &:focus {
            outline: 1px dotted theme(nav-link--focus-outline);
            text-decoration: none;
          }

          &:active,
          &.vds-state--selected {
            background-color: theme(nav-link--hover-background);
            border-bottom: 4px solid transparent;
            box-shadow: none;
            color: theme(nav-link--active);
            text-decoration: none;

            .vds-icon {
              @include svg-icon(1rem, theme(nav-link--active));
            }
          }

          @media (min-width: $grid-width-lg) {
            padding: px2em(10px) 1rem px2em(4px) 1rem;
            z-index: $utils-layer-4 + 4;

            &:active,
            &.vds-state--selected {
              border-bottom: 4px solid theme(nav-link-default--active-border);
              box-shadow: none;
            }
          }

          .vds-btn-label {
            @extend %navlink-label;
          }

          @media (min-width: $grid-width-lg) {
            &.vds-state--selected {
              background-color: theme(nav-button--active-background);
            }
          }
        }
      }
    }
  }

  .#{$parent}-action {
    padding: 0 1rem;

    &-info {
      @include display-flex;
      height: 100%;
      margin: auto;
    }
  }

  [class^='vds-menu'] {
    background-color: theme(nav-dropdown--background);

    .vds-listbox {
      background-color: theme(nav-dropdown--background);

      // @TODO: Move states to internal link (onyl text styles)
      &-item {
        border-bottom: 2px solid theme(nav-listbox--border);
        box-shadow: none;
        display: block;
        padding: 0;

        &-container {
          color: theme(nav-link);
          display: block;
          padding: 14px;

          &:hover {
            background-color: theme(nav-listbox--hover-background);
          }

          &:active {
            background-color: theme(nav-default-background);
            color: theme(nav-link--active);
          }

          // @TODO: Focus state
          &:focus {
            outline: 1px dotted theme(nav-link--focus-outline);
          }

          &:first-child {
            margin-top: 1px;
          }
        }
      }

      @media (min-width: $grid-width-lg) {
        max-width: 320px;
        min-width: 240px;
      }
    }
  }

  .vds-btn-icon {
    margin: auto 0;

    &:focus {
      outline: 1px dotted theme(nav-link--focus-outline);
    }
  }

  .col {
    // display: flex;
    @include display-flex;
    padding: 0;
  }

  // @TODO remove .vds-nav-header-curtain
  .#{$parent}-tain,
  .#{$parent}-curtain {
    display: none;
    height: 100%;
    left: 0;
    overflow: hidden;
    position: fixed;
    top: 0;
    width: 100%;
    // @TODO move z-index to absolute/fixed nav only
    z-index: $utils-layer-4 + 3;

    // @TODO: remove .curtain-show
    &.curtain-show,
    &.vds-state--show {
      display: block;
    }
  }

  // @TODO: rename to .vds-nav--consumer
  &.vds-type--consumer {
    background: theme(nav-consumer-background);

    .vds-nav-panel {
      background: theme(nav-consumer-background);
    }

    .#{$parent}-links {
      // @TODO remove .mobile-nav
      &.mobile-nav,
      &.vds-state--mobile {
        background: theme(nav-consumer-background);
      }
    }
  }

  // @TODO: rename to .vds-nav--marketing
  &.vds-type--marketing {
    background: theme(nav-marketing-background);

    .vds-nav-panel {
      background: theme(nav-marketing-background);
    }

    .#{$parent}-site {
      // @TODO remove .vds-nav-links-menubar
      .#{$parent}-links-menubar,
      .vds-tab-list {
        .vds-tab {
          // @media (min-width: $grid-width-lg) {
          &:active,
          &.vds-state--selected {
            border-image: theme(nav-link-marketing--active-border-gradient);
            border-image-slice: 1;
          }
          // }
        }
      }

      // @TODO remove .mobile-nav
      &.mobile-nav,
      &.vds-state--mobile {
        background: theme(nav-marketing-background);

        // @TODO remove .vds-nav-links-menubar
        .#{$parent}-links-menubar,
        .vds-tab-list {
          .vds-tab {
            &:active,
            &.vds-state--selected {
              border-image: theme(nav-link-marketing--active-border-gradient-mobile);
              border-image-slice: 1;
            }
          }
        }

        .vds-navmenu {
          .vds-btn {
            border-image: theme(nav-link-marketing--active-border-gradient-mobile);
            border-image-slice: 1;
          }
        }
      }
    }
  }

  // @TODO: rename to .vds-nav--light
  &.vds-type--light {
    color: $color-indigo;
  }

  &.vds-nav--absolute {
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: $utils-layer-4 + 2;

    .#{$parent}-site {
      // @TODO remove .mobile-nav
      &.mobile-nav,
      &.vds-state--mobile {
        position: absolute;
      }
    }
  }

  &.vds-nav--fixed {
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: $utils-layer-4 + 2;

    .#{$parent}-site {
      // @TODO remove .mobile-nav
      &.mobile-nav,
      &.vds-state--mobile {
        bottom: 0;
        position: fixed;
      }
    }
  }

  &.vds-nav--relative {
    position: relative;
    z-index: $utils-layer-0;

    .#{$parent}-site {
      // @TODO remove .mobile-nav
      &.mobile-nav,
      &.vds-state--mobile {
        position: absolute;
      }
    }
  }
}
