/***
  vds Buttons
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

// Default
$color-button-text--primary-text: theme(button--default-text);
$color-button-text--primary-background: theme(button--default-text--background);
// Hover
$color-button-text--primary-text--hover: theme(button--default-text--hover);
$color-button-text--primary-background--hover: theme(button--default-text--background--hover);
// Focus
$color-button-text--primary-text--focus: theme(button--default-text--focus);
$color-button-text--primary-background--focus: theme(button--default-text--background--focus);
$color-button-text--primary-border--focus: theme(button--default-text--focus);
// Active
$color-button-text--primary-text--active: theme(button--default-text--active);
$color-button-text--primary-background--active: theme(button--default-text--background--active);
// Disabled
$color-button-text--primary-text--disabled: theme(button--default-text--disabled);
$color-button-text--primary-background--disabled: theme(button--default-text--disabled-background);

/* === Text Button - Secondary ========== */
// Default
$color-button-text--secondary-text: theme(button--secondary-text);
$color-button-text--secondary-background: theme(button--secondary-text--background);
$color-button-text--secondary-border: theme(button--secondary-text--border);
// Hover
$color-button-text--secondary-text--hover: theme(button--secondary-text--hover);
$color-button-text--secondary-background--hover: theme(button--secondary-text--background--hover);
$color-button-text--secondary-border--hover: theme(button--secondary-text--border--hover);
// Focus
$color-button-text--secondary-text--focus: theme(button--secondary-text--focus);
$color-button-text--secondary-background--focus: theme(button--secondary-text--background--focus);
$color-button-text--secondary-border--focus: theme(button--secondary-text--border--focus);
// Active
$color-button-text--secondary-text--active: theme(button--secondary-text--active);
$color-button-text--secondary-background--active: theme(button--secondary-text--background--active);
$color-button-text--secondary-border--active: theme(button--secondary-text--border--active);
// Disabled
$color-button-text--secondary-text--disabled: theme(button--secondary-text--disabled);
$color-button-text--secondary-background--disabled: theme(button--secondary-text--background--disabled);
$color-button-text--secondary-border--disabled: $color-button-text--secondary-background--disabled;

/* === Text Button - Tertiary ========== */
// Default
$color-button-text--tertiary-text: theme(button--tertiary-text);
$color-button-text--tertiary-background:  theme(button--tertiary-text--background);
$color-button-text--tertiary-border: theme(button--tertiary-text);
// Hover
$color-button-text--tertiary-text--hover: theme(button--tertiary-text--hover);
$color-button-text--tertiary-background--hover:  theme(button--tertiary-text--background--hover);
$color-button-text--tertiary-border--hover: theme(button--tertiary-text--hover);
// Focus
$color-button-text--tertiary-text--focus: theme(button--tertiary-text--focus);
$color-button-text--tertiary-background--focus: theme(button--tertiary-text--background--focus);
$color-button-text--tertiary-border--focus: theme(button--tertiary-border);
// Active
$color-button-text--tertiary-text--active: theme(button--tertiary-text--active);
$color-button-text--tertiary-background--active:  theme(button--tertiary-text--background--active);
// Disabled
$color-button-text--tertiary-text--disabled: theme(button--tertiary-text--disabled);
$color-button-text--tertiary-background--disabled:  theme(button--tertiary-text--background--disabled);
$color-button-text--tertiary-border--disabled: $color-button-text--tertiary-background--disabled;

/* === Icon `Button ========== */
// Default
$color-button-icon: theme(button--default-text);
$color-button-icon-background: theme(action-primary);
// Hover
$color-button-icon--hover: theme(button--default-text--hover);
$color-button-icon-background--hover: theme(action-primary--hover);
// Active
$color-button-icon--selected: theme(button--default-text--selected);
$color-button-icon-background--selected: theme(action-primary--selected);
// Disabled
$color-button-icon--disabled: theme(button--default-text--disabled);
$color-button-icon-background--disabled: theme(button--default-text--disabled-background);

/* === Icon Button - Light ========== */
$color-button-icon--light: theme(button-icon);
$color-button-icon--background: theme(button-icon);
$color-button-icon--light--hover: theme(button-icon-light--hover);
$color-button-icon--light-background--hover: theme(button-icon-light-background--hover);
$color-button-icon--light--disabled: theme(button-icon-light--diabled);
$color-button-icon--light--selected: theme(button-icon-text);
$color-button-icon-text: theme(button-icon-light); // for numbered pagination dots

/* === Icon Button - Dark ========== */
$color-button-icon--dark--focus: theme(button--default-text--focus);

/* === Link Button - Primary ========== */
$color-button-link--primary-text: theme(action-primary);
$color-button-link--primary-text--hover: theme(action-primary--hover);
$color-button-link--primary-text--disabled: theme(action-primary--disabled);
$color-button-link--primary-text--selected: theme(action-primary--selected);

/* === Full Width Button - Primary ========== */
$color-button-fullwidth--primary-border--focus: theme(button--default-text--focus);

/* === Full Width Button - Secondary ========== */
$color-button-fullwidth--secondary-border--focus: theme(focus-outline-color);




@mixin make-icon-flippable {
  &.vds-state--flippable {
    &:active,
    &.vds-state--active {
      .vds-icon {
        transform: rotate(180deg);
      }
      // avoid flipping avatar icons
      .vds-avatar--photo {
        [class^='vds-icon'] {
          transform: none;
        }
      }
    }
  }
}

@mixin default-button {
  @include state-focus;
  -webkit-font-smoothing: antialiased; // to look like Sketch mockup
  border-radius: theme(button--radius);
  cursor: pointer !important;
  font-family: theme(font);
  font-size: theme(font-size);
  font-weight: theme(font-weight--semibold); // match body--alt
  outline: none;
  padding-bottom: 0;
  padding-top: 0;
  transition: background .25s ease;
  white-space: nowrap;

  &[disabled],
  &.vds-state--disabled {
    cursor: default !important;
    pointer-events: none;

    .vds-btn-label,
    .vds-btn--action {
      cursor: default !important;
    }

    .vds-btn--action {
      border-color: theme(button--default-text);
    }
  }

  [class^='vds-icon'] {
    @include svg-icon(1.5rem, $color-button-icon);
  }
}

@mixin default-button-text {
  @include default-button;
  @include make-icon-flippable;
  align-items: center;
  background: $color-button-text--primary-background;
  border: 0;
  color: $color-button-text--primary-text;
  cursor: pointer;
  display: inline;
  justify-content: center;
  margin: .25rem; // 4px
  min-height: 2.5rem; // 40px
  min-width: 4rem; // 64px
  padding: 0 !important;
  text-decoration: none;
  text-transform: uppercase;
  vertical-align: middle;

  _:-ms-fullscreen, // sass-lint:disable-line force-pseudo-nesting
  .ie11up,
  & {
    display: inline;
  }

  &:hover {
    background: $color-button-text--primary-background--hover;
    color: $color-button-text--primary-text--hover;
  }

  &:focus {
    background: $color-button-text--primary-background--focus;
    color: $color-button-text--primary-text--focus;
    outline-offset: 1px;
  }

  &:active,
  &.vds-state--active {
    background: $color-button-text--primary-background--active;
    color: $color-button-text--primary-text--active;
  }

  &[disabled],
  &.vds-state--disabled {
    background: $color-button-text--primary-background--disabled;
    color: $color-button-text--primary-text--disabled;
    cursor: not-allowed !important;
  }
}

@mixin default-button-icon {
  @include display-flex;
  @include default-button;
  align-items: center;
  background: $color-button-icon-background;
  border: 0;
  border-radius: 2.5rem; // 40px
  box-sizing: border-box;
  color: $color-button-icon;
  height: 3rem; // 48px
  justify-content: center;
  min-width: 3rem; // 40px
  position: relative; // for ::before positioning on pagination dot focus state

  _:-ms-fullscreen, // sass-lint:disable-line force-pseudo-nesting
  .ie11up,
  & {
    display: inline;
  }

  [class^='vds-icon'] {
    @include svg-icon(1.5rem, $color-button-icon);
  }

  &:hover,
  &:active,
  &:focus {
    background: $color-button-icon-background--hover;
    color: $color-button-icon--hover;

    // fixes mozila firefox's double focus ring
    &::-moz-focus-inner {
      border: 0;
    }

    [class^='vds-icon'] {
      @include svg-icon(1.5rem, $color-button-icon--hover);
    }
  }

  &:focus {
    .vds-btn-label {
      text-decoration: none;
    }
  }

  &[disabled],
  &.vds-state--disabled {
    background-color: theme(#{transparent});

    [class^='vds-icon'] {
      @include svg-icon(1.5rem, $color-button-icon--light--disabled);
    }
  }

  &.vds-state--active {
    background: $color-button-icon-background--selected;
    color: $color-button-icon--selected;

    [class^='vds-icon'] {
      @include svg-icon(1.25rem, $color-button-icon--selected);
    }
  }

  .vds-btn-label {
    padding: 0;

    [class^='vds-icon'] {
      &:first-child {
        margin-right: 0;
      }

      &:last-child {
        margin-left: 0;
      }
    }
  }
}

@mixin default-button-link {
  @include default-button;
  background-color: theme(#{transparent});
  border: 0;
  color: $color-button-link--primary-text;
  display: inline-block;
  font-weight: theme(font-weight--bold);
  padding: 0;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;

  & [class^='vds-icon'] {
    @include svg-icon(1rem, $color-button-link--primary-text);
  }

  &:link,
  &:visited {
    color: $color-button-link--primary-text;

    & [class^='vds-icon'] {
      @include svg-icon(1rem, $color-button-link--primary-text);
    }
  }

  &:hover,
  &:active,
  &:focus {
    color: $color-button-link--primary-text--hover;
    text-decoration: none;

    & [class^='vds-icon'] {
      @include svg-icon(1rem, $color-button-link--primary-text--hover);
    }
  }

  &[disabled] {
    color: $color-button-link--primary-text--disabled;

    & [class^='vds-icon'] {
      @include svg-icon(1rem, $color-button-link--primary-text--disabled);
    }
  }

  & .vds-avatar {
    & + [class^='vds-icon'] {
      margin-top: -.4375rem; // 7px
      position: absolute;
      right: -1.25rem; // 20px
      top: 50%;
    }
  }

  .vds-btn-label {
    padding: 0;
  }
}

@-moz-document url-prefix() {
  .vds-btn-split {
    .vds-btn-label {
      display: block !important;
      float: left;
      text-align: center;
    }
  }
}

@-moz-document url-prefix() {
  .vds-btn-split {
    .vds-btn--action {
      display: block !important;
      float: right;
      // max-width: px2em(20px);
      text-align: center;
    }
  }
}

.vds-btn {
  @include make-icon-flippable;

  &-container {
    margin-bottom: 1.875rem; // 30px
  }

  &-group {
    @include display-flex;
    align-items: center;
    flex-wrap: wrap;

    // Default button group button margin/spacing
    & > [class^='vds-btn'] {
      &:not(.vds-btn-label) {
        margin: 0 .25rem 3px .25rem;

        &:first-child {
          margin-left: 0;
        }

        &:last-child {
          margin-right: 0;
        }
      }
    }

    &--reverse {
      @include xbrowser-property(flex-direction, row-reverse);

      // Default reversed button group button margin/spacing
      & > [class^='vds-btn'] {
        &:not(.vds-btn-label) {

          &:first-child {
            margin-left: .25rem;
            margin-right: 0;
          }

          &:last-child {
            margin-left: 0;
            margin-right: .25rem;
          }
        }
      }
    }

    &--right {
      justify-content: flex-end;
    }
  }

  &-text {
    @include default-button-text;
    display: inline-block;

    &--primary {
      @include default-button-text;
    }

    &--secondary {
      @include default-button-text;
      background: $color-button-text--secondary-background;
      border: 2px solid $color-button-text--secondary-border;
      color: $color-button-text--secondary-text;
      text-decoration: none;

      [class^='vds-icon'] {
        @include svg-icon(1.5rem, $color-button-text--secondary-text);
      }

      &:hover {
        background-color: $color-button-text--secondary-background--hover;
        border-color: $color-button-text--secondary-text--hover;
        color: $color-button-text--secondary-text--hover;

        [class^='vds-icon'] {
          @include svg-icon(1.5rem, $color-button-text--secondary-text--hover);
        }
      }

      &:focus {
        background-color: $color-button-text--secondary-background--focus;
        border-color: $color-button-text--secondary-text--focus;
        color: $color-button-text--secondary-text--focus;

        [class^='vds-icon'] {
          @include svg-icon(1.5rem, $color-button-text--secondary-text--focus);
        }
      }

      &:active,
      &.vds-state--active {
        background: $color-button-text--secondary-background--active;
        border-color: $color-button-text--secondary-background--active;
        color: $color-button-text--secondary-text--active;

        [class^='vds-icon'] {
          @include svg-icon(1.5rem, $color-button-text--secondary-text--active);
        }
      }

      &[disabled],
      &.vds-state--disabled {
        background: $color-button-text--secondary-background--disabled;
        border: 2px solid $color-button-text--secondary-border--disabled;
        color: $color-button-text--secondary-text--disabled;

        [class^='vds-icon'] {
          @include svg-icon(1.5rem, $color-button-text--secondary-text--disabled);
        }
      }
    }

    &--tertiary {
      @include default-button-text;
      @include state-focus;

      background: $color-button-text--tertiary-background;
      border: 1px solid $color-button-text--tertiary-border;
      color: $color-button-text--tertiary-text;
      font-weight: theme(font-weight--regular); // match body - tertiary specific
      text-decoration: none;

      [class^='vds-icon'] {
        @include svg-icon(1.5rem, $color-button-text--tertiary-text);
      }

      .vds-btn-label {
        padding: 0 .5rem;
      }

      &:hover {
        background: $color-button-text--tertiary-background--hover;
        border-color: $color-button-text--tertiary-text--hover;
        color: $color-button-text--tertiary-text--hover;

        [class^='vds-icon'] {
          @include svg-icon(1.5rem, $color-button-text--tertiary-text--hover);
        }
      }

      &:focus {
        background: $color-button-text--tertiary-background--focus;
        border-color: $color-button-text--tertiary-text--focus;
        color: $color-button-text--tertiary-text--focus;

        [class^='vds-icon'] {
          @include svg-icon(1.5rem, $color-button-text--tertiary-text--focus);
        }
      }

      &:active,
      &.vds-state--active {
        background: $color-button-text--tertiary-background--active;
        border-color: $color-button-text--tertiary-background--active;
        color: $color-button-text--tertiary-text--active;

        [class^='vds-icon'] {
          @include svg-icon(1.5rem, $color-button-text--tertiary-text--active);
        }
      }

      &[disabled],
      &.vds-state--disabled {
        background: $color-button-text--tertiary-background--disabled;
        border-color: $color-button-text--tertiary-border--disabled;
        color: $color-button-text--tertiary-text--disabled;

        [class^='vds-icon'] {
          @include svg-icon(1.5rem, $color-button-text--tertiary-text--disabled);
        }
      }
    }

    &--fullwidth {
      border-radius: 0;
      display: block;
      height: 4rem; // 64px
      width: 100%;

      // Pseudo-selector used for this effect because IE11 doesn't honor the outline-offset property
      &[class^='vds-btn-text'] {
        &.focus-visible {
          outline: none;
          position: relative;
  
          &:after {
            content: '';
            position: absolute;
            top: 4px;
            left: 4px;
            right: 4px;
            bottom: 4px;
            display: block;
            border: 1px dashed $color-button-fullwidth--primary-border--focus;
          }
        }
      }

      &.vds-btn-text--secondary.focus-visible:after {
        border: 1px dashed $color-button-fullwidth--secondary-border--focus;
      }
    }
  }

  &-label { // span is used because Safari doesn't render button elem as display: flex
    @extend %ie-btn-jump;
    @include display-flex;
    align-items: center;
    cursor: pointer;
    flex: 0 0 auto;
    justify-content: center;
    margin: auto;
    max-width: 100%;
    overflow-wrap: break-word;
    padding: 0 1rem;
    text-align: center;
    word-break: normal;
    word-wrap: normal;

    [class^='vds-icon'] {
      &:first-child {
        margin-right: px2em(4px);
      }

      &:last-child {
        margin-left: px2em(4px);
      }
    }

    @-moz-document url-prefix() {
      & {
        padding: 0 px2em(11px);
      }
    }
  }

  &-icon {
    @include default-button-icon;

    &--light {
      background-color: theme(#{transparent});

      [class^='vds-icon'] {
        @include svg-icon(1.5rem, $color-button-icon--light);
      }

      &:active {
        background-color: $color-button-icon--light--hover;

        [class^='vds-icon'] {
          @include svg-icon(1.5rem, $color-button-text--primary-text--hover);
        }
      }

      &:hover,
      &:focus {
        &:not(:active) {
          background-color: $color-button-text--secondary-background--hover;

          [class^='vds-icon'] {
            @include svg-icon(1.5rem, $color-button-text--secondary-text--hover);
          }
        }
      }

      &:disabled {
        background-color: theme(#{transparent});

        [class^='vds-icon'] {
          @include svg-icon(1.5rem, $color-button-text--primary-background--disabled);
        }
      }
    }

    &--light-outline {
      background-color: theme(#{transparent});
      border: 2px solid $color-button-icon--light;

      [class^='vds-icon'] {
        @include svg-icon(1.5rem, $color-button-icon--light);
      }

      &:active {
        background-color: $color-button-icon--light--hover;
        border-color: $color-button-icon--light--hover;

        [class^='vds-icon'] {
          @include svg-icon(1.5rem, $color-button-text--primary-text--hover);
        }
      }

      &:hover {
        &:not(:active) {
          background-color: $color-button-icon--light-background--hover;
          border-color: $color-button-icon--light--hover;

          [class^='vds-icon'] {
            @include svg-icon(1.5rem, $color-button-icon--light--hover);
          }
        }
      }

      &:focus {
        &:not(:active) {
          background-color: $color-button-icon--light-background--hover;
          border-color: $color-button-icon--light--hover;

          [class^='vds-icon'] {
            @include svg-icon(1.5rem, $color-button-icon--light--hover);
          }
        }
      }

      &:disabled {
        background-color: theme(#{transparent});
        border-color: $color-button-text--primary-background--disabled;

        [class^='vds-icon'] {
          @include svg-icon(1.5rem, $color-button-text--primary-background--disabled);
        }
      }
    }

    &--light-tiny {
      background-color: theme(#{transparent});
      color: $color-button-icon-text; // for numbered pagination dots
      height: 2rem;
      min-width: 2rem;

      [class^='vds-icon'] {
        @include svg-icon(1rem);
      }

      &:active {
        background-color: $color-button-icon--light--hover;

        [class^='vds-icon'] {
          @include svg-icon(1rem);
        }
      }

      &:hover {
        &:not(:active) {
          background-color: $color-button-icon--light-background--hover;
          color: $color-button-icon-text; // for numbered pagination dots

          [class^='vds-icon'] {
            @include svg-icon(1rem);
          }
        }
      }

      &:focus {
        &:not(:active) {
          background-color: $color-button-icon--light-background--hover;
          color: $color-button-icon-text; // for numbered pagination dots

          [class^='vds-icon'] {
            @include svg-icon(1rem);
          }
        }
      }

      &:disabled {
        background-color: theme(#{transparent});
        border-color: $color-smoke;

        [class^='vds-icon'] {
          @include svg-icon(1rem, $color-button-icon-background--disabled);
        }
      }
    }

    &--dark {
      background-color: theme(#{transparent});

      &:focus {
        &:not(:active) {
          outline-color: $color-button-icon--dark--focus;
        }
      }

      &:hover,
      &:focus {
        background-color: $color-button-icon--light--hover;
        border-color: $color-button-icon--light--hover;
      }

      &:disabled {
        background-color: theme(#{transparent});
        border-color: $color-button-text--primary-background--disabled;

        [class^='vds-icon'] {
          @include svg-icon(1.5rem, $color-button-text--primary-background--disabled);
        }
      }
    }

    &--dark-outline {
      background-color: theme(#{transparent});
      border: 2px solid $color-button-text--primary-text;

      &:focus {
        &:not(:active) {
          outline-color: $color-button-icon--dark--focus;
        }
      }

      &:hover,
      &:focus {
        background-color: $color-button-icon--light--hover;
        border-color: $color-button-icon--light--hover;
      }

      &:disabled {
        background-color: theme(#{transparent});
        border-color: $color-button-text--primary-background--disabled;

        [class^='vds-icon'] {
          @include svg-icon(1.5rem, $color-button-text--primary-background--disabled);
        }
      }
    }

    &--dark-tiny {
      background-color: theme(#{transparent});
      height: 2rem;
      min-width: 2rem;

      &:focus {
        &:not(:active) {
          outline-color: $color-button-icon--dark--focus;
        }
      }

      [class^='vds-icon'] {
        @include svg-icon(1rem, $color-button-icon);
      }

      &:active {
        background-color: $color-button-icon--light--hover;

        [class^='vds-icon'] {
          @include svg-icon(1rem, $color-button-text--primary-text--hover);
        }
      }

      &:hover,
      &:focus {
        &:not(:active) {
          background-color: $color-button-icon--light--hover;

          [class^='vds-icon'] {
            @include svg-icon(1rem, $color-button-text--primary-text--hover);
          }
        }
      }

      &:disabled {
        background-color: theme(#{transparent});
        border-color: $color-smoke;

        [class^='vds-icon'] {
          @include svg-icon(1rem, $color-button-text--primary-background--disabled);
        }
      }
    }
  }

  // LoVeHAte Definition Order (:link, :visited, :hover, :active)
  &-link {
    @include default-button-link;

    &--primary {
      @include default-button-link;
    }

    &--secondary {
      @include default-button-link;
      font-size: 1em; // 14px
      font-weight: theme(font-weight--regular);
      text-decoration: none;
      text-transform: capitalize;

      &:link,
      &:visited {
        color: $color-button-link--primary-text;
      }

      &:hover,
      &:active,
      &:focus {
        color: $color-button-link--primary-text--hover;
        text-decoration: underline;
      }

      &[disabled] {
        color: $color-button-link--primary-text--disabled;
        text-decoration: none;
      }
    }

    &--tertiary {
      @include default-button-link;
      font-size: .6875rem; //  11px
      font-weight: theme(font-weight--bold);
      text-decoration: none;
      text-transform: capitalize;

      &:link,
      &:visited {
        color: $color-button-link--primary-text;
      }

      &:hover,
      &:active,
      &:focus {
        color: $color-button-link--primary-text--hover;
        text-decoration: underline;
      }

      &[disabled] {
        color: $color-button-link--primary-text--disabled;
        text-decoration: none;
      }
    }
  }

  &--action {
    @include display-flex;
    align-items: center;
    cursor: pointer;
    flex: 0 0 auto;
    justify-content: center;
    min-width: 2.5rem; // 40px

    &:last-child {
      border-left: 1px solid theme(button--default-text);
    }

    &:first-child {
      border-right: 1px solid theme(button--default-text);
    }

    [class^='vds-icon'] {
      @include svg-icon(1rem, $color-button-icon);
    }
  }

  &-split {
    @include default-button-text;

    .vds-btn--action {
      @extend %ie-btn-jump;
      display: inline-flex;
      height: 1.5rem;
      vertical-align: middle;

      [class^='vds-icon'] {
        display: block;
      }
    }

    .vds-btn-label {
      display: inline-block;
      vertical-align: middle;
    }

    [class^='vds-icon'] {
      @include svg-icon(px2em(16px), $color-button-icon);
    }

    &.vds-nav-vertical-toggle-button {
      &.focus-visible {
        outline: none;
        position: relative;

        &:after {
          content: '';
          position: absolute;
          top: 2px;
          left: 2px;
          right: 2px;
          bottom: 2px;
          display: block;
          border: 1px dotted $color-button-fullwidth--primary-border--focus;
        }
      }
    }

    &--primary {
      @include default-button-text;

      [class^='vds-icon'] {
        @include svg-icon(1rem, $color-button-icon);
      }
    }

    &--secondary {
      @include default-button-text;
      background: $color-button-text--secondary-background;
      border: 2px solid $color-button-text--secondary-border;
      color: $color-button-text--secondary-text;
      text-decoration: none;

      .vds-btn--action {
        border-color: $color-button-text--secondary-text;
      }

      [class^='vds-icon'] {
        @include svg-icon(px2em(16px), $color-button-text--secondary-text);
      }

      &:active,
      &.vds-state--active {
        background: $color-button-text--secondary-background--hover;
        border-color: $color-button-text--secondary-border--active;
        color: $color-button-text--secondary-text--hover;
      }

      &:hover,
      &:focus {
        background-color: $color-button-text--secondary-background--hover;
        border-color: $color-button-text--secondary-text--hover;
        color: $color-button-text--secondary-text--hover;

        [class^='vds-icon'] {
          @include svg-icon(1rem, $color-button-text--secondary-text--hover);
        }

        .vds-btn--action {
          border-color: $color-button-text--secondary-text--hover;
        }
      }

      &[disabled],
      &.vds-state--disabled {
        background: $color-button-text--secondary-background--disabled;
        border-color: $color-button-text--secondary-border--disabled;
        color: $color-button-text--secondary-text--disabled;

        [class^='vds-icon'] {
          @include svg-icon(1rem, $color-button-text--secondary-text--disabled);
        }

        .vds-btn--action {
          border-color: $color-button-text--secondary-text--disabled;
        }
      }
    }

  }
}
