/***
  vds-alert
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/


$color-alert-foreground-light: theme(notification-foreground--light);
$color-alert-foreground-dark: theme(notification-foreground--dark);

$color-alert-information: theme(notification-information);
$color-alert-information-background: theme(notification-information--light);
$color-alert-success: theme(notification-success);
$color-alert-success-background: theme(notification-success--light);
$color-alert-warning: theme(notification-warning);
$color-alert-warning-background: theme(notification-warning--light);
$color-alert-error: theme(notification-error);
$color-alert-error-background: theme(notification-error--light);

$alert-border-width: px2em(4px);

$alert-box-padding: theme(notification-alert-padding);

.vds-alert {
  background-color: $color-alert-information-background;
  border-bottom: $alert-border-width solid $color-alert-information;
  color: $color-alert-foreground-dark;
  left: 0;
  padding: $alert-box-padding 0;
  position: fixed;
  top: -500px;
  transition: .5s ease;
  width: 100%;
  z-index: $utils-layer-3 + 1;

  &--relative {
    position: unset;
    top: 0;
    z-index: $utils-layer-0 + 2;

    &.vds-state--global {
      display: none;

      &.vds-state--show {
        display: block;
      }
    }

    .vds-alert-label,
    .vds-alert-icon {
      display: inline-block;
    }

    -ms-fullscreen, // sass-lint:disable-line force-pseudo-nesting
    .ie11up,
    & {
      position: relative !important;
    }
  }

  [class^='col'] {
    padding-bottom: 0 !important;
    padding-top: 0 !important;
  }

  .vds-btn-container {
    margin: 0;
    margin-top: 2px;
    padding: 0 10px;

    /* targetting safari */
    ::i-block-chrome,
    & {
      margin-right: 20px;
    }
  }

  &-icon {
    display: none;
    margin-top: -2px;
    position: absolute;
  }

  &-label {
    display: none;
    padding-left: calc(#{$icon-size--low} + 8px);

    &-title {
      @extend %header-title;
      display: block;
      margin-bottom: 0;
      position: relative;
      top: -4px;
      // font-weight: theme(font-weight--bold);
    }

    a {
      text-decoration: underline !important;
    }
  }

  .vds-btn-group {
    .vds-btn-group--right {
      [class^='vds-btn'] {
        color: $color-alert-foreground-dark;
        display: none;
        padding-bottom: 0;
        padding-top: 0;
      }
    }
  }

  &.vds-state--show {
    top: 64px;

    .vds-alert-label,
    .vds-alert-icon {
      display: inline-block;

      -ms-fullscreen, // sass-lint:disable-line force-pseudo-nesting
      .ie11up,
      & {
        display: inline-block;
      }
    }

    .vds-btn-group {
      .vds-btn-group--right {
        [class^='vds-btn'] {
          display: unset;

          -ms-fullscreen, // sass-lint:disable-line force-pseudo-nesting
          .ie11up,
          & {
            @include display-flex;
          }

        }
      }
    }
  }

  &--success {
    background-color: $color-alert-success-background;
    border-bottom: $alert-border-width solid $color-alert-success;

    .vds-icon {
      @include svg-icon($icon-size--low, $color-alert-success);
    }
  }

  &--information {
    background-color: $color-alert-information-background;
    border-bottom: $alert-border-width solid $color-alert-information;

    .vds-icon {
      @include svg-icon($icon-size--low, $color-alert-information);
    }
  }

  &--warning {
    background-color: $color-alert-warning-background;
    border-bottom: $alert-border-width solid $color-alert-warning;

    .vds-icon {
      @include svg-icon($icon-size--low, $color-alert-foreground-dark);
    }
  }

  &--error {
    background-color: $color-alert-error-background;
    border-bottom: $alert-border-width solid $color-alert-error;

    .vds-icon {
      @include svg-icon($icon-size--low, $color-alert-error);
    }
  }

  &--sidenav--open {
    left: 240px;
    width: calc(100% - 240px);

    &.vds-state--show {
      top: 0px;
    }
  }

  &--sidenav--closed {
    left: 56px;
    width: calc(100% - 56px);

    &.vds-state--show {
      top: 0px;
    }
  }
}

.vds-viewport {
  &.vds-state--low {
    .vds-alert {
      &.vds-state--show {
        top: 70px;
      }
    }
  }
}
