/***
  vds-card-art
  BEM Naming Convention http://getbem.com/naming/
  PX to Em Conversion http://px-em.com/

  @author dpoynor
*/

$ripple-color: theme(card-art--ripple-color);
$ripple-border-color: theme(card-art--ripple-border-color);
$ripple-duration: 1000ms;
$ripple-2-delay: 380ms;
$ripple-easing: ease-out;
$ripple-0-background-color: theme(card-art--ripple-zero-background-color);
$ripple-0-border-color: theme(card-art--ripple-zero-border-color);
$ripple-33-background-color: theme(card-art--ripple-thirtythree-background-color);
$ripple-33-border-color: theme(card-art--ripple-thirtythree-border-color);
$ripple-34-border-color: theme(card-art--ripple-thirtyfour-border-color);
$ripple-100-border-color: theme(card-art--ripple-onehundred-border-color);
$ripple-0-background-gold-color: theme(card-art--ripple-zero-background-gold-color);
$ripple-0-border-gold-color: theme(card-art--ripple-zero-border-gold-color);
$ripple-33-background-gold-color: theme(card-art--ripple-thirtythree-background-gold-color);
$ripple-33-border-gold-color: theme(card-art--ripple-thirtythree-border-gold-color);
$ripple-34-border-gold-color: theme(card-art--ripple-thirtyfour-border-gold-color);
$ripple-100-border-gold-color: theme(card-art--ripple-onehundred-border-gold-color);
$zoom-bounce-duration: 1200ms;
$zoom-bounce-easing: ease-out;
$zoom-bounce-iteration-count: 1;
$wiggle-duration: 500ms;
$wiggle-easing: ease;
$wiggle-iteration-count: 1;
$shimmer-color: theme(card-art--shimmer-color);
$shimmer-duration: 580ms;
$shimmer-easing: ease;
$mute-color: theme(card-art--mute-color);
$mute-color-duration: 500ms;
$mute-color-easing: ease;
$unmute-color-duration: 1000ms;
$card-art-border-radius-base: px2em(16px);

@mixin ripple {
  border-radius: px2em(12px);

  &::after,
  &::before {
    animation: ripple $ripple-duration $ripple-easing 1;
    border-color: $ripple-border-color;
    border-radius: 100%;
    border-style: solid;
    box-sizing: border-box;
    content: '';
    display: block;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  &::before {
    animation-delay: $ripple-2-delay;
  }
}

@mixin shimmer {
  &::after {
    animation: shimmer $shimmer-duration $shimmer-easing 1;
    border-bottom: 18px solid $shimmer-color;
    box-shadow: 0 108px 0 0 $shimmer-color inset;
    content: '';
    display: inline-block;
    height: px2em(120px);
    left: -100%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%) rotate(30deg);
    width: px2em(640px);
  }
}

@keyframes ripple {
  0% {
    background-color: $ripple-0-background-color;
    border-color: $ripple-0-border-color;
    border-width: px2em(38px);
    height: px2em(72px);
    width: px2em(72px);
  }

  33% {
    background-color: $ripple-33-background-color;
    border-color: $ripple-33-border-color;
    border-width: px2em(220px);
    height: px2em(400px);
    width: px2em(400px);
  }

  34% {
    background-color: transparent;
    border-color: $ripple-34-border-color;
    border-width: px2em(190px);
    height: px2em(400px);
    width: px2em(400px);
  }

  100% {
    border-color: $ripple-100-border-color;
    border-width: 0;
    height: px2em(400px);
    width: px2em(400px);
  }
}

@keyframes gold-ripple {
  0% {
    background-color: $ripple-0-background-gold-color;
    border-color: $ripple-0-border-gold-color;
    border-width: px2em(38px);
    height: px2em(72px);
    width: px2em(72px);
  }

  33% {
    background-color: $ripple-33-background-gold-color;
    border-color: $ripple-33-border-gold-color;
    border-width: px2em(220px);
    height: px2em(400px);
    width: px2em(400px);
  }

  34% {
    background-color: transparent;
    border-color: $ripple-34-border-gold-color;
    border-width: px2em(190px);
    height: px2em(400px);
    width: px2em(400px);
  }

  100% {
    border-color: $ripple-100-border-gold-color;
    border-width: 0;
    height: px2em(400px);
    width: px2em(400px);
  }
}

@keyframes zoom-bounce {
  0% {
    transform: scale(1);
  }

  20% {
    transform: scale(1.15);
  }

  26% {
    transform: scale(1.15);
  }

  40% {
    transform: scale(.9);
  }

  50% {
    transform: scale(1.02);
  }

  60% {
    transform: scale(.96);
  }

  65% {
    transform: scale(1);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes wiggle {
  0% {
    transform: rotate(0);
  }

  20% {
    transform: rotate(3deg);
  }

  40% {
    transform: rotate(-1.5deg);
  }

  60% {
    transform: rotate(.5deg);
  }

  80% {
    transform: rotate(-.2deg);
  }

  100% {
    transform: rotate(0);
  }
}

@keyframes shimmer {
  0% {
    left: -100%;
  }

  100% {
    left: 200%;
  }
}

@keyframes mute-color {
  0% {
    background-color: transparent;
  }

  100% {
    background-color: $mute-color;
  }
}

@keyframes unmute-color {
  0% {
    background-color: $mute-color;
  }

  100% {
    background-color: initial;
  }
}

.vds-card-art {
  background-image: url('../_imgs/cards/visa-cardart-default-334x210.png');
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: $card-art-border-radius-base;
  height: px2em(210px);
  overflow: hidden;
  position: relative;
  width: px2em(334px);
}

.vds-state {
  &--286x180 {
    background-image: url('../_imgs/cards/visa-cardart-default-286x180.png');
    border-radius: 286 / 334 * $card-art-border-radius-base;
    height: px2em(180px);
    width: px2em(286px);
  }

  &--146x92 {
    background-image: url('../_imgs/cards/visa-cardart-default-146x92.png');
    border-radius: 146 / 334 * $card-art-border-radius-base;
    height: px2em(92px);
    width: px2em(146px);
  }

  &--108x68 {
    background-image: url('../_imgs/cards/visa-cardart-default-108x68.png');
    border-radius: 108 / 334 * $card-art-border-radius-base;
    height: px2em(68px);
    width: px2em(108px);
  }

  &--48x30 {
    background-image: url('../_imgs/cards/visa-cardart-vbm-48x30.png');
    border-radius: 48 / 334 * $card-art-border-radius-base;
    height: px2em(30px);
    width: px2em(48px);
  }

  &--40x25 {
    background-image: url('../_imgs/cards/visa-cardart-vbm-40x25.png');
    border-radius: 40 / 334 * $card-art-border-radius-base;
    height: px2em(25px);
    width: px2em(40px);
  }

  &--ripple {
    @include ripple;
  }

  &--gold-ripple {
    @include ripple;

    &::after,
    &::before {
      animation: gold-ripple $ripple-duration $ripple-easing 1;
    }

    &::before {
      animation-delay: $ripple-2-delay;
    }
  }

  &--zoom-bounce {
    animation: zoom-bounce $zoom-bounce-duration $zoom-bounce-easing $zoom-bounce-iteration-count;
  }

  &--wiggle {
    animation: wiggle $wiggle-duration $wiggle-easing 1;
  }

  &--double-wiggle {
    animation: wiggle $wiggle-duration $wiggle-easing 2;
  }

  &--shimmer {
    @include shimmer;
    border-radius: px2em(12px);
  }

  &--double-shimmer {
    @include shimmer;
    border-radius: px2em(12px);

    &::after {
      animation-iteration-count: 2;
    }
  }

  &--mute-color {
    border-radius: px2em(12px);

    &::after {
      animation: mute-color $mute-color-duration $mute-color-easing 1 forwards;
      content: '';
      display: block;
      height: 100%;
      width: 100%;
    }
  }

  &--unmute-color {
    border-radius: px2em(12px);

    &::after {
      animation: unmute-color $unmute-color-duration $mute-color-easing forwards;
      content: '';
      display: block;
      height: 100%;
      width: 100%;
    }
  }

  &--combo {
    &-1,
    &-2 {
      @include shimmer;
      border-radius: px2em(12px);

      &::after {
        animation-delay: 1s;
      }
    }

    &-1 {
      animation: zoom-bounce $zoom-bounce-duration 1;
    }

    &-2 {
      animation: wiggle $wiggle-duration 1;
    }

  }
}
