/***
  vds Form
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

$vds-form-group-spacing: .5rem;

.vds-form {
  // border: 1px solid theme(notification-error);

  // .vds-input,
  // .vds-combobox {
  //   margin-bottom: px2em(28px);
  // }

  &-control {
    // border: 1px solid theme(notification-success);
    display: block;
    // margin-bottom: px2em(24px);
    // width: calc(100% - 8px);
    // margin-bottom: px2em(28px);

    &.vds-input--split-field {
      margin-left: 0;
      margin-right: px2em(4px);
      vertical-align: top;

      & + .vds-form-control {
        &.vds-input--split-field {
          margin-left: px2em(4px);
          margin-right: 0;
        }
      }

      .vds-input {
        min-width: unset;
      }
    }

    @media only screen and (max-width: $grid-width-sm - 1) {
      &.vds-input--split-field {
        margin: 0;
      }
    }

    &.vds-form-space {
      margin: 0 px2em(2px);

      // &--left {
      //   margin-left: px2em(2px);
      // }
      //
      // &--right {
      //   margin-right: px2em(2px);
      // }
    }

    &--twenty-five {
      display: inline-block;
      width: calc(30% - 6px);
    }

    &--thirty {
      display: inline-block;
      width: calc(30% - 6px);
    }

    &--forty {
      display: inline-block;
      width: calc(40% - 6px);
    }

    &--fifty {
      // default size
      display: inline-block;
      width: calc(50% - 6px);
    }

    &--sixty {
      display: inline-block;
      width: calc(60% - 6px);
    }

    &--seventy {
      display: inline-block;
      width: calc(70% - 6px);
    }

    @mixin form-input-full-width {
      width: 100%;

      .vds-input-bar {
        width: calc(100% - 2px);
      }
    }

    .vds-input,
    .vds-combobox,
    .vds-combobox-container,
    .vds-input-textarea-container {
      @include form-input-full-width;

      &.vds-state--filled {
        @include form-input-full-width;
      }
    }
  }

  &[dir='rtl'] {
    .vds-input {
      & .vds-input-label,
      & .vds-select-label {
        right: 0;
      }
    }

    .vds-form-control {
      &.vds-input--split-field {
        margin-left: px2em(4px);
        margin-right: 0;
        vertical-align: top;

        & + .vds-form-control {
          &.vds-input--split-field {
            margin-left: 0;
            margin-right: px2em(4px);
          }
        }
      }
    }
  }

  .vds-btn-group {
    margin: $vds-form-group-spacing 0;
  }
}

.vds-fieldset {
  border: 0;
  margin: px2em(24px) 0;
  padding: 0;

  &:first-of-type {
    margin-top: 0;
  }

  &:last-of-type {
    margin-bottom: 0;
  }

  %vds-fieldset-fullwidth {
    width: 100%;
  }

  // .vds-combobox-container {
  //   position: relative;
  //
  //   // .vds-menu--absolute,
  //   // .vds-listbox {
  //   //   @extend %vds-fieldset-fullwidth;
  //   // }
  //   //
  //   // .vds-menu--absolute {
  //   //   padding: 0;
  //   //   position: absolute;
  //   //   top: px2em(65px);
  //   // }
  //   //
  //   // .vds-listbox {
  //   //   margin: 0;
  //   // }
  // }
  &.vds-state--error {
    .vds-legend {
      color: theme(notification-error);
    }

    & > .vds-input-message {
      @include default-input-message-error;
      @include display-flex;
    }
  }
}

.vds-legend {
  // border: 1px solid theme(notification-warning);
  margin-bottom: px2em(14px);

  // @TODO: remove since error class has been moved to fieldset
  &.vds-state--invalid,
  &.vds-state--error {
    color: theme(notification-error);
  }
}
