/***
  vds-progress
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

$color-progress-foreground: theme(progress-foreground);
$color-progress-background: theme(progress-background);
$color-progress-accent: theme(progress-accent);

$progress-size--small: 3rem;
$progress-size--large: 4.5rem;

/*
// Linear progress mixins
*/
// sass-lint:disable-block no-empty-rulesets
@mixin progress-animation--indeterminate {
  // @TODO uncomment code when sass lint issue is fixed
  // 0% {
  //   left: 0;
  //   width: 0;
  // }
  // 50% {
  //   left: 15%;
  //   width: 70%;
  // }
  // 100% {
  //   left: 100%;
  //   width: 0;
  // }
}

@keyframes progress-indeterminate {
  @include progress-animation--indeterminate;

  0% {
    left: 0;
    width: 0;
  }

  50% {
    left: 15%;
    width: 70%;
  }

  100% {
    left: 100%;
    width: 0;
  }
}

@-webkit-keyframes progress-indeterminate {
  @include progress-animation--indeterminate;

  0% {
    left: 0;
    width: 0;
  }

  50% {
    left: 15%;
    width: 70%;
  }

  100% {
    left: 100%;
    width: 0;
  }
}


/*
// Circular progress mixins
*/
@keyframes animation-circle-foreground--indeterminate {
  0% {
    stroke-dashoffset: 137.37;
    transform: rotate(0deg);
    transform-origin: 50% 50%;
  }

  65% {
    stroke-dashoffset: 326.76;
  }

  80% {
    stroke-dashoffset: 266.41;
  }

  100% {
    stroke-dashoffset: 137.37;
    transform: rotate(1820deg);
    transform-origin: 50% 50%;
  }

}

@keyframes animation-circle-background--indeterminate {
  0% {
    stroke-dashoffset: 241.44;
    transform: rotate(0deg);
    transform-origin: 50% 50%;
  }

  65% {
    stroke-dashoffset: 326.76;
  }

  80% {
    stroke-dashoffset: 326.76;
  }

  100% {
    stroke-dashoffset: 241.44;
    transform: rotate(1820deg);
    transform-origin: 50% 50%;
  }
}

/*
// Linear and circular progress mixins
*/
%progress-common {
  position: relative;
}

@mixin progress-size($size) {
  height: $size;
  width: $size;
}

%progress-size--small {
  @include progress-size($progress-size--small);
}

%progress-size--large {
  @include progress-size($progress-size--large);
}


/*
// Linear progress
*/
.vds-progress {
  &,
  &--rounded {
    @extend %progress-common;

    &:not(.vds-state--determinate),
    &.vds-state--indeterminate {
      .vds-progress-background {
        background-color: $color-progress-foreground;
      }

      .vds-progress-bar {
        animation: progress-indeterminate 2s infinite;
        background-color: $color-progress-accent;
        width: 20%;
      }
    }

    &.vds-state--determinate {
      .vds-progress-background {
        background-color: $color-progress-background;
      }

      .vds-progress-bar {
        background-color: $color-progress-foreground;
        transition: width .25s ease;
      }
    }
  }

  &-background,
  &-bar {
    display: block;
    height: .25rem;
    left: 0;
    position: absolute;
    top: 0;
  }

  &-background {
    width: 100%;
  }

  &--rounded {
    .vds-progress-background,
    .vds-progress-bar {
      border-radius: .25rem;
    }
  }

  &-label {
    .col {
      padding-bottom: .5rem;
      padding-top: .5rem;

      &:first-of-type {
        padding-left: 1rem;
      }

      &:last-of-type {
        padding-right: 1rem;
      }
    }
  }

  /*
  // Circular progress
  */
  &--spinner {
    @extend %progress-common;
    @extend %progress-size--large;

    &-track {
      @extend %progress-size--large;
    }

    &-bar {
      @extend %default-spinner-props;
      stroke: $color-progress-foreground;
      z-index: $utils-layer-3 + 2;
    }

    &-background {
      @extend %default-spinner-props;
      stroke: $color-progress-background;
      z-index: $utils-layer-3 + 1;
    }

    .vds-progress-label {
      @extend %progress-size--large;
      display: none;
      line-height: $progress-size--large;
      position: absolute;
      text-align: center;
      top: 0;
    }

    &:not(.vds-state--determinate) {
      .vds-progress--spinner {
        &-track {
          animation: animation-circle-background--indeterminate 7.1289s infinite linear;
        }

        &-bar {
          animation: animation-circle-background--indeterminate 5.34s infinite linear;
          stroke: $color-progress-foreground;
        }

        &-background {
          animation: animation-circle-foreground--indeterminate 5.34s infinite linear;
          stroke: $color-progress-accent;
        }
      }
    }

    &.vds-state--determinate {
      .vds-progress--spinner-bar {
        transform: rotate(-90deg);
        transform-origin: 50% 50%;
        transition: stroke-dashoffset .35s;
      }

      .vds-progress-label {
        display: block;
      }
    }

    &.vds-state--small {
      @extend %progress-size--small;

      .vds-progress--spinner-track {
        @extend %progress-size--small;
      }

      .vds-progress--spinner-bar {
        transform-origin: 50% 50%;
      }

      .vds-progress-label {
        @extend %progress-size--small;
        line-height: $progress-size--small;
      }
    }
  }
}
