/***
  vds-rating
  BEM Naming Convention http://getbem.com/naming/
  PX to Em Conversion http://px-em.com/

  @author sacuna
*/

.vds-rating {
  @at-root #{&}-label {
    cursor: pointer;
    display: inline-block;
    margin: px2em(0px) px2em(8px);

    &:first-of-type {
      margin-left: 0;
    }

    &:last-of-type {
      margin-right: 0;
    }

    &.vds-state--focus {
      outline: 1px dashed theme(focus-outline-color);
    }

    &.vds-state--hover {
      &,
      &.vds-state--active {
        .vds-rating-star--filled {
          .vds-icon {
            @include svg-icon(theme(rating-star-size), theme(rating-star--hover));
          }
        }
      }
    }

    &.vds-state--active {
      .vds-rating-star {
        .vds-icon {
          @include svg-icon(theme(rating-star-size), theme(rating-star--active));
        }
      }
    }
  }

  @at-root #{&}-radio {
    clip: rect(0, 0, 0, 0);
    left: -999999px;
    position: absolute;
    top: -999999px;
  }

  @at-root #{&}-star {
    &--outline,
    &--filled {
      &:not(.vds-state--hidden) {
        display: inline-block;

        .vds-icon {
          @include svg-icon(theme(rating-star-size), theme(rating-star));
        }
      }
    }
  }

  &.vds-state--disabled {

    .vds-rating-label {
      &,
      &.vds-state--hover,
      &.vds-state--active {
        .vds-rating-star {
          &--outline,
          &--filled {
            .vds-icon {
              @include svg-icon(theme(rating-star-size), theme(rating-star--disabled));
            }
          }
        }
      }
    }

  }
}
