/***
  vds Grid
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
  @author dpoynor
*/

$grid-min: $grid-width-min; // 320px
$grid-max: $grid-width-max; // 1600px
$break-sm: $grid-width-sm; // 480px
$break-md: $grid-width-md; // 768px
$break-lg: $grid-width-lg; // 1024px
$break-xl: $grid-width-xl; // 1440px
$grid-gutter-sm: $grid-gutter-sm; // 16px / 2
$grid-gutter-lg: $grid-gutter-lg; // 24px / 2
$grid-gutter-breakpoint: 1024px; // note CSS custom vars don't currently work with media queries
$breakpoint-list: (
  (#{$media-screen-xs}, $grid-min, 'xs'),
  (#{$media-screen-sm}, $break-sm, 'sm'),
  (#{$media-screen-md}, $break-md, 'md'),
  (#{$media-screen-lg}, $break-lg, 'lg'),
  (#{$media-screen-xl}, $break-xl, 'xl'),
  (#{$media-screen-xxl}, $grid-max, 'xxl'),
);

@mixin column-helper-maker($column-classname) {
  &--#{$column-classname}-hidden {
    display: none;
  }

  &--#{$column-classname}-text-center {
    text-align: center;
  }

  &--#{$column-classname}-text-right {
    text-align: right;
  }

  &--#{$column-classname}-no-gutter {
    padding: 0;
  }

  &--#{$column-classname}-no-top-gutter {
    padding-top: 0;
  }

  &--#{$column-classname}-no-right-gutter {
    padding-right: 0;
  }

  &--#{$column-classname}-no-bottom-gutter {
    padding-bottom: 0;
  }

  &--#{$column-classname}-no-left-gutter {
    padding-left: 0;
  }
}

@mixin col {
  box-sizing: inherit;

  // http: //stackoverflow.com/questions/34250282/flexbox-safari-bug-flex-wrap
  &::before,
  &::after {
    content: none;
  }
}

@function column-width($column-count: 1, $total-columns: $grid-columns) {
  @return ($column-count / $total-columns) * 100%;
}

@mixin grid-column($column-count: $grid-columns, $total-columns: $grid-columns) {
  @if $column-count != 0 {
    @include col;
    flex: 0 0 column-width($column-count, $total-columns);
    max-width: column-width($column-count, $total-columns);
  }
}

@mixin column-maker($column-classname, $total-columns) {
  $i: 0;
  @while $i < $total-columns + 1 {

    &--#{$column-classname}-#{$i} {
      @include grid-column($column-count: $i, $total-columns: $total-columns);
    }

    $i: $i + 1;
  }
}

// Grid - Container
.vds-grid {
  display: flex;
  flex-wrap: wrap;
  margin: auto;

  // http: //stackoverflow.com/questions/34250282/flexbox-safari-bug-flex-wrap
  &::before,
  &::after {
    content: none;
  }

  // Grid - Container Responsive Sizes
  @each $item in $breakpoint-list {
    @media #{nth($item, 1)} {
      max-width: #{nth($item, 2)};
    }
  }

  // Grid - Container-fluid
  &--fluid {
    display: block;
    margin: auto;
    max-width: none;
    width: 100%;
  }

  &--inner {
    width: auto !important;

    .col {
      padding: 0 !important;
    }
  }

  // Rows
  .row {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;

    // http: //stackoverflow.com/questions/34250282/flexbox-safari-bug-flex-wrap
    &::before,
    &::after {
      content: none;
    }
  }

  // Columns

  // responsive column padding
  // select both the even spaced and responsive columns and
  // avoid selecting classes like .col--sm-center and .vds-state--complete-4
  [class^='col'] {
    &,
    &[class*='-1'],
    &[class*='-2'],
    &[class*='-3'],
    &[class*='-4'],
    &[class*='-5'],
    &[class*='-6'],
    &[class*='-7'],
    &[class*='-8'],
    &[class*='-9'] {
      @media only screen and (max-width: $grid-gutter-breakpoint - 1) {
        padding: $grid-gutter-sm;
      }

      @media only screen and (min-width: $grid-gutter-breakpoint) {
        padding: $grid-gutter-lg;
      }
    }
  }

  // responsive columns default to 100% width
  [class^='col--'] {
    &[class*='-1'],
    &[class*='-2'],
    &[class*='-3'],
    &[class*='-4'],
    &[class*='-5'],
    &[class*='-6'],
    &[class*='-7'],
    &[class*='-8'],
    &[class*='-9'] {
      width: 100%;
    }
  }

  .col {
    @include col;
    flex-basis: 0;
    flex-direction: column;
    flex-grow: 1;
    max-width: 100%;

    &::before,
    &::after {
      content: none;
    }

    @media only screen and (min-width: 0px) {
      @include column-maker('xs', 4);
    }

    @media only screen and (min-width: $break-sm) {
      @include column-maker('sm', 8);
    }

    @media only screen and (min-width: $break-md) {
      @include column-maker('md', 8);
    }

    @media only screen and (min-width: $break-lg) {
      @include column-maker('lg', 12);
    }

    @media only screen and (min-width: $break-xl) {
      @include column-maker('xl', 12);
    }

    @media only screen and (min-width: $grid-max) {
      @include column-maker('xxl', 12);
    }

    // Helpers

    @each $item in $breakpoint-list {
      @media #{nth($item, 1)} {
        @include column-helper-maker(#{nth($item, 3)});
      }
    }

    &--align {
      &-center {
        margin-left: auto;
        margin-right: auto;
      }

      &-end {
        margin-left: auto;
      }
    }

    &--valign {
      &-top {
        align-self: flex-start;
      }

      &-center {
        align-self: center;
      }

      &-bottom {
        align-self: flex-end;
      }
    }

    &--no {
      &-gutter {
        padding: 0;

        .col {
          padding: 0;
        }
      }

      &-top-gutter {
        padding-top: 0;
      }

      &-right-gutter {
        padding-right: 0;
      }

      &-bottom-gutter {
        padding-bottom: 0;
      }

      &-left-gutter {
        padding-left: 0;
      }
    }
  }
}
