/***
  vds-footer
  BEM Naming Convention http://getbem.com/naming/

  @author sacuna
*/

$color-footer: theme(footer-text-color);
$color-footer-background: theme(footer-background);
$color-footer-locale: theme(footer-text-color);
$color-footer-copyright: theme(footer-copyright-color);

$footer-padding: 16px;

%footer-caption {
  margin-bottom: 0 !important;
  padding: $footer-padding / 2 0;
}

@mixin footer-col-gutter {
  padding-left: $grid-gutter-lg !important;
  padding-right: $grid-gutter-lg !important;
}

.vds-footer {
  &,
  &--fixed,
  &--relative {
    background-color: $color-footer-background;
    color: $color-footer;
    display: block;
    flex-shrink: 0;
    font-size: 1em;
    z-index: $utils-layer-2;

    .vds-grid {
      padding: $footer-padding 0 $footer-padding / 2 0;

      @media #{$media-screen-xs} {
        & {
          padding-left: $footer-padding;
          padding-right: $footer-padding;
        }
      }

      @media #{$media-screen-sm} {
        & {
          padding-left: $footer-padding;
          padding-right: $footer-padding;
        }
      }

      @media #{$media-screen-md} {
        & {
          padding-left: $footer-padding;
          padding-right: $footer-padding;
        }
      }

      @media #{$media-screen-lg} {
        .col {
          @include footer-col-gutter;
        }
      }

      @media #{$media-screen-xl} {
        .col {
          @include footer-col-gutter;
        }
      }

      @media #{$media-screen-xxl} {
        .col {
          @include footer-col-gutter;
        }
      }
    }
  }

  &--fixed {
    bottom: 0;
    left: 0;
    position: fixed;
    width: 100%;
  }

  &--relative {
    margin-top: 40px;
  }

  &-region {
    color: $color-footer-locale;
    order: 2;
    position: relative;
    top: -1px;

    &-col {
      @media #{$media-screen-xs} {
        & {
          order: 1;
          padding-bottom: $footer-padding !important;
        }
      }

      @media #{$media-screen-sm} {
        & {
          order: 1;
          padding-bottom: $footer-padding !important;
        }
      }

      @media #{$media-screen-md} {
        & {
          order: 1;
          padding-bottom: $footer-padding !important;
        }
      }
    }

    &-label {
      display: inline-block;
      margin-bottom: 0px !important;
      margin-left: 22px;
      margin-top: -1px;
    }

    &-icon {
      .vds-icon {
        @include svg-icon(px2em(18px), $color-footer-locale);
        position: absolute;
        top: 1px;
      }
    }
  }

  &-logo {
    height: 20px;
    order: 1;
    width: 62px;

    &-col {
      @media #{$media-screen-xs} {
        & {
          order: 2;
        }
      }

      @media #{$media-screen-sm} {
        & {
          order: 2;
        }
      }

      @media #{$media-screen-md} {
        & {
          order: 2;
        }
      }
    }
  }

  &-copyright {
    &.vds-text--caption {
      @extend %footer-caption;
      color: $color-footer-copyright;
    }
  }

  &-links {
    padding-top: $footer-padding / 2;

    &.vds-text--caption {
      @extend %footer-caption;

      @media #{$media-screen-xs} {
        & {
          padding-bottom: 0;
          padding-top: 0;
        }
      }

      @media #{$media-screen-sm} {
        & {
          padding-bottom: 0;
          padding-top: 0;
        }
      }
    }

    a {
      color: $color-footer-locale;
      padding: 0 $footer-padding / 2;

      &:first-of-type {
        padding-left: 0;
      }

      &:last-of-type {
        padding-right: 0;
      }
    }
  }
}
