/***
  vds-combobox
  BEM Naming Convention http://getbem.com/naming/
  PX to Em Conversion http://px-em.com/

  @author pejin
*/

.vds-combobox-container {
  display: block;
  position: relative;

  .vds-input {
    &-bar {
      .vds-btn-icon {
        &,
        &.vds-state--active {
          height: px2em(26px);
          margin-right: 2px;
          max-width: px2em(26px);
          min-width: px2em(26px);

          .vds-btn--label {
            margin-top: 4px;
          }
        }

        &.vds-state--active {
          .vds-btn--label {
            margin-top: 2px;

            [class^='vds-icon'] {
              @include svg-icon(1rem, theme(button--secondary-text--active));
            }
          }
        }
      }
    }
  }

  .vds-menu--absolute {
    overflow-y: unset;

    > .vds-listbox {
      min-height: px2em(20px);
      min-width: auto;
      width: auto;

      > .vds-listbox--item-highlight {
        background-color: $color-blue;
        color: $color-white;
      }

      > .vds-listbox-item {
        &:hover {
          background-color: $color-blue;
          color: $color-white;
        }
      }

      > .vds-listbox--item-selected {
        background-color: $color-blue;
        color: $color-white;

        [class^='vds-icon'] {
          @include svg-icon(1rem, theme(listbox-text--active));
          float: right;
          margin-top: 2px;
        }
      }
    }

  }
}
